.categories-panel {
    padding: 0;
    border: none;
    -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.07);
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.07);
    border-radius: 5px;

    .navflex-basis {
        flex-basis: 100%;
    }

    .dropdown-submenu {
        position: relative;
    }

    .dropdown-submenu:hover>.dropdown-menu {
        display: block;
        top: 0;
    }

    .dropdown:hover>.dropdown-menu {
        display: block;
    }

    ul {
        width: 100%;


        .nav-item {
            margin-bottom: 0;
            display: flex;
            align-items: center;

            .fas {
                position: absolute;
                right: 0;
                font-size: 20px;
                margin-right: 10px;
                color: var(--secondary);
                cursor: pointer;
            }

            a {
                font-size: 14px;
                font-weight: 600;
                border: none;
                // background-color: var(--color-white);
                border-bottom: 1px solid $gray-300;
                color: var(--color-text) !important;
                padding: 11.4px 5px;
                // display: flex;
                transition: color .4s;
                text-decoration: none;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;

                &:last-child {
                    border-bottom: none;
                }

                &:hover {
                    background-color: var(--secondary-darkest) !important;

                }


                img {
                    width: 18px;
                    height: 18px;
                    margin-right: 7px;
                    object-fit: contain;
                }

            }

            .dropdown-item::before {
                left: 10px;
            }

            .dropdown-menu {
                animation: animationmenu ease 0.5s;
                animation-iteration-count: 1;
                transform-origin: 50% 50%;
                animation-fill-mode: forwards;

                margin: 0;
                padding: 0;
                border-color: $gray-300;

                width: 250px;

                .dropdown-item {
                    padding: 0.5rem;
                }
            }
        }
    }
}