// ...........footer.............



.footer-seventeen{
    
    .banner-width{
        background-image: url('../../../images/index-17/f.png');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: bottom right;
        padding: 8rem 4rem 0rem 4rem;
    
    }

    h4{
        width: fit-content;
        margin: auto;
        padding-top: 6px;
    }

    .social{
        text-decoration: none;
        list-style: none;

        li{
            margin: 5px;
            a{
                padding: 5px;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background-color: var(--secondary);
                display: flex;
                align-items: center;
                justify-content: center;

                &:hover{
                    background-color: var(--primary);
                }
            }
        }
    }

    .newsletter{
        padding-bottom: 60px;
        text-align: center;
        
        h5{
           
            font-family: $font-family-base;
            font-size: $font-size-lg;
            color: var(--secondary);
        }
        h3{
            color: var(--color-text);
            font-family: $font-family-base-bold;
            font-size:( $font-size-lg + 2);
        }
        
        .input-group{
            .form-control{
                height: auto;
                border-radius: 20px  0 0 20px;
            
            }
            .btn{
                border-radius: 0 20px 20px 0;
            }

        }
        input{
            background-color: transparent;
            color: white;
        }
        span{
            border: 1px solid white;
            border-radius: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 20px;
            height: 20px;
        }
    }

     
    
   
    .copyright-content {
        background-color: var(--color-article2-background);
        padding-top: 10px;
        padding-bottom: 10px;
        
        .footer-info {
            font-size: $font-size-sm;
            color: var( --color-text);
            a{
                color: var( --color-text);
                &:hover{
                    color: var(--secondary);
                }
            }
        }

        .social {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 0;
            text-decoration: none;

            li{
                font-size: ($font-size-lg + 0.475);
                margin-left: 15px;
                list-style: none;
                a {
                    color: var(--color-text-dark);
                    transition: 0.4s;

                    &.fb:hover{
                        color: #3b5998;
                    }
                    
                    &.tw:hover {
                        color: #00acee;
                    }
                    &.sk:hover {
                        color: #00aff0;
                    }
                    &.In:hover {
                        color: #0077B5;
                    }
                    &.ig:hover {
                        color: #3f729b;
                    }

                }
            }
        } 
    }
}
.bodyrtl{
    .footer-one .newsletter .input-group .form-control{
        border-radius: 0 20px 20px 0;
    }
    .footer-one .newsletter .input-group .btn{
        border-radius: 20px 0 0 20px ;
    }
}

@media only screen and (max-width:1200px){
    .footer-one .newsletter h3{
        font-size: 2rem;
    }
}
@media only screen and (max-width:768px){
    .footer-one{
        padding: 30px 0 0 0;
        margin: 30px 0 0 0;
     .newsletter{
         padding-bottom: 30px;
     }
    }
    .footer-one .newsletter h3{
        font-size: 2rem;
    }
    .footer-one .newsletter p{
        font-size: 14px;
    }
    .footer-one .copyright-content .footer-info{
        
    text-align: center;
    }
    .footer-one .copyright-content .socials{
        justify-content: center;
    }
}

