.home-20{
    .header-two {
        background-color: transparent;
        .mini-header-right {
                margin: 0;
                padding: 0;
                display: flex;
                justify-content: end;
                .dropdown{
                    .dropdown-menu{
                        width: 300px;
                    }
                }
    
            li {
                list-style-type: none;
                padding-left: 20px;
    
                a {
                    display: flex;
    
                    h6 {
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: normal;
                    }
    
                    i {
                        width: 25px;
                        height: 25px;
                    }
                }
            }
    
            .shopping-cart-items {
    
                padding-left: 25px;
                padding-right: 25px;
                padding-top: 15px;
                padding-bottom: 15px;
                max-height: 350px;
                list-style-type: none;
                overflow-y: auto;
    
                li {
                    margin-right: 0;
                    padding-right: 0;
                    border-right: none;
                    float: left;
                    position: relative;
                    width: 100%;
                    margin-bottom: 10px;
                    border-bottom: $border-width solid;
                    border-color: var(--color-border);
                    padding-bottom: 10px;
                    margin-left: 0;
    
                    .item-thumb {
                        position: absolute;
                        left: 0;
                        top: 0;
    
    
                        .image {
                            width: 64px;
                            height: 64px;
                            border: $border-width solid;
                            border-color: var(--color-border);
                            border-radius: $border-radius;
                            overflow: hidden;
    
                        }
                    }
    
                    .item-detail {
                        float: left;
                        padding-left: 74px;
    
                        h3 {
                            font-family: $font-family-base-bold;
                            font-size: $font-size-base;
                            text-transform: capitalize;
                            color: var(--color-text);
                        }
    
                        .item-s {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            font-size: $font-size-base;
                            color: var(--color-text);
    
    
                            .fas {
                                color: var(--color-text-secondary);
                                float: right;
                                margin-left: 8px;
                                -webkit-transition: 0.4s;
                                transition: 0.4s;
    
                                &:hover {
                                    color: var(--secondary);
                                }
                            }
                        }
                    }
    
                    &:last-child {
                        border-bottom: none;
                        margin-bottom: 0;
                        padding: 0;
                    }
                }
    
                .item-summary {
                    font-family: $font-family-base-bold;
                    font-size: $font-size-lg;
                    font-weight: $font-weight-bold;
                    color: var(--secondary);
                    margin-bottom: 0;
                    margin-top: 5px;
                    white-space: normal;
                }
    
            }
        }
    
        form {
            .form-control {
                border-radius: 29px;
                border: 1px solid var(--Grey-6, #E5E5E5);
                background: transparent;
            }
    
        }
    
        nav {
            .dropdown-toggle::after {
                display: block;
                bottom: 10px !important;
                right: -15px !important;
            }
    
            li {
                color: var(--DarkGrey-9, #383838);
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;
                letter-spacing: 0.7px;
                text-transform: uppercase;
            }
        }
    
        .header-pills {
            ul {
                margin: 0;
                padding: 0;
                display: flex;
                justify-content: end;
            }
    
            li {
                list-style-type: none;
                padding-left: 20px;
    
                a {
                    display: flex;
    
                    h6 {
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: normal;
                    }
    
                    i {
                        width: 25px;
                        height: 25px;
                    }
                }
            }
        }
    
        .header-mini {
            background-color: var(--secondary);
    
            .btn.dropdown-toggle {
                color: white !important;
            }
    
            a {
                color: var(--color-text);
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                text-align: start;
            }
    
            .list-link {
                color: #FFF;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                padding: 0 !important;
                border: none !important;
            }
        }
    }
}
.vheader-sixM .header-maxi .navigation-mobile-container .navigation-mobile-toggler {
    background-color: var(--secondary) !important;
}
