.home17 {
    $secondary: #222222;
    --secondary: #{$secondary};
    $primary: #a976c1;
    --primary: #{$primary};
}

.index-page-17 {

    .text-body {
        color: var(--color-text) !important;
    }

    .font {
        font-family: fantasy !important;
    }

    .carousel {
        .set-w {
            width: fit-content;
            height: fit-content;

            h1 {
                font-weight: 500;
                font-size: 60px;
            }
        }

        .carousel-indicators {
            bottom: 9rem;
        }
    }

    .page {
        margin-top: -150px;
    }

    .pro-top-sellers {
        .slick-arrow.slick-prev {
            right: unset;
            top: 50%;
            left: auto;
            left: -60px;
        }

        .slick-arrow.slick-next {
            right: -60px;
            left: unset;
            top: 50%;
        }

        .slick-prev:before {
            content: url('../../images/index-17/left.png');
        }

        .slick-next:before {
            content: url('../../images/index-17/right.png');
        }
    }

    .banner-sale .banner-caption.left-caption {
        padding-left: 65px;
        padding-right: 65px;
    }

    .fullwidth-banner {
        background-image: url('../../images/index-17/t.png');
        height: unset;
        padding: 5rem;
        background-attachment: unset;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

        h3 {
            margin-top: -15px;
        }
        .parallax-banner-text{
            z-index: 99;
        }
    }

    .footer-shipping {
        margin-bottom: -150px;

        .black_border {
            .d-flex {
                height: 150px;
                padding: 12px;
                background-color: var(--color-article4-background);
            }
        }
    }
}

html.boxed {

    .index-page-17 .carousel .set-w h1,
    .index-page-17 .carousel .set-w .h1 {
        font-weight: 500;
        font-size: 50px;
    }

    .index-page-17 .page {
        margin-top: -100px;
    }

    .index-page-17 .carousel .carousel-indicators {
        bottom: 6rem;
    }
}

@media only screen and (max-width: 1200px) {
    .index-page-17 .page {
        margin-top: -70px;
    }

    .index-page-17 .carousel .carousel-indicators {
        bottom: 4rem;
    }

    .index-page-17 .carousel .set-w h1 {
        font-size: 48px;
    }

    .index-page-17 .banner-sale .banner-caption.left-caption {
        padding-left: 40px;
        padding-right: 40px;
    }

    .index-page-17 .fullwidth-banner {
        padding: 2rem;

        h2 {
            font-size: 36px;
        }

        h3 {
            font-size: 25px;
        }
    }

    .index-page-17 .blog {
        h3 {
            font-size: 25px;
        }
    }
}

@media only screen and (max-width: 1100px) {
    .index-page-17 .fullwidth-banner {
        padding: 5rem;
    }
}

@media only screen and (max-width: 991px) {
    .index-page-17 .carousel .set-w h1 {
        font-size: 40px;
    }

    .index-page-17 .page {
        .btn {
            padding: 10px;
        }
    }

    .index-page-17 .fullwidth-banner {
        padding: 2rem;

        h2 {
            font-size: 30px;
        }

        h3 {
            font-size: 21px;
            margin-top: -7px;
        }

        .btn {
            padding: 10px;
        }
    }
}

@media only screen and (max-width: 776px) {
    .index-page-17 .carousel .set-w h1 {
        font-size: 30px;
    }

    .index-page-17 .carousel .set-w .btn {
        padding: 10px 12px;
    }

    .index-page-17 .page {
        margin-top: 0px;
        padding-top: 50px;
    }

    .index-page-17 .carousel .carousel-indicators {
        bottom: 2rem;
    }

    .index-page-17 .banner_ .collection-banner {
        margin-bottom: 12px;
    }

    .pro-content {
        padding-top: 60px;
    }

    .index-page-17 .fullwidth-banner {
        padding: 2rem;
    }

    .index-page-17 .fullwidth-banner h2 {
        font-size: 22px;
    }

    .index-page-17 .fullwidth-banner h3 {
        font-size: 16px;
    }

    .index-page-17 .fullwidth-banner .btn {
        padding: 7px 12px;
    }
}

@media only screen and (max-width: 600px) {

    .index-page-17 .carousel .set-w h1,
    .index-page-17 .carousel .set-w .h1 {
        font-size: 24px;
    }

    .index-page-17 .carousel .set-w .btn {
        padding: 8px 12px;
    }
}

@media only screen and (max-width: 500px) {
    .index-page-17 .carousel .set-w h1 {
        font-size: 18px;
        margin-bottom: 0px;
    }

    .index-page-17 .fullwidth-banner {
        padding: 1rem 0rem;
    }

    .index-page-17 .carousel .carousel-indicators {
        bottom: 0rem;
    }

    .index-page-17 .carousel .set-w .btn {
        padding: 5px 6px;
        font-size: 7px;
    }

    .index-page-17 .fullwidth-banner h2 {
        font-size: 17px;
    }

    .index-page-17 .fullwidth-banner h3 {
        font-size: 13px;
    }

    .index-page-17 .fullwidth-banner .btn {
        padding: 4px 12px;
        font-size: 8px;
    }

    .index-page-17 .blog h3 {
        font-size: 17px;
    }

    .index-page-17 .blog h5 {
        font-size: 17px;
    }
}