/*-----------
/----- Header Top bar -------- */

.header-ten {

    .headertop {
        background-color: var(--color-article2-background);
        padding: 15px 0;

        .leftsidelink {
            display: flex;
            align-items: center;
        }

        .rightside {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            .followus {
                display: flex;
                padding-right: 15px;

                p {
                    margin-bottom: 0;
                }

                .socials {
                    list-style: none;
                    margin: 0;
                    padding: 0;

                    li {
                        display: inline-block;
                        margin-left: 10px;

                        a {
                            color: var(--color-text-dark);
                            transition: 0.4s;
                            font-size: 17px;
                        }
                    }
                }
            }

            .login {
                padding-left: 15px;
                border-left: 1px solid var(--color-border);
            }
        }

        .navbar-lang {
            margin-right: 15px;

            ul {
                display: flex;
                list-style: none;
                padding: 0;
                margin: 0;

                li {
                    .btn {
                        padding: 0;
                        position: relative;
                        font-family: $font-family-base;
                        font-size: $font-size-base;
                        color: var(--color-text);
                        padding: 0;
                        padding-right: 10px;
                        line-height: normal;


                        &:hover {
                            text-decoration: none;
                        }

                    }

                }

                .dropdown:last-child:after {
                    content: "";
                    height: 22px;
                    width: 1px;
                    background-color: var(--color-border);
                    display: block;
                    position: absolute;
                    right: -12px;
                    top: 50%;
                    transform: translateY(-50%);
                }

                .dropdown {
                    margin-right: 15px;


                    .dropdown-toggle::after {
                        position: absolute;
                        right: 0px;
                        bottom: 7px;
                        font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
                        font-weight: 900;
                        content: "\f107";
                        border: none;
                        font-size: 11px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 5px;
                        width: 10px;
                    }


                    .dropdown-menu {
                        background-color: var(--color-article1-background);
                        color: var(--color-text);
                        animation: animationmenu ease 0.5s;
                        animation-iteration-count: 1;
                        transform-origin: 50% 50%;
                        animation-fill-mode: forwards;

                        margin: 0px 0 0 0;
                        padding: 15px 0;
                        border-radius: $border-radius;
                        border: none;

                        width: 180px;
                        -webkit-box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);
                        box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);

                        .dropdown-item {
                            white-space: normal;
                            color: var(--color-text);

                            &:hover {
                                background-color: transparent;
                                color: var(--secondary);
                                transition: 0.5s;
                            }
                        }
                    }

                    &:hover>.dropdown-menu {
                        display: block;
                    }
                }

            }

        }
    }

    .headermidium {
        padding: 15px 0;
    }

    .itemshover {
        i {
            color: var(--color-text);
        }

        &:hover {
            i {
                color: var(--secondary);
            }
        }
    }

    .headerbottom {
        padding: 5px 0;
        background-color: var(--color-article2-background);

        .dropdown-menu {

            border: none;
            animation: animationcartmenu ease 0.5s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
            border-color: #fff;
            position: absolute;
            width: 250px;
            top: 0 !important;
        }

        .Categroy-toggle {
            .dropdown-toggle {
                background-color: $primary;
                color: var(--text-color-primary);
                display: flex;

                .fas {
                    margin-left: 15px;
                }

                &:hover,
                &:active,
                &:focus {
                    background-color: var(--secondary);
                    color: var(--text-color-secondary);
                }

                &::after {
                    content: "";
                }
            }
        }

        .form-inline {
            .search-field-module {
                position: relative;
                background-color: var(--color-article1-background);
                box-shadow: none;
                width: 100%;

                input {
                    height: auto;
                }

                &:focus {
                    border-color: var(--secondary);
                }

                .search-field-wrap {

                    border-color: vsr(--color-border);

                    input {
                        border: 1px solid;
                        border-color: var(--color-border);
                        background-color: var(--color-article1-background);
                        color: var(--text-color-light);
                        float: left;
                        padding: 0 55px 0 20px;
                        height: 47px;
                        width: 100%;
                        outline: none !important;
                        border-radius: 3px;


                    }

                    .btn-primary {
                        position: absolute;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        height: 100%;
                        border-radius: unset;
                    }
                }

                .search-field-select {
                    float: left;
                    position: relative;
                    width: 160px !important;
                    height: 50px;

                    .bootstrap-select {
                        width: 100% !important;

                        .btn.dropdown-toggle {
                            background-color: $white;
                            color: var(--color-text-secondary);
                            font-weight: $font-weight-normal;
                            font-family: $font-family-base;
                            height: 50px;
                            width: 100%;
                            border: none;
                            padding: 0;

                            // padding: 0.6rem 4px 0.6rem 15px;
                            .filter-option {
                                display: flex;
                                justify-content: center;
                                align-items: center;
                            }

                            .filter-option-inner-inner {
                                overflow: hidden;
                                text-overflow: ellipsis;
                            }

                            &:focus {
                                outline: none !important;
                            }

                            &::after {
                                font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
                                font-weight: 900;
                                /* regular style/weight */
                                content: "\f107";
                                position: absolute;
                                top: 17px;
                                right: 6px;
                                color: var(--color-text-secondary);
                                border: none;
                            }

                        }

                        >.dropdown-menu {
                            animation: animationselect ease 0.7s;
                            animation-iteration-count: 1;
                            transform-origin: 100% 100%;
                            animation-fill-mode: forwards;
                            margin: 0;
                            border: 0;

                            .dropdown-item {
                                padding: 5px 15px 5px 23px;
                            }
                        }

                    }

                    &::before {
                        content: "";
                        width: 1px;
                        height: 25px;
                        background: $border-color;
                        position: absolute;
                        top: 50%;
                        margin-top: -12px;
                        right: -14px;
                        z-index: 1;
                    }
                }
            }
        }

        .mostsearch {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            p {
                margin-bottom: 0;
                margin-right: 5px;
                font-family: $font-family-base-medium;
                text-transform: capitalize;
            }

            ul {
                list-style: none;
                margin: 0;
                padding: 0;

                li {
                    display: inline-block;
                    padding-right: 5px;
                    border-right: 1px solid var(--color-border);

                    &:last-child {
                        border-right: none;
                        padding-right: 0;
                    }
                }
            }
        }
    }

    .list-right-options {
        display: flex;
        justify-content: flex-end;

        .listingLi {
            display: inline-block;
            margin-left: 15px;
            position: relative;

            &:first-child {
                margin-left: 0;
            }
        }

        .itemclick {
            display: flex;
            align-items: center;
            border: none;
            background-color: transparent;
            outline: unset;

            i {
                font-size: $font-size-lg + 0.5rem;
            }

            .detail {
                margin-left: 15px;

                .heading {
                    color: var(--secondary);
                    text-transform: uppercase;
                    margin-bottom: -3px;
                    font-size: 13px;
                }

                .subheading {
                    margin-bottom: 0;
                    text-transform: uppercase;
                    text-align: start;
                    color: var(--color-text-secondary);
                    font-size: 13px;
                }
            }
        }

        .dropdown {


            .dropdown-toggle::after {
                content: none;
            }

            .dropdown-menu {
                background-color: var(--color-article1-background);
                border-color: var(--color-article1-background);
                animation: animationcartmenu ease 0.5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
                -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                width: 270px;
                border-radius: $border-radius;
                z-index: 99999;
                margin-top: 11px;
                left: auto !important;

                .dropdown-item {
                    color: var(--color-text);
                }

                .shopping-cart-items {

                    padding-left: 25px;
                    padding-right: 25px;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    max-height: 350px;
                    overflow-y: auto;
                    list-style: none;

                    li {
                        float: left;
                        position: relative;
                        width: 100%;
                        margin-bottom: 10px;
                        border-bottom: $border-width solid;
                        border-color: var(--color-border);
                        padding-bottom: 10px;
                        margin-left: 0;

                        .item-thumb {
                            position: absolute;
                            left: 0;
                            top: 0;


                            .image {
                                width: 64px;
                                height: 64px;
                                border: $border-width solid;
                                border-color: var(--color-border);
                                border-radius: $border-radius;
                                overflow: hidden;

                            }
                        }

                        .item-detail {
                            float: left;
                            padding-left: 74px;

                            h3 {
                                font-family: $font-family-base-bold;
                                font-size: $font-size-base;
                                text-transform: capitalize;
                                color: var(--color-text);
                            }

                            .item-s {
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                font-size: $font-size-base;
                                color: var(--color-text);


                                .fas {
                                    color: var(--color-text-secondary);
                                    float: right;
                                    margin-left: 8px;
                                    -webkit-transition: 0.4s;
                                    transition: 0.4s;

                                    &:hover {
                                        color: var(--secondary);
                                    }
                                }
                            }
                        }

                        &:last-child {
                            border-bottom: none;
                            margin-bottom: 0;
                            padding: 0;
                        }
                    }

                    .item-summary {
                        font-family: $font-family-base-bold;
                        font-size: $font-size-lg;
                        font-weight: $font-weight-bold;
                        color: var(--secondary);
                        margin-bottom: 0;
                        margin-top: 5px;
                        white-space: normal;
                    }

                }
            }
        }

    }

    .logo {
        margin-right: 0px;

        a {
            display: block;
        }
    }

    .pro-header-right-options {
        padding-left: 0;
        padding-right: 0px;
        margin-bottom: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .search-field {
            .dropdown-menu {
                padding: 0;
            }

            .search-field-module {
                .btn {
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 10px;
                }

                .fas {
                    font-size: $font-size-lg;
                    color: var(--dark);

                    -webkit-transition: 0.4s;
                    transition: 0.4s;

                    &:hover {
                        color: var(--secondary);
                    }
                }

            }

        }

        .profile-tags {
            .dropdown-menu {
                width: auto !important;

                .dropdown-item {
                    &:hover {
                        color: var(--secondary);
                        background-color: transparent;
                    }
                }
            }
        }

        li {
            position: relative;
            display: inline-block;
            list-style: none;
            margin-left: 25px;


            .btn-light {

                &:hover {
                    color: var(--secondary);
                }

                border:none;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 34px;
                width: 34px;
                padding: 0;
                border-radius: 200px;


                .fas,
                .far {
                    font-size: $font-size-lg;

                }


                .badge {
                    position: absolute;
                    left: -5px;
                    bottom: -5px;
                    top: auto;
                    height: 16px;
                    width: 16px;
                    color: var(--text-color-secondary);
                    border-radius: $border-radius;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    z-index: 99;
                    border-radius: 200px;
                }
            }

            &:first-child {
                margin-left: 0;
            }
        }

        .dropdown {
            float: right;

            .dropdown-toggle::after {
                content: none;
            }

            .dropdown-menu {
                background-color: var(--color-article1-background);
                border-color: var(--color-article1-background);
                animation: animationcartmenu ease 0.5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
                -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                width: 270px;
                border-radius: $border-radius;
                z-index: 99999;
                margin-top: 11px;
                left: auto !important;

                .dropdown-item {
                    color: var(--color-text);
                }

                .shopping-cart-items {

                    padding-left: 25px;
                    padding-right: 25px;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    max-height: 350px;
                    overflow-y: auto;

                    li {
                        float: left;
                        position: relative;
                        width: 100%;
                        margin-bottom: 10px;
                        border-bottom: $border-width solid;
                        border-color: var(--color-border);
                        padding-bottom: 10px;
                        margin-left: 0;

                        .item-thumb {
                            position: absolute;
                            left: 0;
                            top: 0;


                            .image {
                                width: 64px;
                                height: 64px;
                                border: $border-width solid;
                                border-color: var(--color-border);
                                border-radius: $border-radius;
                                overflow: hidden;

                            }
                        }

                        .item-detail {
                            float: left;
                            padding-left: 74px;

                            h3 {
                                font-family: $font-family-base-bold;
                                font-size: $font-size-base;
                                text-transform: capitalize;
                                color: var(--color-text);
                            }

                            .item-s {
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                font-size: $font-size-base;
                                color: var(--color-text);


                                .fas {
                                    color: var(--color-text-secondary);
                                    float: right;
                                    margin-left: 8px;
                                    -webkit-transition: 0.4s;
                                    transition: 0.4s;

                                    &:hover {
                                        color: var(--secondary);
                                    }
                                }
                            }
                        }

                        &:last-child {
                            border-bottom: none;
                            margin-bottom: 0;
                            padding: 0;
                        }
                    }

                    .item-summary {
                        font-family: $font-family-base-bold;
                        font-size: $font-size-lg;
                        font-weight: $font-weight-bold;
                        color: var(--secondary);
                        margin-bottom: 0;
                        margin-top: 5px;
                        white-space: normal;
                    }

                }
            }
        }

    }

    .navbar {
        padding: 0px;

        .navbar-collapse {
            .navbar-nav {
                .nav-item {
                    margin-right: 43px;
                    padding: 1.2rem 0;

                    .nav-link {
                        position: relative;
                        font-family: $font-family-base;
                        font-size: $font-size-base;
                        color: var(--color-text);
                        padding: 0;

                        &:hover {
                            text-decoration: none;
                        }

                        .badge {
                            position: absolute;
                            top: 2px;
                            left: 10px;
                            font-family: $font-family-base;
                            background-color: $danger;
                            color: color-yiq($danger);

                        }
                    }

                    .dropdown-menu {
                        background-color: var(--color-article1-background);
                        animation: animationmenu ease 0.5s;
                        animation-iteration-count: 1;
                        transform-origin: 50% 50%;
                        animation-fill-mode: forwards;

                        margin: 0px 0 0 -25px;
                        padding: 15px 0;
                        border-radius: $border-radius;
                        border: none;

                        width: 180px;
                        -webkit-box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);
                        box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);

                        .dropdown-item {
                            white-space: normal;
                            color: var(--color-text);

                            img {
                                width: auto;
                                position: fixed;
                                right: -150px;
                                top: -5px;
                                visibility: hidden;
                                z-index: 3;
                                width: 150px;
                                -webkit-box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                opacity: 0;
                                -webkit-transition: .2s;
                                transition: .2s;
                            }

                            &:hover {
                                background-color: transparent;
                                color: var(--secondary);
                                transition: 0.5s;

                                img {
                                    visibility: visible;
                                    top: 0;
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }

                .nav-item.dropdown:hover>.dropdown-menu {
                    display: block;
                }
            }
        }
    }

    .mega-menu-md {
        width: 654px !important;
    }

    .mega-menu-sm {
        width: 436px !important;
    }

    .mega-menu {
        padding: 0 !important;

        .no-gutters {
            padding: 0;
        }

        ul {
            list-style: none;
            padding: 15px 0;

            li.dropdown-header {
                white-space: normal;

                h5 {
                    font-family: $font-family-base;
                    font-size: $font-size-base;
                    color: var(--color-text);
                    text-transform: uppercase;
                    font-weight: bold;
                }
            }


        }

        .banner-menu {
            display: block;
            color: var(--color-text);

            &:hover {
                color: var(--secondary);
            }

            .banner-content {
                position: absolute;
                top: 0;
                left: 0;
                padding: 15px;

                .banner-title {
                    font-family: $font-family-base;
                    font-size: $font-size-lg;

                    span {
                        strong {
                            font-family: $font-family-base-bold;
                            font-size: ($font-size-lg + 1rem);
                        }
                    }
                }
            }
        }

    }

}


.dropdown-menu {
    background-color: var(--color-article1-background);

    .dropdown-item {
        color: var(--color-text);
        background-color: transparent;

        &:hover {
            color: var(--secondary);
        }
    }
}

.dropdown-submenu:hover>.dropdown-menu {
    display: block;
}

.dropdown-submenu {
    position: relative;

    .dropdown-menu {
        top: -15px;
        left: 100%;
        margin-left: 0 !important;
    }
}





.bodyrtl {

    .header-ten .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-right: auto;
        margin-left: 43px;

        .nav-link {
            &::after {
                right: auto;
                left: -14px;
            }
        }
    }

    .mega-menu .banner-content {
        right: 0;
        left: auto;
    }

    .header-ten .navbar .navbar-collapse .navbar-nav .nav-item.dropdown:hover>.dropdown-menu {
        left: auto;
        right: 0;
        text-align: right;
        margin: 0px -25px 0 0;
    }

    .header-ten .list-right-options .dropdown .dropdown-menu {
        right: auto;
        left: 0px !important;
        transform: translate3d(0px, 33px, 0) !important;
    }

    .header-ten .list-right-options .search-field .dropdown-menu {
        margin-left: 0;
    }

    .dropdown-submenu .dropdown-menu {
        right: 100%;
        left: auto;
    }

    .header-ten .list-right-options .dropdown .dropdown-menu .shopping-cart-items li .item-thumb {
        left: auto;
        right: 0;
    }

    .header-ten .list-right-options .dropdown .dropdown-menu .shopping-cart-items li .item-detail {
        float: left;
        padding-left: 0;
        padding-right: 74px;
    }

    .header-ten .list-right-options .dropdown .dropdown-menu .shopping-cart-items li {
        text-align: right;
        margin-right: 0;
    }

    .header-ten .list-right-options .search-field .search-field-module .btn {
        right: auto;
        left: 0;
    }

    .header-ten .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu .dropdown-item {
        text-align: right;

    }

    .header-ten .list-right-options li:first-child {
        margin-right: 0;
    }

    .header-ten .navbar-lang {
        margin-right: 0;
        margin-left: 15px;
    }

    .header-ten .navbar-lang ul .dropdown {
        margin-right: 0;
        margin-left: 15px;
    }

    .header-ten .navbar-lang ul li .btn {
        padding-left: 10px;
        padding-right: 0;
    }

    .header-ten .navbar-lang ul .dropdown .dropdown-toggle::after {
        right: auto;
        left: 0;
        margin-right: 0.255em;
        margin-left: 0;
    }

    .header-ten .navbar-lang ul .dropdown .dropdown-menu {
        text-align: right;
        right: 0;
        left: auto;
    }

    .header-ten .navbar-lang ul .dropdown:last-child:after {
        right: auto;
        left: -12px;
    }

    .header-ten .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu .dropdown-item img {
        left: -150px;
        right: auto;
    }

    .header-ten .list-right-options .itemclick .detail {
        margin-left: 0;
        margin-right: 15px;
    }

    .header-ten .list-right-options .listingLi {
        margin-left: 0;
        margin-right: 15px;
    }

    .header-ten .list-right-options .listingLi:first-child {
        margin-right: 0;
    }

    .header-ten .headertop .rightside .followus {
        padding-right: 0;
        padding-left: 15px;
    }

    .header-ten .headertop .rightside .login {
        padding-left: 0;
        padding-right: 15px;
        border-left: none;
        border-right: 1px solid var(--color-border);
    }

    .header-ten .headertop .rightside .followus .socials li {
        margin-left: 0;
        margin-right: 10px;
    }

    .header-ten .headerbottom {
        .mostsearch {
            p {
                margin-right: 0;
                margin-left: 5px;
            }
        }
    }
}


@media only screen and (max-width: 1024px) {

    .header-desktop {
        display: none;
    }
}