.home16 {
    $secondary: #b30138;
    --secondary: #{$secondary};
    $primary: #131313;
    --primary: #{$primary};
}

.index-page-16 {
    .text-body {
        color: var(--color-text) !important;
    }

    .btn-dot {
        background-color: transparent;
        border: 1px dashed var(--secondary);
        border-radius: 8px;
        padding: 10px 14px;
        color: var(--secondary);
    }

    .pur {
        color: #5a2192;
    }

    .red {
        color: #ec015f;
    }

    .border-dash {
        border: 1px dashed var(--secondary);
    }

    .border-arounded {
        border: 1px dashed var(--secondary);
        border-radius: 8px;
    }

    .upper-img::before {
        content: url('../../images/index-16/paws.png');
    }

    .banner-box-one {
        background-image: url("../../images/index-16/Hero\ banner.png");
        padding: 14rem 0rem 37rem 0rem;
        background-repeat: no-repeat;
        background-size: cover;

        h1 {
            line-height: 82px;
            text-transform: uppercase;
            font-size: 65px;

            span {
                border-bottom: 1px solid var(--secondary);
            }
        }

        .btn {
            font-size: 14px;
            padding: 20px 25px;
        }
    }

    .bounse {
        margin-top: -100px;
    }

    .catagorise {
        background-image: url("../../images/index-16/w.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding: 22rem 0rem;

        .pro-price {
            ins {
                color: var(--secondary);
            }
        }

        .product article .pro-thumb {

            height: 200px;
            background-color: unset;

            .img-fluid {
                width: unset;
            }
        }

        .starts {
            i {
                font-size: 12px;
            }
        }

        .countdown-start {
            background-color: #a976c1;
            width: fit-content;
            padding: 6px 25px;
            border-radius: 8px;

            .couunt-box {
                span {
                    padding: 2px 10px;
                }
            }

            .pre-timer {
                span {
                    font-size: 18px;
                    font-weight: 700;
                    padding: 2px 6px;
                }

                small {
                    display: none;
                }
            }
        }
    }

    .brands-banner {
        .banner-caption.left-caption {
            flex-direction: row;
            align-items: center;
            justify-content: end;
        }

        .left {
            .banner-caption.left-caption {
                flex-direction: row;
                align-items: center;
                justify-content: start;
            }
        }
    }

    .best-selling {

        .pro-price {
            ins {
                color: var(--secondary);
            }
        }

        .product article .pro-thumb {

            height: 200px;
            background-color: unset;

            .img-fluid {
                width: unset;
            }
        }

        .starts {
            i {
                font-size: 12px;
            }
        }
    }

    .shop-fav {
        background-image: url("../../images/index-16/w.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        padding: 19rem 0rem;

        h1 {
            font-size: 58px;
            font-weight: 600;
        }

        h6 {
            font-size: 25px;
        }

        .btn {
            margin-top: 20px;
        }
    }

    .feature {
        .pro-price {
            ins {
                color: var(--secondary);
            }
        }

        .product article .pro-thumb {

            height: 200px;
            background-color: unset;

            .img-fluid {
                width: unset;
            }
        }

        .starts {
            i {
                font-size: 12px;
            }
        }
    }

    .blog-wrapper {
        background-image: url("../../images/index-16/w.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding: 19rem 0rem;

        svg {
            width: 20px;
        }
    }

}

html.boxed {

    .index-page-16 .banner-box-one h1,
    .index-page-16 .banner-box-one .h1 {
        line-height: 60px;
        text-transform: uppercase;
        font-size: 50px;
    }

    .index-page-16 .banner-box-one .btn {
        font-size: 14px;
        padding: 13px 23px;
    }

    .index-page-16 .banner-box-one {
        padding: 14rem 0rem 25rem 0rem;
    }
}

@media only screen and (max-width: 1500px) {
    .index-page-16 .banner-box-one {
        padding: 13rem 0rem 27rem 0rem;
    }

    .index-page-16 .bounse {
        margin-top: -90px;
    }

    .index-page-16 .catagorise {
        padding: 13rem 0rem;
    }

    .index-page-16 .shop-fav {
        padding: 13rem 0rem;
    }

    .index-page-16 .blog-wrapper {
        padding: 13rem 0rem;
    }
}

@media only screen and (max-width: 1300px) {
    .index-page-16 .banner-box-one {
        padding: 10rem 0rem 24rem 0rem;
    }

    .index-page-16 .banner-box-one h1 {
        line-height: 55px;
        font-size: 50px;
    }

    .index-page-16 .catagorise {
        padding: 7rem 0rem;
    }

    .index-page-16 .shop-fav {
        padding: 7rem 0rem;
    }

    .index-page-16 .blog-wrapper {
        padding: 7rem 0rem;
    }
}

@media only screen and (max-width: 1050px) {
    .index-page-16 .banner-box-one {
        padding: 9rem 0rem 19rem 0rem;
    }

    .index-page-16 .banner-box-one h1 {
        line-height: 46px;
        font-size: 40px;
    }

    .index-page-16 .product {
        margin-bottom: 25px;
    }

    .index-page-16 .brands-banner {
        h3 {
            font-size: 50px;
        }

        h4 {
            font-size: 30px;
        }

        p {
            font-size: 22px;
        }
    }

    .index-page-16 .shop-fav {
        h1 {
            font-size: 30px;
        }

        h6 {
            font-size: 22px;
        }
    }
}

@media only screen and (max-width: 991px) {

    .index-page-16 .banner-box-one {
        padding: 5rem 0rem 17rem 0rem;
    }

    .index-page-16 .banner-box-one h1 {
        line-height: 28px;
        font-size: 25px;
    }

    .index-page-16 .banner-box-one .btn {
        font-size: 14px;
        padding: 14px 12px;
    }

    .index-page-16 .bounse {
        margin-top: -44px;
    }
}

@media only screen and (max-width: 776px) {
    .index-page-16 .banner-box-one {
        padding: 5rem 0rem 14rem 0rem;
    }

    .index-page-16 .banner-box-one h1 {
        line-height: 23px;
        font-size: 20px;
    }

    .index-page-16 .banner-box-one .btn {
        font-size: 11px;
        padding: 8px 12px;
    }

    .index-page-16 .catagorise {
        padding: 5rem 0rem;
    }

    .index-page-16 .shop-fav {
        padding: 5rem 0rem;
    }

    .index-page-16 .blog-wrapper {
        padding: 5rem 0rem;
    }

    .index-page-16 .shop-fav h1,
    .index-page-16 .shop-fav h6,
    .index-page-16 .shop-fav div {
        text-align: center;
    }
}

@media only screen and (max-width: 500px) {

    .index-page-16 .banner-box-one h1,
    .index-page-16 .banner-box-one .h1 {
        line-height: 20px;
        font-size: 17px;
    }

    .index-page-16 .banner-box-one {
        padding: 3rem 0rem 9rem 0rem;
    }

    .index-page-16 .product2 article .pro-info .pro-heading {
        display: flex;
        flex-direction: column;
        align-items: center !important;
        justify-content: center;
        text-align: center;
    }
}

@media only screen and (max-width: 400px) {

    .index-page-16 .banner-box-one h1,
    .index-page-16 .banner-box-one .h1,
    .index-page-16 .banner-box-one .h1 {
        line-height: 15px;
        font-size: 14px;
    }

    .index-page-16 .banner-box-one .btn {
        font-size: 9px;
        padding: 5px 7px;
    }

    .index-page-16 .banner-box-one {
        padding: 2rem 0rem 7rem 0rem;
    }

    .index-page-16 .bounse {
        margin-top: -7px;
    }

    .index-page-16 .brands-banner h3,
    .index-page-16 .brands-banner .h3 {
        font-size: 35px;
    }

    .index-page-16 .brands-banner h4,
    .index-page-16 .brands-banner .h4 {
        font-size: 20px;
    }
}

.bodyrtl {

    .home-sixteen .product2 article .pro-info .pro-heading {
        display: unset;
        flex-direction: unset;
        align-items: unset;
        justify-content: unset;
        text-align: unset;
    }
}