.home7 {
    $secondary: #f9ac00;
    --secondary: #{$secondary};

    a,
    .btn {
        border-radius: 3px;
    }

    .pro-heading-title3 h5 {
        color: var(--secondary);
    }

    .header-mobile7 .header-maxi .navigation-mobile-container .navigation-mobile-toggler {
        background-color: var(--secondary);
    }

    .header-mobile7 .header-maxi .pro-header-right-options .dropdown .dropdown-menu .shopping-cart-items .item-summary {
        color: var(--secondary);
    }

    .rev_slider_wrapper .gyges .tp-thumb:hover .tp-thumb-img-wrap,
    .rev_slider_wrapper .gyges .tp-thumb.selected .tp-thumb-img-wrap {
        border-color: var(--secondary);
    }

    .blog7 article .pro-info h3 a:focus,
    .blog7 article .pro-info h3 a:hover {
        color: var(--secondary);
    }

    .tabs-content7 .nav .nav-link.active,
    .tabs-content7 .nav .nav-link:hover {
        background-color: var(--secondary);
    }

    .product2 article .pro-thumb .pro-hover-icons .icons .icon {
        &:hover {
            color: var(--secondary);
        }
    }

    .product2 article .pro-info h3 a {
        &:hover {
            color: var(--secondary);
        }
    }

    .header-seven .pro-header-right-options .search-field .search-field-module .fas:hover {
        color: var(--secondary);
    }

    .product2 article .pro-info .pro-price ins {
        color: var(--secondary);
    }

    .btn-secondary:not(:disabled):not(.disabled):active,
    .btn-secondary:not(:disabled):not(.disabled).active,
    .show>.btn-secondary.dropdown-toggle {
        background-color: var(--secondary);
        border-color: var(--secondary);
    }

    .flash-sale7 .pro-deals-info h3 {
        color: var(--secondary);
    }

    .footer-seven .contact-list li h4 {
        color: var(--secondary);
    }

    .footer-seven .contact-list li .fas {
        color: var(--secondary);
    }

    .alert-cookie7a .pt-discription .pro-detail span,
    .alert-cookie7a .pt-discription .btn-link,
    .alert-cookie7 .pt-discription .pro-price ins {
        color: var(--secondary);
    }

    .alert-cookie7,
    .alert-cookie7a {
        border-color: var(--secondary);
    }

    .top-sellers {

        .color-option {

            ul {
                padding-left: 0px;
            }
        }
    }
}

// index-7
.arrow7 .slick-arrow {
    top: -83px;
    height: auto;
    width: auto;
    z-index: 2;
}

.arrow7 .slick-arrow.slick-prev {
    right: 23px;
    left: auto;
}

.arrow7 .slick-arrow.slick-next {
    right: 10px;
}

.deal-index7-carousel-js.arrow7 {
    .slick-arrow {
        top: -30px;
    }
}

.pro-seven-rev.rev_slider_wrapper {

    .rev_slider {
        li {
            .BigBold-SubTitle {
                font-family: $font-family-base-medium;
                line-height: 1.2 !important;
                margin-top: 15px !important;
                color: $white;



            }

            .BigBold-Title {
                font-size: 62px !important;
                font-family: $font-family-base-bold;
                line-height: 60px !important;
                margin-top: 30px !important;
                color: $white;

                span {
                    font-family: $font-family-base;
                    font-size: 40px !important;
                }
            }

            .BigBold-Button {
                background-color: transparent;
                box-shadow: none !important;

            }
        }
    }

    .tp-leftarrow.tparrows.hermes {

        &::before {
            content: "\f104";
            font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
            font-weight: 900;
            font-size: 28px;
        }
    }

    .tp-rightarrow.tparrows.hermes {

        left: auto !important;
        right: 10px;

        &::before {
            content: "\f105";
            font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
            font-weight: 900;
            font-size: 28px;
        }
    }

    .hermes.tparrows {
        top: 50% !important;
        width: 40px !important;
        height: 40px;
        background-color: var(--color-article1-background) !important;
        color: var(--color-text) !important;
    }

    .hermes.tparrows:before {
        line-height: 40px !important;
        color: var(--color-text) !important;
    }
}

.pro-content.arrow-7 {
    .slick-slide img {
        width: 100%;
    }

    .slick-arrow.slick-prev {
        right: auto;
        left: -125px;
    }

    .slick-arrow.slick-next {
        right: -125px;
    }

    .slick-arrow:before {
        margin-left: 7px;
    }

    .slick-next::before {
        content: "\f105";
        font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
        font-weight: 900;
        font-size: 28px;
    }

    .slick-prev::before {
        content: "\f104";
        font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
        font-weight: 900;
        font-size: 28px;
    }

    .slick-arrow {
        top: 50%;
        height: 40px;
        width: 40px;
        background-color: var(--color-article1-background);
        z-index: 2;
    }
}

.tabs-content7.pro-content {
    .product {
        article {
            .pro-heading-with-icon {
                h3 {
                    a {
                        color: $white;
                    }
                }
            }
        }
    }
}

.tabs-content7 {

    .pro-heading-title h2,
    .pro-heading-title p {
        color: $white;
    }

    .nav {
        justify-content: flex-end;

        .nav-link {
            font-family: $font-family-base-bold;
            margin: 0 0.2rem;

            &:last-child {
                // padding-right: 0;
                margin-right: 0;
            }

            &.active,
            &:hover {
                background-color: var(--secondary);
                color: $white !important;

                &:last-child {
                    padding-right: 1rem;

                }
            }
        }
    }

    .product {
        article {
            .pro-info {
                .pre-heading {
                    h3 {
                        a {
                            color: $white;
                        }
                    }
                }


                .pro-price {

                    del {
                        color: $gray-300;
                    }
                }
            }
        }
    }
}

.featured-index7-carousel-js,
.featured-index7a-carousel-js {
    .slick-slide {
        >div:first-child {
            .productcard7a {
                margin-top: 0;
            }
        }
    }
}

.flash-sale7 {
    .pro-content {
        background-color: var(--color-article1-background);
        padding-bottom: 100px;
    }

    .pro-deals-info {
        h3 {
            color: var(--secondary);
            font-size: 18px;
            margin-bottom: 15px;
        }

        h2 {
            font-size: 34px;
            margin-bottom: 30px;
        }

        .pro-timer {
            margin-bottom: 30px;

            span {
                border: none;
                background-color: var(--color-page-background);
                border-radius: 3px;
            }
        }
    }
}

.banner-bg7 {
    .banner-inner {
        position: relative;
        overflow: hidden;

        .container-fluid {
            position: absolute;
            top: 0;
            height: 100%;
            width: 100%;
            display: flex;

            p {
                font-size: 11vw;
                line-height: 1;
                color: $white;
                font-family: $font-family-base-thin;

            }

            h2 {
                font-size: 11vw;
                line-height: 1;
                color: $white;
                margin-bottom: 25px;
            }
        }
    }
}

.model-style {
    background-color: var(--color-article1-background);

}

.newslater7 {

    .bold-text {
        font-size: 26px;
        text-align: center;
    }

    .low-text {
        font-size: 12px;
        font-weight: 600;
        text-align: center;
    }

    .input-group {
        .form-control {
            height: auto;
        }
    }
}

.blog7 {
    article {
        display: inline-block;
        width: 100%;

        .pro-thumb {
            position: relative;
            background-color: var(--color-article1-background);
        }

        .pro-info {
            margin-top: 30px;

            .blog-meta {
                color: var(--color-text);
            }

            .pro-heading {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                text-align: center;
            }

            h3 {
                font-family: $font-family-base-bold;
                font-size: $font-size-lg;
                color: var(--color-text);
                margin: 0;
                line-height: 1;
                margin-bottom: 10px;


                a {
                    color: var(--color-text);
                    text-decoration: none;
                    text-decoration: none;
                    white-space: normal;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;

                    &:focus,
                    &:hover {
                        outline: none;
                        color: var(--secondary);
                    }

                }
            }

            .pro-options {
                margin-top: 0;
            }

            .pro-price {
                display: flex;
                justify-content: center;
                line-height: 1;
                margin-bottom: 15px;

                del {
                    font-size: $font-size-base ;
                    color: var(--color-text-dark);
                    text-decoration: line-through;

                }

                ins {
                    font-family: $font-family-base-bold;
                    font-size: ($font-size-lg + 0.475);
                    color: var(--secondary);
                    text-decoration: none;
                    margin-right: 10px;
                }


            }

        }

        &:hover {
            .pro-hover-icons {
                overflow: hidden;
                opacity: 1;
                transition-timing-function: ease-in;
                transition: 0.4s;
                transform: translateY(0);
                -webkit-transform: translateY(0);
            }
        }
    }

}

@media only screen and (min-width:1100px) {
    .flash-sale7 .pro-content {
        padding-bottom: 70px;
    }
}

@media only screen and (max-width:1199px) {
    .pro-seven-rev.rev_slider_wrapper .rev_slider li .BigBold-Title {
        font-size: 50px !important;
    }

    .pro-seven-rev.rev_slider_wrapper .rev_slider li .BigBold-Title span {
        font-size: 30px !important;
    }

    .banner-bg7 .banner-inner .container-fluid h2,
    .banner-bg7 .banner-inner .container-fluid p {
        line-height: 1;
    }

}

@media only screen and (max-width:991px) {
    .tabs-content7 {
        .pro-heading-title3 {
            padding-bottom: 30px;
        }

        .nav {
            justify-content: flex-start;
            margin-bottom: 30px;
        }
    }

    .product2 article .pro-thumb {
        height: 235px;
    }
}

@media only screen and (max-width:767px) {
    .tabs-content7 {
        .nav {
            justify-content: center;
        }
    }

    .pro-seven-rev.rev_slider_wrapper .rev_slider li .BigBold-Title {
        font-size: 40px !important;


    }

    .pro-content {
        .model-style {
            .inner {
                padding: 30px 0;
            }
        }
    }

    .res-padd {
        padding-top: 30px;
    }
    .pro-timer {
        justify-content: center;
    }
}

@media only screen and (max-width:575px) {
    .pro-seven-rev.rev_slider_wrapper .rev_slider li .BigBold-Title {
        line-height: 35px !important;
        margin-top: 25px !important;
    }

    .pro-seven-rev.rev_slider_wrapper .rev_slider li .BigBold-Title span {
        font-size: 20px !important;
        line-height: 35px !important;
    }

    .product2 article .pro-thumb {
        height: auto;
    }
}

.bodyrtl {
    .tp-leftarrow.tparrows.hermes {
        left: 52px !important;
    }

    .tabs-content7 .nav .nav-link:last-child {
        padding-left: 0;
        padding-right: 1rem;

        &:hover {
            padding-left: 1rem;
        }
    }

    .pro-content.arrow-7 .slick-arrow.slick-next {
        right: -25px;
        left: auto;
    }

    .arrow7 .slick-arrow.slick-next {
        left: 31px;
        right: auto;
    }

    .arrow7 .slick-arrow.slick-prev {
        right: auto;
        left: 15px;
    }

    .flash-sale7 .pro-deals-info .pro-timer {
        justify-content: flex-start;
    }

    .flash-sale7 .pro-deals-info .pro-timer span {
        margin-left: 15px;
        margin-right: 0;
    }

    .productcard7a article {
        direction: rtl;

        .pro-info {
            .pro-price {
                direction: rtl;

                ins {
                    margin-right: 0;
                    margin-left: 10px;
                }
            }
        }
    }

}