.header-fourteen {
    .header-maxi {
        padding-top: 1.9375rem;
        padding-bottom: 1.9375rem;

        .pro-header-right-options .dropdown .dropdown-menu {
            transform: translate3d(-23px, 5px, 0px) !important;
        }

        .pro-header-right-options li .btn-light {
            background-color: transparent;
            border: 1px solid var(--color-border);
            width: 45px;
            height: 45px;
        }

        .pro-header-right-options li {
            margin-left: 30px;
        }
    }

    .bg-header-bar {
        background-color: var(--color-article1-background);
    }
}

@media only screen and (min-width: 700px) {
    .header-fourteen {
        .header-maxi .navigation-mobile-container #navigation-mobile.navigation-active {
            width: 40% !important;
        }

        .header-maxi .navigation-mobile-container #navigation-mobile .logout-main {
            width: 100%;
        }

        .header-maxi .navigation-mobile-container #navigation-mobile .main-manu {
            width: 100%;
            text-align: left;
        }
    }
}