* {
    padding: 0;
    margin: 0;
    letter-spacing: 1px;

    a {
        &:focus {
            outline: none;
        }
    }
}

body {
    direction: ltr;
    text-align: left;
}

h2,
h3,
h4 {
    font-family: $font-family-base-bold;
    color: var(--color-text);
}

.font-sizebig {
    font-size: 22px;
}

p {
    // font-family: $font-family-base;
    color: var(--color-text);
}

.bodyrtl {
    direction: rtl;
    text-align: right;
}

html.boxed body {
    margin: 0 auto;
    -webkit-box-shadow: 0 4px 30px rgba(51, 51, 51, 0.1);
    box-shadow: 0 4px 30px rgba(51, 51, 51, 0.1);
}

label {
    color: var(--color-text);
}

.bg-secondary {
    background-color: var(--secondary) !important;

}

.bg-primary {
    background-color: var(--primary) !important;
}

.text-secondary {
    color: var(--secondary) !important;
}

.text-primary {
    color: var(--primary) !important;
}

@media only screen and (min-width:992px) and (max-width:1199px) {
    .container {
        max-width: 1000px;
    }
}

@media only screen and (min-width:1385px) {
    html.boxed {
        body {
            max-width: 1300px;
        }

        #stickyHeader {
            max-width: 1300px;
        }

        .header-one {
            max-width: 1300px;
        }

        .container {
            max-width: 1200px;
        }

        body.container-w {
            max-width: 1400px;

            #stickyHeader {
                max-width: 1400px;
            }

            .header-eight {
                max-width: 1400px;
            }

            .container {
                max-width: 1385px;
            }
        }

    }

}

