.pro-heading-title5 {
    padding-bottom: 60px;
    text-align: center;

    .inner-heading {
        h1 {
            color: var(--color-text);
            text-transform: capitalize;
            font-family: $font-family-base-bold;
            line-height: 1.5;
            margin: -9px 0 0 0;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;

        }

        h2 {
            color: var(--color-text);
            text-transform: capitalize;
            font-family: $font-family-base-bold;
            line-height: 1.5;
            margin: -9px 0 0 0;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }

        p {


            text-align: center;
            line-height: 1.5;
            letter-spacing: 1.5px;
            font-size: 16px;
            color: var(--color-text-secondary);
            margin: 0 0 -6px 0;
            text-decoration: none;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;

            span {
                &::before {
                    position: absolute;
                    background-color: var(--color-text-dark);
                    border: 1px solid;
                    border-color: var(--color-border);
                    padding: 5px;
                    -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
                    left: 45%;
                    bottom: 15px;
                    content: "";
                    z-index: 1;
                }

                &::after {
                    position: absolute;
                    background-color: var(--color-text-dark);
                    border: 1px solid;
                    border-color: var(--color-border);
                    padding: 5px;
                    -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
                    left: 55%;
                    bottom: 15px;
                    content: "";
                    z-index: 1;
                }

            }
        }

        .linestyle {
            position: relative;
            width: 100%;
            padding-bottom: 30px;
            display: flex;
            justify-content: center;
            align-items: center;

            &::before {
                position: absolute;
                background-color: var(--color-text-dark);
                border: 1px solid;
                border-color: var(--color-border);
                padding: 5px;
                -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
                left: 50%;
                bottom: 15px;
                content: "";
                z-index: 1;
            }


            &::after {
                content: "";
                position: absolute;
                bottom: 20px;
                width: 55%;
                height: 1px;
                background-color: var(--color-text-dark);

            }

        }

    }
}

