}

select {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;
    border: none;
    box-shadow: none !important;

    option {
        font-size: $font-size-base;
        color: var(--color-text);
    }

    &:focus {
        box-shadow: none;
    }
}

.form-control {
    border-width: 2px;
    border-radius: 3px;
    border-color: var(--color-border);
    background-color: var(--color-article1-background);
    color: var(--text-color-light);
}

.select-control {
    float: left;
    position: relative;

    .form-control {
        padding: 0.375rem 1.5rem 0.375rem .75rem;
        border-width: 2px;
        border-radius: 3px;
        border-color: var(--color-border);
    }

    &::before {
        font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
        font-weight: 900;
        content: "\f107";
        position: absolute;
        color: var(--color-text);
        bottom: 8px;
        right: 8px;
        z-index: 1;
        font-size: 12px;
    }
}

.form-control {
    &:focus {
        z-index: 0 !important;
        box-shadow: none;
        border: 2px solid;
        border-color: var(--color-border);
        background-color: var(--color-article1-background);
        color: var(--text-color-light);
    }
}

.dropdown-toggle {
    position: relative;

    &::after {
        position: absolute;
        right: -14px;
        bottom: 7px;
        font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
        font-weight: 900;
        content: "\f107";
        border: none;
        font-size: 11px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 5px;
        width: 10px;
    }
}

.pro-options {
    .color-option {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 20px;

        b {
            font-size: $font-size-base;
            color: var(--color-text);
        }

        ul {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            margin-bottom: 0;

            li {
                display: inline-block;
                list-style: none;
                margin-left: 10px;

                a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 20px;
                    width: 20px;
                    outline-width: 2px;
                    outline-style: solid;
                    outline-color: transparent;
                    outline-offset: 2px;

                    &.green {
                        background-color: #7fffff;
                    }

                    &.pink {
                        background-color: #f7b6d4;
                    }

                    &.blue {
                        background-color: #7fd6f7;
                    }

                    &.yellow {
                        background-color: #d4af37;
                    }

                    &.red {
                        background-color: $red;
                    }
                }

                &.active a {
                    outline-color: var(--color-text) !important;
                }
            }
        }
    }

    .size-option {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 20px;

