.newsletterfooter-v-six {
    background: var(--color-article1-background);
    padding-bottom: 30px;
    .newsletter{
        align-items: center !important;
    }

    .input-group {
        border: none !important;
    }

    h2 {
        color: var(--base-color-dark, #1C1C1C);
        text-align: center;
        font-feature-settings: 'clig' off, 'liga' off;
        /* Title-H4 */
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        /* 140% */
        letter-spacing: -0.2px;
        margin: 0;
    }

    p {
        color: var(--base-color-gray-800, #606060);
        text-align: center;
        font-feature-settings: 'clig' off,

            'liga' off;
        margin-top: 5px;
        /* Text-info */
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        /* 150% */
        letter-spacing: -0.2px;
    }
}
.footer-main {
    section {
        border-top: 1px solid var(--gray-300, #DEE2E7);
        background: var(--color-article1-background);
        span{
            color: var(--color-text);
        }
        i{
            color: var(--color-text);
        }
    }
}
@media only screen and (min-width:576px) and (max-width:768px){
    .home-21 .newsletterfooter-v-six .newsletter .input-group {
        border: none !important;
        width: 75% !important;
    }
}
@media only screen and (min-width:320px) and (max-width:576px){
    .home-21 .newsletterfooter-v-six
     .input-group {
        border: none !important;
        width: 100% !important;
    }
}