.product {
    .pro-rating {
        .fas {
            color: $yellow;
        }
    }

    article {
        display: inline-block;
        width: 100%;
        padding: 0;

        .pro-thumb {
            position: relative;

            .img-fluid {
                width: 100%;
            }

            .badges {
                position: absolute;
                left: 14px;
                top: 14px;

                .badge {
                    display: inline-block;
                    margin-bottom: 14px;
                    margin-right: 5px;

                    &:last-child {
                        margin-bottom: 0;
                        margin-right: 0;
                    }
                }

                .badge-info {
                    background-color: $info;
                    color: $white;
                }

                .badge-danger {
                    background-color: $danger;
                    color: $white;
                }

                .badge-success {
                    background-color: $success;
                    color: $white;
                }

                .badge-dark {
                    background-color: $dark;
                    color: $white;
                }
            }
        }

        .pro-info {

            margin-top: 15px;
            margin-bottom: 0px;

            .pro-info {
                font-size: $font-size-base;
                color: var(--color-text-secondary);
            }

            .pro-category {
                font-family: $font-family-base;
                color: var(--color-text-secondary);
                text-transform: uppercase;
                line-height: 1.5;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }

            h3 {
                font-family: $font-family-base-bold;
                font-size: $font-size-lg;
                color: var(--color-text);
                margin: 0;
                line-height: 1.5;

                a {
                    color: var(--color-text);
                    text-decoration: none;
                    white-space: normal;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;

                    &:focus,
                    &:hover {
                        outline: none;
                        color: var(--secondary);
                    }

                }
            }


            .pro-price {
                display: flex;
                justify-content: space-between;
                align-items: center;

                del {
                    font-size: $font-size-base ;
                    color: var(--color-text-dark);
                    text-decoration: line-through;

                }

                ins {
                    font-family: $font-family-base-bold;
                    font-size: ($font-size-lg + 0.475);
                    color: var(--secondary);
                    text-decoration: none;
                    margin-right: 10px;
                }

                .icon {

                    outline: none;
                    border: none;
                    position: relative;
                    border-radius: 200px;
                    display: flex;
                    width: 40px;
                    height: 40px;
                    margin: 0px;
                    justify-content: center;
                    align-items: center;
                    text-decoration: none;
                    box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
                    transition-duration: 0.3s;

                }

            }
        }

        .pro-options {
            margin-top: 10px;

            div:last-child {
                margin-bottom: 5px;
            }

            .color-option,
            .size-option {
                ul {
                    li {
                        &:first-child {
                            margin-left: 5px;
                        }
                    }
                }
            }
        }

        .pro-hover-icons {
            opacity: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            overflow: hidden;

            z-index: 2;
            -webkit-transition: 1.2s ease-out;
            transition: 1.2s ease-out;
            transform: translateY(40px);
            -webkit-transform: translateY(40px);

            .icons {
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                margin-bottom: 14px;

                .icon {
                    outline: none;
                    border: none;
                    position: relative;
                    // background-color:  $white;
                    border-radius: 200px;
                    // color: #9496b0;
                    display: flex;
                    width: 40px;
                    height: 40px;
                    margin: 7px;
                    justify-content: center;
                    align-items: center;
                    text-decoration: none;
                    box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
                    transition-duration: 0.3s;

                }
            }

        }
    }

    &:hover {
        .pro-hover-icons {
            overflow: hidden;
            opacity: 1;
            transition-timing-function: ease-in;

            /* A litttttle slower on the way in */
            transition: 0.4s;
            /* Move into place */
            transform: translateY(0);
            -webkit-transform: translateY(0);
        }
    }
}

.product-list {
    article {
        .pro-info {
            margin-top: 0;

            h3 {
                margin-bottom: 10px;
            }

            p {
                color: var(--color-text);
            }

            .pro-price {
                margin-bottom: 15px;

            }
        }


        .icons {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            overflow: hidden;

            .icon {
                outline: none;
                border: none;
                position: relative;
                border-radius: 200px;
                display: flex;
                width: 40px;
                height: 40px;
                margin: 0px 7px 0 0px;
                justify-content: center;
                align-items: center;
                text-decoration: none;
                box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
                transition-duration: 0.3s;

            }
        }
    }
}

