.productcard1 {
    article {
        border: 1px solid;
        border-color: var(--color-border);
        border-radius: 5px;

        .pro-thumb {
            overflow: hidden;

            .img-fluid {
                border-radius: 5px;
            }

            .back {
                opacity: 0;
                position: absolute;
                -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
                top: 0;
                left: 0;
                -webkit-transition: all 0.5s ease;
                transition: all 0.5s ease;
                -webkit-transform: translateX(-100px);
                transform: translateX(-100px);
                width: 100%;
            }

            .badges {
                .badge {
                    background-color: transparent;
                    display: block;
                    margin-bottom: 5px;
                    margin-right: 5px;
                    width: fit-content;
                }

                .badge-info {
                    border: 1px solid $info;
                    color: $info;
                }

                .badge-danger {
                    border: 1px solid $danger;
                    color: $danger;
                }
            }
        }

        .pro-info {
            .pro-text {
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid;
                border-color: var(--color-border);
                padding: 0 10px;

                h3 {
                    margin-top: -5px;
                }

                .icon {
                    outline: none;
                    border: none;
                    border-color: transparent;
                    position: relative;
                    background-color: transparent;

                    box-shadow: none;

                    &:hover {
                        color: var(--secondary);
                    }

                }
            }

            .pro-options {
                padding: 10px 5px;
                margin: 0;
                border-bottom: 1px solid;
                border-color: var(--color-border);

                .size-option ul li a.size-select {
                    height: 25px;
                    padding: 0 5px;
                }

                .size-option,
                .color-option {
                    margin-bottom: 0;
                }
            }

            .pro-buttons {
                display: flex;
                justify-content: space-between;
                padding: 10px;

                .btn {
                    padding: 12px 18px;
                    font-size: 10px;
                }
            }
        }

        &:hover {
            .pro-thumb {

                .back {
                    opacity: 1;
                    -webkit-transition: all 0.5s ease;
                    transition: all 0.5s ease;
                    -webkit-transform: translateX(0);
                    transform: translateX(0);
                }
            }
        }
    }

}

