}

.topsaller {
    // height: 29vmax;
    overflow: hidden;
    background-color: var(--color-white-background);
    border-radius: 8px;

    .title {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background-color: var(--secondary);
        padding: 15px 0.5rem;

