}

.minilistcard {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--color-border);
    margin-top: 17px;

    .pro-thumb {
        width: 100px;
        overflow: hidden;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            width: 100%;
            // height: 100%;
            object-fit: cover;
        }
    }

    .pro-info {
        width: calc(100% - 135px);
        margin-left: 10px;

        .pro-heading {
            h3 {
                font-size: $font-size-lg;
                transition: color .4s;
                text-decoration: none;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
        }

        .pro-price {
            ins {
                font-family: $font-family-base-bold;
                font-size: $font-size-lg;
                color: var(--secondary);
                text-decoration: none;
                margin-right: 0px;
            }

            del {
                font-family: $font-family-base-bold;
                font-size: $font-size-base;
                color: var(--color-text-dark);
                text-decoration: line-through;
            }
        }

        .liked-button {
            .icon {
                font-size: 20px;

                &:first-child {
                    margin-right: 5px;
                }
            }
        }
    }
}

