.home10 {

    @media only screen and (min-width: 1200px) {
        .container {
            max-width: 1350px;
        }
    }

}

.index-page-10 {
    $duration: 30s;

    @-webkit-keyframes ticker {
        0% {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
            visibility: visible;
        }

        100% {
            -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
        }
    }

    @keyframes ticker {
        0% {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
            visibility: visible;
        }

        100% {
            -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
        }
    }

    .ticker-wrap {

        position: fixed;
        bottom: 0;
        width: 100%;
        overflow: hidden;
        height: 4rem;
        background-color: rgba(#000, 0.9);
        padding-left: 100%;
        box-sizing: content-box;

        .ticker {

            display: inline-block;
            height: 4rem;
            line-height: 4rem;
            white-space: nowrap;
            padding-right: 100%;
            box-sizing: content-box;

            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
            -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
            -webkit-animation-name: ticker;
            animation-name: ticker;
            -webkit-animation-duration: $duration;
            animation-duration: $duration;

            &__item {

                display: inline-block;

                padding: 0 2rem;
                font-size: 2rem;
                color: white;

            }
        }
    }

}

h2::selection {
    background-color: #8e44ad;
    color: white;
}


.arrival-carousel-js10 .slick-slide>div:first-child .product17 {
    margin-top: 0;
}

.arrival-carousel-js10,
.flash-carousel-js10 {
    .slick-arrow {
        top: -82px;
        border-radius: 200px;

        &::before {
            color: var(--secondary);
        }
    }

    .product17 {
        margin-top: 36px;
    }
}

.productfalshsale {
    background-color: var(--color-white-background);
    padding: 15px;

    article {
        display: inline-block;
        width: 100%;
        padding: 0;

        .pro-thumb {
            position: relative;
            height: 394px;
            width: 100%;
            overflow: hidden;

            .img-fluid {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .badges {
                position: absolute;
                left: 14px;
                top: 14px;

                .badge {
                    display: inline-block;
                    margin-bottom: 14px;
                    margin-right: 5px;

                    &:last-child {
                        margin-bottom: 0;
                        margin-right: 0;
                    }
                }
            }
        }

        .pro-info {
            text-align: center;
            margin-top: 15px;
            margin-bottom: 0px;
            padding-bottom: 15px;
            border-bottom: 1px solid var(--color-border);

            .pro-info {
                font-size: $font-size-base;
                color: var(--color-text-secondary);
            }

            .pro-category {
                font-family: $font-family-base;
                color: var(--color-text-secondary);
                text-transform: uppercase;
                line-height: 1.5;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }

            h3 {
                font-family: $font-family-base-medium;
                font-size: ($font-size-lg + 0.5rem);
                color: var(--color-text);
                margin: 0;
                line-height: 1.5;

                a {
                    color: var(--color-text);
                    text-decoration: none;
                    text-decoration: none;
                    white-space: normal;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;

                    &:focus,
                    &:hover {
                        outline: none;
                        color: var(--secondary);
                    }

                }
            }


            .pro-price {
                display: flex;
                justify-content: center;
                align-items: center;

                del {
                    font-size: $font-size-base ;
                    color: var(--color-text-dark);
                    text-decoration: line-through;

                }

                ins {
                    font-family: $font-family-base-bold;
                    font-size: ($font-size-lg + 0.475);
                    color: var(--secondary);
                    text-decoration: none;
                    margin-right: 10px;
                }

                .icon {

                    outline: none;
                    border: none;
                    position: relative;
                    margin: 0px;
                    transition-duration: 0.3s;

                    i {
                        font-size: ($font-size-lg + 0.3rem);
                        color: var(--color-text);
                    }

                }

            }
        }

        .ratings {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 5px;

            p {
                margin-bottom: 0;
                margin-left: 5px;
                color: var(--color-text-secondary)
            }
        }

        .pro-rating {
            .fas {
                color: $yellow;
            }
        }

        .falsing {
            text-align: center;
            margin-top: 15px;

            p {
                font-size: $font-size-lg;
                margin-bottom: 15px;

            }
        }

        .pro5-timer {
            display: flex;
            justify-content: center;

            span {
                padding: 0 15px;
                color: var(--secondary);
                font-family: $font-family-base-bold;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                small {
                    display: block;
                    font-family: $font-family-base;
                    color: $black;
                }
            }
        }

        .clickme {
            margin-top: 15px;
        }

        .icon {
            outline: none;
            border: none;
            position: relative;
            margin: 0px;
            transition-duration: 0.3s;
            background-color: transparent;

            i {
                font-size: 1.3rem;
                color: var(--color-text);
            }
        }
    }
}

.cardwithbg {
    // background-color: var(--color-article1-background);
    padding-bottom: 100px;
}

.fullwidth-banner10 {
    position: relative;
    background-image: url("../images/index-2/fullwidthbanner.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 582px;

    &:before {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        content: '';
        display: block;
    }

    .parallax-banner-text {
        position: relative;

        h2 {
            font-size: 4rem;
            font-family: $font-family-base-bold;
            color: $white;
            margin: 0;
        }

        p {
            font-family: $font-family-base;
            font-size: 1rem;
            color: $white;
        }

    }
}

.pro-content .tabssect {
    .pro-heading-title {
        padding-bottom: 30px;
    }

    .banner-withimage {
        margin-top: 30px;
        height: 180px;
    }
}

.clcicklinkM {

    .nav-pills .nav-link.active,
    .nav-pills .show>.nav-link {
        color: var(--secondary);
        background-color: transparent;
    }

    .nav {
        .nav-link {
            color: var(--color-text);
            padding: 0;
            padding-left: 10px;
        }
    }

}

.banner-withimage {
    position: relative;
    background-color: var(--color-article1-background);
    overflow: hidden;
    margin-bottom: 0;
    box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);

    padding: 15px;
    width: 100%;
    display: flex;

    .imgs {
        width: 38%;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }


    .banner-caption {
        width: calc(100% - 37%);
        margin-left: 15px;

        .ratings {
            display: flex;
            align-items: center;
            margin-bottom: 15px;

            p {
                margin-bottom: 0;
                margin-left: 5px;
                color: var(--color-text-secondary)
            }
        }

        .pro-rating {
            .fas {
                color: $yellow;
            }
        }

        .pro-price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 10px;

            del {
                font-size: $font-size-base ;
                color: var(--color-text-dark);
                text-decoration: line-through;

            }

            ins {
                font-family: $font-family-base-medium;
                font-size: ($font-size-lg + 0.475);
                color: var(--secondary);
                text-decoration: none;
                margin-right: 10px;
            }

            .icon {

                outline: none;
                border: none;
                position: relative;
                border-radius: 200px;
                color: #9496b0;
                display: flex;
                width: 40px;
                height: 40px;
                margin: 7px;
                justify-content: center;
                align-items: center;
                text-decoration: none;
                box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
                transition-duration: 0.3s;
                background-color: $body-bg-secondary;

                &:hover {
                    background-color: $dark;
                    border-color: $dark ;
                    color: color-yiq($dark);
                }



            }

        }

        h3 {
            color: var(--color-text);
            font-family: $font-family-base-medium;
            margin: 0;
            font-size: 14px;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            a {
                text-decoration: none;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
        }

        p {
            color: var(--secondary);
        }

        .iconclick {
            margin-top: 15px;

            .icon {
                margin-right: 5px;

            }
        }

        .icon {

            outline: none;
            border: none;
            position: relative;
            margin: 0px;
            transition-duration: 0.3s;
            background-color: transparent;

            i {
                font-size: ($font-size-lg + 0.3rem);
                color: var(--color-text);
            }

        }

    }

}

.arrow-style10 {

    .slick-arrow {
        top: -82px;
        height: 25px;
        width: 25px;
        border-radius: 200px;
        z-index: 2;
        display: flex !important;
        align-items: center;
        justify-content: center;
        background-color: var(--light);

        &::before {
            display: flex;
            width: 100%;
            align-items: center;
            justify-content: center;
            position: absolute;
            font-size: 20px;
            color: var(--secondary);
        }

        &:hover,
        &:focus {
            background-color: var(--light);
        }



        &.slick-prev {
            right: 45px;
            left: auto;
        }

    }
}


@media only screen and (max-width:992px) {
    .productfalshsale article .pro-thumb {
        height: auto;
    }

    .cardcarousel {
        margin-top: 30px;
    }

    .cardcarousel:first-child {
        margin-top: 0;
    }

    .arrow-style10 .slick-arrow {
        display: none !important;
    }
}

@media only screen and (max-width:768px) {
    .fullwidth-banner10 .parallax-banner-text h2 {
        font-size: 2rem;
    }

    .productfalshsale article .pro-info h3 {
        font-size: $font-size-lg;
    }
}

@media only screen and (max-width:576px) {
    .index-page-10 {
        .banner-withimage .imgs {
            width: 40%;
            height: 163px;
            margin: auto;
        }

        .pro-content .clcicklinkM {
            justify-content: center;
        }

        .pro-content .tabssect .banner-withimage {
            margin-top: 30px;
            height: unset;
            text-align: center;
        }

        .banner-withimage .banner-caption .ratings {
            justify-content: center;
        }

        .banner-withimage .banner-caption .pro-price {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 10px;
        }

        .pro-timer .days,
        .pro-timer .hours,
        .pro-timer .mintues,
        .pro-timer .seconds {
            float: left;
            padding: 23px;
            font-family: "Poppins-Bold", sans-serif;
            font-size: 1rem;
            color: var(--color-text);
            border: 2px solid var(--color-text-secondary);
            width: 85px;
            height: 85px;
            margin-right: 15px;
            text-align: center;
        }
    }

    .banner-withimage {
        flex-direction: column;

        .imgs {
            width: 100%;
        }

        .banner-caption {
            width: 100%;
            margin-left: 0;
            margin-right: 0 !important;
            margin-top: 15px;
        }
    }
}

@media only screen and (max-width:350px) {
    .fullwidth-banner10 .parallax-banner-text h2 {
        font-size: 27px;
    }
}

.bodyrtl {
    .banner-withimage .banner-caption {
        margin-right: 15px;
        margin-left: 0;
    }

    .arrow-style10 .slick-arrow.slick-prev {
        left: 15px;
        right: auto;
    }

    .arrow-style10 .slick-arrow.slick-next {
        right: auto;
        left: 55px;
    }
}