.footer-15 {
    .footerlinkheading {
        border-bottom: unset !important;
    }

    .footer-main {
        padding: 70px 0 0 0;
        background-color: var(--secondary);
    }

    ul {
        li {
            a {
                color: white;
            }
        }
    }

    .footer-icons {
        ul {
            li {
                a {
                    padding: 6px;
                    margin: 4px;
                }
            }
        }

        .footer-social-icon {
            a {
                margin: 10px;
            }

        }
    }

    .footerbottomin {
        ul {
            flex-wrap: wrap-reverse;
        }

        li {
            margin: 6px;
            padding: 5px;
        }
    }

    .footerbottomin-bottom {
        background-color: #dffbfc;
    }
}



@media only screen and (max-width: 500px) {

    .footer-v-six {
        margin: 20px 0 0 0;
    }

}