.home13 {
    $secondary: #f2980c;
    --secondary: #{$secondary};
}

.index-page-13 {

    // BANNER
    .banner-box-one {
        background-image: url("../../images/index-13/Banner1.png");
        padding: 15rem 0rem 16rem 0rem;
        background-repeat: no-repeat;
        background-size: cover;

        h1 {
            line-height: 82px;
            text-transform: uppercase;
            font-family: cursive;
            font-size: 65px;
        }

        .btn {
            border-radius: 9px;
            font-size: 14px;
            padding: 20px 25px;
        }
    }

    .text-body {
        color: var(--color-text) !important;
    }

    .text-font {
        font-family: cursive !important;
    }

    .bg-white {
        background-color: var(--color-page-background) !important;
    }

    // FRESH BURGER
    .fresh-burger {
        .banner-image {
            height: 400px;
        }

        .banner-caption {
            h2 {
                font-family: cursive;
            }
        }

        .banners-content4 .banner-caption {
            text-align: left;
            height: 100%;
            align-items: center;
            display: grid;

            .btn {
                border: 1px solid var(--secondary);
                padding: 16px 25px;
                border-radius: 9px;
                font-size: 14px;
                width: fit-content;
            }
        }

        .fullwidth-banner4-2 {
            background-color: unset;
        }

        .date-selector {
            background-color: #f0502c;
            padding: 20px 130px;
            border-radius: 40px;

            .btn {
                padding: 20px 30px;
                background-color: var(--text-color-primary);
                color: #f0502c;
                width: fit-content;
                border-radius: 10px;
            }

            h1 {
                font-family: cursive;
            }

            select,
            input {
                height: 55px;
                background: transparent;
                color: var(--text-color-primary);
                border-radius: 12px;
                border-width: 1px;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
                background-position: 13px;
                background-size: 13px;
                padding-right: 18px;
                background-position-x: 303px;
            }
        }
    }

    .banners-content4 .banner-image {
        margin-bottom: 7px;
    }

    // bounse
    .bounse {
        @keyframes bounce {

            0%,
            100%,
            20%,
            50%,
            80% {
                -webkit-transform: translateY(0);
                -ms-transform: translateY(0);
                transform: translateY(0)
            }

            40% {
                -webkit-transform: translateY(-30px);
                -ms-transform: translateY(-30px);
                transform: translateY(-30px)
            }

            60% {
                -webkit-transform: translateY(-15px);
                -ms-transform: translateY(-15px);
                transform: translateY(-15px)
            }
        }

        .bounce_button {
            -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
            -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
            -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            -webkit-animation-iteration-count: infinite;
        }

        .bounce_button:hover {
            cursor: pointer;
            animation-name: bounce;
            -moz-animation-name: bounce;
        }
    }

    .slider-banner {

        .prev0,
        .next0 {
            position: absolute;
            top: 50%;
            font-size: 26px;
            border: none;
            background: transparent;
            left: 30px;
        }

        .next0 {
            right: 30px;
            left: unset;
        }

        .tab-carousel-js2 {
            .border {
                background-color: var(--color-page-background);
                // background-color: #f6f6f6;
            }

            .starts {
                i {
                    font-size: 10px;
                    color: #f2980c;
                }
            }
        }

    }

    .fullwidth-banner4-2 {
        background-color: var(--color-article13-background);
    }

    .best-seller {
        background-image: url('../../images/index-13/menu\ bac.png');
        background-repeat: no-repeat;
        background-size: cover;

    }

    .insta-blog {
        .insta-row {
            margin-top: -7px;
        }

        .insta-detail .instaawesome {
            background: black !important;
        }
    }

    .banner-booking {
        .fullwidth-banner {
            background-image: url("../../images/index-13/CTAimage.png");
            height: unset;
            padding: 3rem;
            background-attachment: unset;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;

            .btn {
                padding: 30px 64px;
                border-radius: 12px;
                color: #936dc4;
                font-size: 23px;

                i {
                    color: #936dc4;
                }
            }
        }
    }

    .event-wrapper {

        .event,
        .text-card {
            background-color: var(--color-article13-background);
        }
    }
}

.bodyrtl {
    .rtl-flex {
        justify-content: end;
    }

    .fresh-burger .banners-content4 .banner-caption .btn {
        justify-self: end;
    }

    .fresh-burger {
        .banners-content4 .container .banner-caption.left-caption {
            padding-right: 30px;
            padding-left: 40px;
            text-align: left !important;
            width: 100%;
        }
    }

    .fresh-burger .date-selector select,
    .index-page-13 .fresh-burger .date-selector input {
        background-position-x: 17px;
    }

    .fresh-burger .date-selector select {
        background-position-x: 46px !important;
    }
}

html.boxed {
    .index-page-13 .banner-box-one {
        padding: 12rem 0rem 12rem 0rem;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .index-page-13 .banner-box-one h1,
    .index-page-13 .banner-box-one .h1 {
        line-height: 67px;
        font-size: 51px;
    }
}

@media only screen and (max-width: 1350px) {
    .index-page-13 .banner-box-one {
        padding: 12rem 0rem 9rem 0rem;
    }

    .index-page-13 .banner-box-one h1 {
        font-size: 50px;
        line-height: 57px;
    }
}

@media only screen and (max-width: 1100px) {
    .index-page-13 .banner-box-one {
        padding: 10rem 0rem 8rem 0rem;
    }

    .index-page-13 .banner-box-one h1 {
        font-size: 36px;
        line-height: 50px;
    }

    .index-page-13 .banner-booking .fullwidth-banner .btn {
        padding: 21px 39px;
        font-size: 17px;
    }

    .fullwidth-banner .parallax-banner-text h2,
    .fullwidth-banner .parallax-banner-text .h2 {
        font-size: 2rem;
        line-height: 1.5;
        white-space: unset;
        overflow: unset;
        text-overflow: unset;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }

    .fullwidth-banner .parallax-banner-text {
        text-align: center;
        padding: 0px;
    }
}

@media only screen and (max-width: 991px) {
    .index-page-13 .banner-box-one {
        padding: 7rem 0rem 8rem 0rem;
    }

    .index-page-13 .banner-box-one h1 {
        font-size: 30px;
        line-height: 43px;
    }

    .index-page-13 .banner-box-one .btn {
        padding: 14px 21px;
    }

    .index-page-13 .fresh-burger .date-selector {
        padding: 6px 32px;
        border-radius: 35px;
    }

    .index-page-13 .fresh-burger .date-selector .btn {
        padding: 15px 21px;
    }

    .index-page-13 .fresh-burger .date-selector select {
        background-position-x: 275px;
    }
}

@media only screen and (max-width: 776px) {
    .index-page-13 .banner-booking .fullwidth-banner .btn {
        padding: 15px 25px;
        font-size: 14px;
    }

    .index-page-13 .banner-box-one {
        padding: 6rem 0rem 6rem 0rem;
    }

    p .index-page-13 .banner-box-one h1 {
        font-size: 25px;
        line-height: 35px;
    }

    .index-page-13 .banner-box-one .btn {
        padding: 12px 15px;
    }

    .index-page-13 .fresh-burger .date-selector select {
        background-position-x: 365px;
        margin-top: 12px;
        margin-bottom: 12px;
    }

    .fullwidth-banner .parallax-banner-text h2 {
        font-size: 1.7rem;
        line-height: 1.2;
    }
}

@media only screen and (max-width: 500px) {
    .header-fourteen .header-maxi {
        padding-top: 1.19375rem;
        padding-bottom: 1.1375rem;
    }

    .header-fourteen .header-maxi .pro-header-right-options li {
        margin-left: 6px;
    }

    .header-fourteen .header-mobile .header-maxi .logo {
        margin-left: 0px;
    }

    .fullwidth-banner .parallax-banner-text h2 {
        font-size: 1rem;
        line-height: 1.2;
    }

    .index-page-13 .banner-booking .fullwidth-banner .btn {
        padding: 11px 16px;
        font-size: 11px;
    }

    .fullwidth-banner .parallax-banner-text h3 {
        font-size: 12px;
    }

    .index-page-13 .banner-box-one .container {
        text-align: center;
    }

    .index-page-13 .banner-box-one h1 {
        font-size: 17px;
        line-height: 26px;
    }

    .index-page-13 .banner-box-one .btn {
        padding: 9px 11px;
    }

    .index-page-13 .banner-box-one {
        padding: 4rem 0rem 4rem 0rem;
    }

    .index-page-13 .fresh-burger .banner-image {
        height: 300px;
    }

    .quality {
        .text-inner {
            text-align: center;
        }
    }
}

@media only screen and (max-width: 390px) {
    .fullwidth-banner .parallax-banner-text h2 {
        font-size: 14px;
        line-height: 1.2;
    }
}