.home9 {
    $secondary: #feb564;
    $secondary-darkest: #ffe5c9;
    --secondary-darkest: #{$secondary-darkest};
    --secondary: #{$secondary};

    .header-nine {
        .header-mini {
            .navbar-lang {
                .dropdown {
                    .dropdown-menu {
                        .dropdown-item {

                            &:hover,
                            &.active,
                            &:active {
                                color: var(--secondary);
                            }
                        }
                    }
                }
            }

            .pro-header-right-options {
                li .list-link {
                    &:hover {
                        color: var(--secondary);
                    }
                }

                .shopping-cart-items {
                    li {
                        .item-detail {
                            .item-s {
                                .fas:hover {
                                    color: var(--secondary);
                                }
                            }
                        }
                    }

                    .item-summary {
                        color: var(--secondary);
                    }
                }
            }
        }

        .navbar {
            .navbar-collapse {
                .navbar-nav {
                    .nav-item {
                        .dropdown-menu {
                            .dropdown-item:hover {
                                color: var(--secondary);
                            }
                        }

                    }
                }
            }
        }

        .mega-menu .banner-menu:hover {
            color: var(--secondary);
        }
    }

    .pro-content {
        padding-top: 150px;
    }

    .lookbook9 .pro-content {
        padding-bottom: 150px;
    }

    .pro-banners-detail .banner-content .pro-price ins {
        color: var(--secondary);
    }

    .blog9 .pro-text .pro-date {
        color: var(--secondary);
    }

    #back-to-top {
        border-radius: unset;
    }

    .alert-cookie2 {
        background-color: var(--secondary-darkest);
    }

    a:hover,
    .btn-link:hover {
        color: var(--secondary);
    }

    .header-sticky .pro-header-right-options .dropdown .dropdown-menu .shopping-cart-items li .item-detail .item-s .fas:hover {
        color: var(--secondary);
    }

    .header-sticky .pro-header-right-options li .btn-light:hover {
        color: var(--secondary);
    }

    .header-sticky .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu .dropdown-item:hover {
        color: var(--secondary);
    }

    .header-sticky .pro-header-right-options .search-field .search-field-module .fas:hover {
        color: var(--secondary);
    }

    .header-sticky .pro-header-right-options .profile-tags .dropdown-menu .dropdown-item:hover {
        color: var(--secondary);
    }

    .header-sticky .pro-header-right-options .dropdown .dropdown-menu .shopping-cart-items .item-summary {
        color: var(--secondary);
    }

    .header-sticky .navbar-lang ul .dropdown .dropdown-menu .dropdown-item:hover {
        color: var(--secondary);
    }

    .sticky-header .mega-menu .banner-menu:hover {
        color: var(--secondary);
    }

    .pro-banners-detail .banner-content h2 span {
        color: var(--secondary)
    }

    .pro-banners-detail {
        .banner-content {
            .pro-timer {

                .days,
                .hours,
                .mintues,
                .seconds {
                    background-color: var(--secondary);
                }
            }
        }
    }

    .productcard13 article .pro-info h3 a:focus,
    .productcard13 article .pro-info h3 a:hover {
        color: var(--secondary);
    }

    .pro-nine-rev.rev_slider_wrapper .hermes.tp-bullets .tp-bullet::after {
        background-color: var(--secondary);
    }

    .pro-nine-rev.rev_slider_wrapper .hermes.tp-bullets .tp-bullet.selected,
    .pro-nine-rev.rev_slider_wrapper .hermes.tp-bullets .tp-bullet:hover {
        background-color: var(--secondary);
    }

    .modal .modal-dialog .modal-body .pro-description .pro-options .size-option ul li.active a.size-select {
        background-color: var(--secondary);
    }

    .modal .modal-dialog .modal-body .pro-description .pro-price ins {
        color: var(--secondary);
    }

    .productcard13 article .pro-info .pro-price ins {
        color: var(--secondary);
    }

    .productcard13 {
        article {
            .pro-hover-icons {
                .icons {
                    .icon {
                        .fas:hover {
                            color: var(--secondary);
                        }
                    }
                }
            }
        }
    }

    .pro-heading-title8 {
        .line {
            background-color: var(--secondary);

            &::after {
                background-color: var(--secondary);
            }
        }
    }

    .badge-secondary {
        background-color: var(--secondary);
    }

    a {
        &:hover {
            color: var(--secondary) !important;
        }
    }

    .btn-secondary {
        background-color: var(--secondary);
        border-color: var(--secondary);
        border-radius: unset;
        font-family: $font-family-base;

        &:hover,
        &:not(:disabled):not(.disabled).active {
            background-color: var(--primary);
            border-color: var(--primary);
            color: var(--text-color-primary-contrast);
        }

        &:before {
            background-color: var(--primary);
        }
    }

    .btn-secondary:not(:disabled):not(.disabled):active,
    .btn-secondary:not(:disabled):not(.disabled).active,
    .show>.btn-secondary.dropdown-toggle {
        background-color: var(--secondary);
        border-color: var(--secondary);
    }

    .btn-primary {
        background-color: var(--primary);
        border-color: var(--primary);
        color: var(--text-color-primary);
        border-radius: unset;
        font-family: $font-family-base;
        font-size: $font-size-lg;

        &:hover {
            background-color: var(--secondary);
            border-color: var(--secondary);
            color: var(--text-color-primary-contrast) !important;
        }

        &:before {
            background-color: var(--secondary);
        }
    }

    .btn-lg {
        padding: 15px 40px;
    }

    .pro-price {
        ins {
            color: var(--secondary);
        }
    }

    .bullet9 {
        .slick-slider {
            .slick-dots {
                li.slick-active button {
                    background-color: var(--secondary);
                }
            }
        }
    }

    .tabs9-product {
        .nav {

            .nav-link:hover,
            .nav-link:focus {
                color: var(--secondary);
            }

            .active {
                color: var(--secondary);
            }
        }
    }

    .pro-banners-detail {
        .banner-content {
            background-color: var(--secondary-darkest);
        }
    }

    .rare-collection {

        .productcard13 article .pro-thumb {
            position: relative;
            overflow: hidden;
            background-color: unset;
            height: 295px;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 100%;
            }
        }
    }
}

.pro-content {
    position: relative;
}

.rightbg {
    background-position: 100% 10%;
    background-repeat: no-repeat;

}

.leftbg {
    background-position: 0 10%;
    background-repeat: no-repeat;

}

.tabs9-product {
    .nav {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 55px;

        .nav-link {
            padding-bottom: 0px;
            padding-top: 0;
            line-height: 1;
            padding-left: 15px;
            padding-right: 15px;
            font-family: $font-family-base-bold;
            color: var(--color-text);
        }

        .nav-link:hover,
        .nav-link:focus {
            color: var(--secondary);
        }

        .active {
            color: var(--secondary);
        }
    }
}

.bullet9 {
    .slick-slider {
        .slick-dots {
            li {
                margin: 0 2px;

                button {
                    border-radius: unset;
                    border: none;
                    background-color: var(--color-article2-background);
                    box-shadow: none;
                }
            }

            li.slick-active {
                button {
                    background-color: var(--secondary);
                }
            }
        }
    }
}

.collection9-carousel-js,
.collection9-1-carousel-js,
.collection9-2-carousel-js {
    .slick-dots {
        margin-top: 0 !important;
    }
}

.lookbook9 {
    .pro-content {
        background-color: var(--color-article1-background);
        padding-bottom: 100px;

        .lookbook9 {
            .pro-thumb {
                img {
                    height: auto;
                }
            }
        }
    }
}

.blog9 {

    .pro-text {
        display: flex;

        .pro-date {
            background-color: var(--color-article1-background);
            width: 75px;
            height: 75px;
            font-family: $font-family-base-bold;
            color: var(--secondary);
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 22px;
            line-height: 1.2;
            text-align: center;
            text-transform: uppercase;
        }

        .pro-detail {
            padding-left: 15px;
            width: calc(100% - 75px);

            h2 {

                font-size: 25px;
                font-family: $font-family-base;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }

            p {
                margin-bottom: 0;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                color: var(--color-text-dark);
            }
        }
    }
}

.bonuce-img {
    position: absolute;
    top: 50px;
    right: 0;
    -webkit-animation: movebounce 2.2s linear infinite;
    animation: movebounce 2.2s linear infinite;
}

.bounce-left {
    position: absolute;
    top: 50px;
    left: 0;
    -webkit-animation: movebounce 3.2s linear infinite;
    animation: movebounce 3.2s linear infinite;
}

.tabs9-product {
    .bannercarousel {
        img {
            height: 89%;
        }
    }
}

.pro-banners-detail {
    .banner-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
        text-align: center;
        background-color: var(--secondary-darkest);

        h4 {
            font-family: $font-family-base;
            font-size: 28px;
            text-transform: uppercase;
            margin-bottom: 15px;
        }

        h2 {
            text-transform: uppercase;
            line-height: 23px;
            margin-bottom: 15px;

            span {
                color: var(--secondary);
            }
        }

        .pro-price {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-bottom: 15px;

            del {
                font-family: $font-family-base;
                font-size: 32px;
                color: var(--color-text-dark);
                text-decoration: line-through;
                line-height: 1;
                margin-bottom: 15px;
            }

            ins {
                font-family: $font-family-base-bold;
                font-size: 32px;
                color: var(--secondary);
                text-decoration: none;
                line-height: 1;

            }
        }

        p {
            text-transform: capitalize;
            line-height: 25px;
            margin-bottom: 15px;
        }

        .pro-timer {
            justify-content: center;
            margin-bottom: 15px;

            .days,
            .hours,
            .mintues,
            .seconds {
                margin-right: 0;
                margin-left: 0;
                background-color: var(--secondary);
                color: var(--text-color-secondary);
                border: none;
                width: 75px;
                height: 75px;
                font-size: 17px;
                text-transform: uppercase;
                font-family: $font-family-base;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                small {
                    font-family: $font-family-base-bold;
                    font-size: 17px;

                }
            }

            .colon {
                color: var(--text-color-secondary);
                font-size: $font-size-base;
                font-family: $font-family-base-bold;
                padding: 0 10px;
                display: flex;
                align-items: center;
            }
        }
    }
}

.video-content {
    position: relative;
    cursor: pointer;

    .added_video1 {
        width: 100%;
        height: 550px;
        z-index: 9;
        border: none;
    }

    .img_static1 {
        width: 100%;
        height: 550px;
    }
}

@keyframes movebounce {
    0% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }

    50% {
        -webkit-transform: translateY(5px);
        transform: translateY(5px);
    }

    100% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
}

@keyframes pulse {

    0% {

        box-shadow: inset 0px 0px 10px 2px var(--secondary),
            0px 0px 40px 2px var(--secondary);
    }

    100% {

        box-shadow: inset 0px 0px 10px 2px rgba(117, 182, 255, 0.5),
            0px 0px 30px 2px rgba(105, 135, 255, 0.3);
    }
}

@-webkit-keyframes pulse {
    0% {
        transform: translate(-50%, -50%);
    }

    100% {
        transform: translate(-50%, -50%) scale(1.2);
    }

}

.pro-nine-rev.rev_slider_wrapper {

    .rev_slider {
        overflow: visible;
        // height: 600px !important;

        li {
            .BigBold-SubTitle {
                font-family: $font-family-base-medium;
                line-height: 1.2 !important;
                margin-top: 15px !important;
                font-size: 26px !important;
                color: var(--color-text);
            }

            .BigBold-Title {
                font-size: 52.86px !important;
                font-family: $font-family-base-bold;
                line-height: 1 !important;
                margin-top: 0px !important;
                text-transform: uppercase;
                color: var(--color-text);

                span {
                    font-family: $font-family-base;
                    font-size: 40px !important;
                }
            }

            .BigBold-Button {
                background-color: transparent;
                box-shadow: none !important;

                button {
                    font-family: $font-family-base;
                    border-radius: unset;
                }
            }
        }
    }

    .hermes.tp-bullets {
        top: auto !important;
        bottom: -93px;

        .tp-bullet {
            border-radius: unset;
            box-shadow: none;
            background-color: var(--color-article2-background);

            &::after {
                background-color: var(--secondary);
                box-shadow: none;
            }
        }

        .tp-bullet.selected,
        .tp-bullet:hover {
            background-color: var(--secondary);
        }
    }


}

