.footer-thirteen {
    svg {
        width: 30px;
    }

    .footer-up {
        background-color: var(--secondary);

        .subscribe {
            background-color: #333333;
            padding: 9px 17px;
        }

        .input-sub {
            padding: 11px 12px;
        }
    }

    .bgfooter {
        background-color: #333333;

        .social {
            list-style: none;
            display: flex;

            a {
                width: 36px;
                height: 36px;
                border-radius: 50%;
                margin: 5px;
                background-color: white;
                display: flex;
                align-items: center;
                justify-content: center;

                i {
                    color: #333333;
                }

                &:hover i {
                    color: var(--secondary);
                }

                &:hover {
                    background-color: black
                }
            }
        }
    }

    .footer-v-six .footerlinkheading {
        padding-bottom: 0;
        border-bottom: unset;
    }

    .footer-v-six .categories .cat-list li a {
        color: white;
    }
    .m-payment-icons--list{
        justify-content: end;
    }
}
@media only screen and (max-width: 500px) {

    .footer-thirteen .footerbottomin{
        text-align: center;
        div{
            text-align: center !important;
        }
        ul{
            text-align: center !important;
            justify-content: center;
        }
    }
}