.home20 {
    $secondary: #4D2952;
    --secondary: #{$secondary};
}

.home-20 {
    .pro-content {
        padding-top: 30px !important;
        background-color: var(--color-article1-background) !important;
        .navflex-basis{
            background-color: var(--color-white-background);
        }
    }
    .timer-banner{
        background-color: var(--color-white-background) !important;
    }

    .slick-slide {
        border: none !important;
    }

    .arrow-style3 .slick-arrow {
        top: 40%;
        height: auto;
        width: auto;
        z-index: 2;
    }

    .best-sale-product-js-20 {
        .slick-arrow.slick-prev {
            left: -15px !important;
            right: auto !important;
        }

        .slick-arrow.slick-next {
            right: -20px;
            left: auto !important;
        }
    }

    .text-white {
        color: var();
    }

    #newsletterModal {
        .modal-body {
            padding: 0;
        }

        .container {
            padding: 0;
        }

        h2 {
            text-align: start;
            color: #000;
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 38px;
            font-style: normal;
            font-weight: 600;
            line-height: 150%;
            /* 57px */
        }

        p {
            color: #000;
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 14px !important;
            font-style: normal;
            font-weight: 400;
            line-height: 150%;
            text-align: start;
            /* 21px */
        }

        button {
            border-radius: 56px;
            background: var(--secondary);
            box-shadow: 0px 3px 12px 0px rgba(74, 58, 255, 0.18);
            margin: 20px 0px;
        }

        .text-modal {
            color: #979797;
            font-size: 12px !important;
            font-style: normal;
            font-weight: 400;
            text-align: start;
            text-justify: inherit;
            /* 18px */
        }
    }

    .pro-content {
        background-color: transparent;

        .info-boxes-bg {
            background-color: var(--color-white-background) !important;
            border-radius: 10px;

            .info-box {
                background-color: transparent !important;
                border-radius: 10px;
                padding: 10px 0px !important;
                .title{
                    font-size: 12px !important;
                }
            }
        }

        .panel {
            h4 {
                color: var(--color-text);
                overflow: none;
                text-align: center;
                font-size: 18px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
            }
        }
    }

    .vbannercarousel {
        .imgbanner {
            height: auto;
        }
    }

    .card2 {
        .pro-info {
            h6 {
                color: var(--dark, #1C1C1C);
                text-align: center;
                font-feature-settings: 'clig' off, 'liga' off;
                /* Text normal */
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 24px;
                /* 150% */
                letter-spacing: -0.2px;
            }

            .pro-rating {
                justify-content: center;

                button {
                    display: inline-flex;
                    height: 28px;
                    padding: 3px 13px 4px 13px;
                    justify-content: center;
                    align-items: center;
                    gap: 10px;
                    flex-shrink: 0;
                    border-radius: 29px;
                    background: #FFE3E3;
                    color: red;
                }
            }
        }
    }

    .vproduct6 {
