.tabs-content2 {
    .pro-options .size-option ul li.active a {
        outline-color: #fff;
    }

    .pro-options .color-option ul li.active a {
        outline-color: #fff;
    }

    .pro-options .size-option ul li a {
        color: $white;

        &:hover {
            color: var(--secondary);
        }
    }
}

.pre-slick {
    .slick-next {
        &::before {
            content: "\f105";
            font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
            font-weight: 900;
            font-size: 28px;
        }
    }

    .slick-prev {
        &::before {
            content: "\f104";
            font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
            font-weight: 900;
            font-size: 28px;
        }
    }
}

.pro-content {
    .product {
        article {
            .pro-heading-with-icon {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;

                .pro-rating {

                    .fa-star,
                    .fa-star-half-alt {
                        color: var(--secondary);
                    }
                }
            }

            .pro-info {
                .liked-button {
                    display: flex;

                    .icon {
                        outline: none;
                        border: none;
                        position: relative;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        text-decoration: none;
                        transition-duration: 0.3s;
                        width: 25px;
                        height: 23px;
                        padding: 0;

                    }

                }
            }


        }

    }
}

.pro-content {
    .countdown2 {
        display: flex;

        .hours,
        .days,
        .mintues,
        .seconds {
            font-size: $font-size-base;
            font-family: $font-family-base;
            padding: 0px 5px 0 5px;
            width: 47px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text);

            small {
                color: var(--color-text);
                margin-left: 5px;

            }
        }

    }

    .countdown-start {
        margin: 10px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        justify-content: center;

        p {
            margin-bottom: 7px;
            color: var(--color-text);
        }

    }
}

.productcard-1 {
    .product article .pro-info .pro-price {
        background-color: $body-bg-secondary;
        -webkit-box-shadow: 0px 2px rgba(51, 51, 51, 0.1);
        box-shadow: 0px 2px rgba(51, 51, 51, 0.1);
        padding: 10px 10px 10px 10px;


    }

    .product {
        article {
            .pro-info {
                margin-top: -7px;
                border: 1px solid $border-color;
                border-top: none;

                h3 {
                    margin-top: 5px;
                    padding: 7px 10px 0px 10px;
                }

                .pro-category {
                    padding: 0px 10px 7px 10px;
                }
            }

            &:hover {
                -webkit-box-shadow: 0 4px 30px rgba(51, 51, 51, 0.1);
                box-shadow: 0 4px 30px rgba(51, 51, 51, 0.1);
            }
        }

    }
}

.pro-content-full-width {
    .slick-list {
        .slick-slide {
            opacity: 0.5;
        }

        .slick-slide.slick-active {
            opacity: 1;
        }

    }

    .fullwidth-carousel-js {
        .slick-arrow {
            &.slick-prev {
                right: 116px;
                left: auto;
            }

            &.slick-next {
                right: 106px;
            }
        }
    }
}

.pre-tabs-content {
    .slick-arrow {
        top: 50%;

        &:before {
            font-size: 100px;
            opacity: 0.5;
        }

        &.slick-prev {
            left: -65px;
        }

        &.slick-next {
            right: -70px;
        }
    }
}

.arrow-style2 {

    .slick-arrow {
        top: -81px;
        height: auto;
        width: auto;
        z-index: 2;

        &:hover {
            background-color: transparent;
        }


        &.slick-prev {
            right: 22px;
            left: auto;
        }

        &.slick-next {
            right: 10px;
        }
    }
}

.pro-content {
    .tab-carousel-js2 {

        .slick-arrow {
            top: 43%;

            &:before {
                font-size: 50px;
            }
        }

        .slick-next {
            right: -100px;
        }

        .slick-prev {
            right: auto;
            left: -100px;
        }
    }
}

.tabs-content2.pro-content {
    .product {
        article {
            .pro-heading-with-icon {
                h3 {
                    a {
                        color: $white;
                    }
                }
            }
        }
    }
}

.tabs-content2 {

    .pro-heading-title h2,
    .pro-heading-title p {
        color: $white;
    }

    .nav {
        justify-content: flex-end;

        .nav-link {

            border-radius: 3px;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: $font-family-base-bold;
            font-size: $font-size-lg;
            text-align: center;
            padding: 5px 10px;
            margin-bottom: 0;
            box-shadow: none;
            color: $white;


            &:last-child {
                margin-right: 0;
                padding-right: 0;
            }

            &.active,
            &:hover {
                color: var(--secondary);
            }
        }
    }

    .product {
        article {
            .pro-info {
                .pre-heading {
                    h3 {
                        a {
                            color: $white;
                        }
                    }
                }

                .pro-price {

                    del {
                        color: $gray-300;
                    }
                }
            }
        }
    }
}

.banner-sale {
    figure {
        position: relative;
        background-color: #f5f5f5;
        border-radius: $border-radius;
        overflow: hidden;
        margin-bottom: 0px;
        width: 100%;


        .img {
            width: auto;
        }

        &.right {
            img {
                float: right;
            }
        }


    }

    .banner-caption {
        position: absolute;
        width: 100%;
        z-index: 2;

        h3 {
            font-family: $font-family-base-bold;
            margin: 0;
            line-height: 1.5;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;

        }

        p {
            color: var(--secondary);
        }

        .btn {
            margin-top: 13px;
        }

        .pro-price {
            display: flex;
            flex-direction: column;
            justify-content: space-between;


            del {
                font-size: $font-size-base ;
                color: $gray-700;
                text-decoration: line-through;

            }

            ins {
                font-family: $font-family-base-bold;
                font-size: ($font-size-lg + 0.475);
                color: var(--secondary);
                text-decoration: none;

            }
        }

        &.left-caption {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            height: 100%;
            padding-left: 65px;
            padding-right: 126px;
        }

    }
}

.fullwidth-banner2-2 {
    position: relative;
    background-image: url("../images/index-2/fullwidthbanner.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 582px;

    &:before {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        content: '';
        display: block;
    }

    .parallax-banner-text {
        position: relative;

        h2 {
            font-size: 3rem;
            font-family: $font-family-base-bold;
            color: $white;
            margin: 0;
        }

        p {
            font-family: $font-family-base;
            font-size: 1rem;
            color: $white;
        }

    }
}

.blog-style2 {
    figure {
        margin: 0;
    }

    .bg-border {

        // width: 90%;
        overflow: hidden;
        height: 90%;
        position: absolute;
        bottom: 0px;
        // right: 15px;
        // border: 1px solid $border-color;
        width: 510px;
        // background: red;
        border: 2px solid black;
        border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color)!important;
        margin-left: 30px;
    }

    .caption-right {
        display: flex;
        align-items: center;

        .banner-caption-right {
            h3 {
                color: var(--color-text);
                font-family: $font-family-base-bold;
                font-size: $font-size-lg;
                margin-bottom: 15px;
                line-height: 1.5;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }

            p {
                color: var(--color-text);
                font-family: $font-family-base;
                font-size: $font-size-base;
                margin-bottom: 15px;
                line-height: 1.5;
                white-space: normal;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
            }

            .blog-meta {
                display: flex;
                margin-bottom: 15px;

                .blog-content {
                    margin-right: 10px;
                    margin-bottom: 0;
                }
            }
        }
    }




}

.fullwidth-banner2 {
    position: relative;
    /* The image used */
    background-image: url("../images/index-2/bg-paralex.jpg");


    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;


    &:before {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: $black;
        content: '';
        display: block;
        opacity: 0.7;
    }

    .parallax-banner-text {


        h2 {
            font-size: 3rem;
            font-family: $font-family-base-bold;
            color: $body-color;
            margin: 0;
        }

    }
}

.pro-content-full-width {
    .arrows {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 60px;

        .slick-arrow {
            background-color: transparent;
            border: none;
            font-size: 28px;
            width: 0;
            margin-right: 13px;

            &:focus {
                outline: none;

                &::before {
                    opacity: 1;
                }
            }

            &:hover::before {

                opacity: 1;
            }

        }

        .prev-fullwidth {
            margin-right: 16px;

            &::before {
                content: "\f104";
                font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
                font-weight: 900;
                font-size: 29px
            }
        }

        .next-fullwidth {
            &::before {
                content: "\f105";
                font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
                font-weight: 900;
                font-size: 29px
            }
        }
    }

}

.tabs-content2 {
    .arrows {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        top: 50%;
        overflow: hidden;
        left: 0;

        .slick-arrow {
            background-color: transparent;
            border: none;
            font-size: 50px;
            display: contents;
            width: 0;

            &:active {
                color: var(--color-text-dark);

                .fas {
                    color: var(--color-text-dark);
                }
            }

            .fas {
                color: var(--color-text-dark);

                &:hover {
                    color: $dark;
                }

            }

            &:focus {
                outline: none;
            }
        }

        .prev-- {
            .fas {
                margin-left: 57px;
            }

        }

        .next-- {
            .fas {
                margin-right: 52px;
            }
        }
    }


}

.tabs-content2.pro-content {
    padding-bottom: 100px;
}

.bodyrtl {

    .arrow-style2 .slick-arrow.slick-prev {
        left: 10px;
        right: auto;
    }

    .arrow-style2 .slick-arrow.slick-next {
        left: 25px;
        right: auto;
    }

    .pro-content .product article .pro-heading-with-icon {
        direction: rtl;
    }

    .tabs-content2 .nav .nav-link:last-child {

        padding-right: 10px;
        padding-left: 0;
    }

    .tabs-content2 .arrows {
        direction: ltr;
    }

    .pro-content .tab-carousel-js2 .slick-next {
        right: -27px;
        left: auto;
    }

    .banner-sale .banner-caption.left-caption {
        align-items: flex-end;
        padding-right: 65px;
        text-align: right;
        padding-left: 125px;
    }

    .blog-style2 .caption-right .banner-caption-right .blog-meta .blog-content {
        margin-right: 0;
        margin-left: 10px;
    }

    .blog-style2 {
        .slick-slide {
            .row {
                direction: rtl;
            }
        }
    }

    .blog-style2 figure {
        display: flex;
    }

    .blog-style2 .bg-border {
        left: 15px;
        right: auto;
    }

    .pro-content-full-width .arrows {
        justify-content: start;
        margin-bottom: 60px;
        direction: ltr;
    }

}

.index-2{

    .pro-info{

        .pro-options{

            ul{
                padding-left: 0px;
            }
        }
    }
}

@media only screen and (max-width:1250px) {

    .bodyrtl {
        .pro-content-full-width .fullwidth-carousel-js .slick-arrow.slick-prev {
            left: 15px;
            right: auto;
        }

        .pro-content-full-width .fullwidth-carousel-js .slick-arrow.slick-next {
            right: auto;
            left: 32px;
        }

    }
}

@media only screen and (min-width:1201px) {
    .blog-style2 {
        figure {
            margin: 0;
            width: 95%;
            height: 92%;
            z-index: 2;
            position: relative;
            display: flex;
        }
    }
}

@media only screen and (max-width:1200px) {
    .blog-style2 .bg-border {
        display: none;
    }

    .blog-style2 figure {
        display: flex;

        img {
            margin-bottom: 30px;
        }
    }

}

@media only screen and (max-width:1099px) and (min-width:992px) {
    .tabs-content2 .tab-carousel-js2 .slick-arrow {
        display: none !important;
    }
}

@media only screen and (max-width:1100px) {
    .pro-content-full-width {
        .arrows {
            display: none;

            .slick-arrow {
                display: none;
            }

        }

    }

    .pro-content .tab-carousel-js2 .slick-arrow {
        display: none !important;
    }

    .arrow-style2 .slick-arrow {
        display: none !important;
    }

    .pro-content-full-width .fullwidth-carousel-js .slick-arrow {
        display: none !important;
    }
}

@media only screen and (max-width:991px) {

    .productcard-1 .product article .pro-info .pro-price {
        display: flex;
        flex-direction: column;
    }

    .productcard-1 .product article .pro-info .pro-price ins {
        margin-right: 0;
        margin-bottom: 10px;
        line-height: 16px;
    }

    .tabs-content2 .nav .nav-link {
        padding: 15px 15px 15px 10px;
    }

    .banner-sale figure.right img {
        width: 100%;
    }


    .tabs-content2 .nav {
        justify-content: center;
    }

    .tabs-content2 .pro-heading-title {
        padding-bottom: 0;
    }

    .tabs-content2 .tab-carousel-js2 .slick-arrow {
        top: -50px;

        &:before {
            font-size: 35px;
        }
    }

    .tabs-content2 .tab-carousel-js2 .slick-next {
        right: 10px;
    }

    .tabs-content2 .tab-carousel-js2 .slick-prev {
        right: 23px;
        left: auto;
    }

}

@media only screen and (max-width:768px) {
    .fullwidth-banner2-2 .parallax-banner-text h2 {
        font-size: 2rem;
    }

    .pro-heading-title {
        h2 {
            font-size: 20px;
        }
    }

    .tabs-content2.pro-content {
        padding-bottom: 60px;
    }
}

@media only screen and (max-width: 575px) {
    .tabs-content2 .nav {
        justify-content: space-between;
    }


    .tabs-content2 .nav .nav-link {
        padding: 15px 0px 15px 0px;
    }
}

@media only screen and (max-width:576px) {
    .pro-content-full-width .fullwidth-carousel-js .slick-arrow.slick-prev {
        right: 15px;
    }

    .pro-content-full-width .fullwidth-carousel-js .slick-arrow.slick-next {
        right: 0;
    }

}

@media only screen and (max-width:480px) {
    .banner-sale .banner-caption.left-caption {

        padding-left: 30px;

    }

    .banner-sale .banner-caption h3 {
        font-size: 15px;
    }

    .banner-sale .banner-caption .pro-price ins {
        font-size: 16px;
    }

    .banner-sale .banner-caption .btn {
        margin-top: 10px;
    }
}

@media only screen and (max-width:350px) {
    .fullwidth-banner2-2 .parallax-banner-text h2 {
        font-size: 29px;
    }
}

// rtl responsive
@media only screen and (max-width: 480px) {
    .bodyrtl .banner-sale .banner-caption.left-caption {

        padding-right: 30px;

    }
}

@media only screen and (max-width: 1200px) and (min-width:767px) {
    .pro-content-full-width .slick-list .slick-slide {
        opacity: 1;
    }
}