  .pro-five-rev.rev_slider_wrapper {
      .hermes .tp-bullet {
          box-shadow: inset 0 0 0 2px var(--secondary);
      }

      .hermes .tp-bullet:hover {
          background-color: var(--secondary);
      }

      .hermes .tp-bullet:after {
          background-color: var(--secondary);
          box-shadow: 0 0 1px var(--secondary);
      }

      .rev_slider {
          li {
              .BigBold-SubTitle {
                  font-family: $font-family-base-bold;
                  line-height: 1.2 !important;
                  margin-top: 15px !important;
                  color: var(--color-text);

                  span {
                      border-bottom: 3px solid;
                      border-color: var(--secondary);
                  }

              }

              .BigBold-Title {
                  font-size: 75px !important;
                  font-family: $font-family-base-medium;
              }

              .BigBold-Button {
                  background-color: transparent;
                  box-shadow: none !important;

                  .btn {
                      border-radius: 200px;
                  }
              }
          }
      }
  }

  .pro-content {
      .productcard3 {
          padding-top: 30px;
      }
  }

  .brandnew-product5 {

      .productcard3 {
          padding-top: 30px;
      }
  }

  .pro-blog-content5 {
      .blog {

          .blog-thumb {
              display: flex;
              justify-content: center;

              a,
              img {
                  width: 100%;

              }
          }

          .blog-info {
              margin-top: 15px;

              .blog-date {
                  color: var(--secondary);
                  text-transform: uppercase;
                  font-family: $font-family-base-medium;
                  font-size: 20px;
                  line-height: 1.5;
                  margin-bottom: 10px;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  overflow: hidden;
              }

              .sub-title {
                  color: var(--secondary);
                  text-transform: uppercase;
                  font-family: $font-family-base-medium;
                  font-size: 20px;
                  line-height: 1.5;
                  margin-bottom: 0px;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  text-transform: uppercase;
              }

              p {
                  color: var(--color-text);
                  text-transform: capitalize;
                  font-family: $font-family-base-bold;
                  line-height: 1.5;
                  margin-bottom: 10px;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  text-transform: capitalize;
              }
          }
      }
  }

  .info-boxes-bg {

      background-color: var(--color-article1-background);

  }

  .boxes-content {
      .info-boxes-content {
          .info-box {
              display: flex;
              position: relative;
              align-items: center;
              justify-content: center;
              background-color: var(--color-article1-background);
              padding: 15px;

              .panel {
                  display: flex;
                  align-items: center;

                  .fas {
                      color: var(--color-text);
                      font-size: 35px;
                      margin-bottom: 0;
                      text-align: center;
                      align-self: center;
                      margin-right: 15px;
                  }

                  .block {
                      h4 {
                          font-size: 1rem;
                          white-space: normal;
                          overflow: hidden;
                          text-overflow: ellipsis;
                          display: -webkit-box;
                          -webkit-line-clamp: 1;
                          -webkit-box-orient: vertical;
                          margin-bottom: 0;
                      }

                      p {
                          margin-bottom: 0;
                      }
                  }
              }

          }
      }
  }

  .detailbanner {
      .discription {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          height: 100%;
          background-image: url("../images/index-5/bg-circle.png");
          background-repeat: no-repeat;
          background-position: center center;
          color: $white;

          h4 {
              margin-bottom: 15px;
              color: $white;
          }

          p {
              margin-bottom: 15px;
              color: $white;
          }

          a {
              cursor: pointer;
              color: $white;

              &:hover {
                  color: $white;
                  text-decoration: underline;
              }
          }
      }

      .bg-red {
          background-color: var(--secondary);
      }

      .bg-orange {
          background-color: #d79768;
      }

      .bg-darkbrown {
          background-color: #c49c7b;
      }

      .bg-gray {
          background-color: #282828;
      }
  }

  .pro-content.arrow-5 {
      .slick-slide img {
          width: 100%;
      }


      .slick-next::before {
          content: "\f105";
          font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
          font-weight: 900;
          font-size: 20px;
          position: absolute;
          line-height: normal;
          margin-left: 1px;
      }

      .slick-prev {
          right: 45px;
          left: auto;

          &::before {
              content: "\f104";
              font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
              font-weight: 900;
              font-size: 20px;
              position: absolute;
              line-height: normal;

          }
      }

      .slick-arrow {
          top: -77px;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 25px;
          width: 25px;
          border-radius: 200px;
          z-index: 2;
          background-color: var(--color-article1-background);
      }
  }

  .featured-index5-carousel-js {
      .slick-slide {
          >div:first-child {
              .productcard5a {
                  margin-top: 0;
              }
          }
      }
  }

  .instafeed {
      .slick-slide {
          padding: 0;

          a {
              display: block;
              position: relative;

          }
      }

      .insta-detail {
          z-index: 2;
          overflow: hidden;

          .instaawesome {
              -webkit-transition: 0.5s ease;
              transition: 0.5s ease;
              opacity: 1;
              position: absolute;
              height: 100%;
              width: 100%;
              top: 0;
              text-align: center;
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-align: center;
              -ms-flex-align: center;
              align-items: center;
              -webkit-box-pack: center;
              -ms-flex-pack: center;
              justify-content: center;
              -webkit-transform: scale(3) rotate(90deg);
              transform: scale(3) rotate(90deg);
              left: 0;
              font-size: 105px;
              color: $white;
              cursor: pointer;
              background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
              opacity: 0;
          }

          &:hover {
              .instaawesome {
                  opacity: 0.5;
                  -webkit-transition: 0.5s ease;
                  transition: 0.5s ease;
                  -webkit-transform: scale(1) rotate(0deg);
                  transform: scale(1) rotate(0deg);
              }
          }
      }


  }

  .tabs5 {
      .nav {
          margin-right: 60px;
          justify-content: flex-end;

          .nav-link {
              padding: 6px 30px 0 0;

          }

          .active {
              span {
                  border-bottom: 3px solid;
                  border-color: var(--secondary);
                  color: var(--secondary);
              }
          }
      }

      .productcard10 {
          margin-top: 60px;
      }

      .slick-slide {
          >div:first-child {
              .productcard10 {
                  margin-top: 0;
              }
          }
      }
  }



  .bodyrtl {
      .pro-content.arrow-5 .slick-prev {
          right: auto;
          left: 15px;
      }

      .pro-content.arrow-5 .slick-next {
          right: auto;
          left: 45px;
      }

      .boxes-content .info-boxes-content .info-box .panel .fas {
          margin-left: 15px;
          margin-right: 0;
      }

      .productcard5a article .pro-info .pro-price {
          justify-content: flex-end;
      }

      .advertismentcard article .pro-timer span:last-child {
          margin-left: 0;
      }

      .pro-timer .days,
      .pro-timer .hours,
      .pro-timer .mintues,
      .pro-timer .seconds {
          margin-left: 0;
          margin-right: 15px;
      }

      .tabs5 .nav {
          margin-left: 60px;
          margin-right: 0;

          .nav-link {
              padding: 6px 0px 0 30px;
          }
      }

  }

