.home15 {
    $secondary: #3d5b81;
    --secondary: #{$secondary};
}

.home-index-15 {
    .innerz{
        z-index: 88;
    }
    .text-body {
        color: var(--color-text) !important;
    }

    .banner-box-one {
        background-image: url("../../images/index-15/image-1400x500.jpg");
        padding: 12rem 0rem 14rem 0rem;
        background-repeat: no-repeat;
        background-size: cover;

        h1 {
            line-height: 82px;
            text-transform: uppercase;
            font-size: 65px;
        }
        h6{
            color: var(--secondary);
        }

        .btn {
            font-size: 14px;
            padding: 20px 25px;
        }
    }

    .bullet9 {

        article .pro-hover-icons {
            height: 123px;
            background-color: #e3fbfd;
        }

        .pro-price {
            ins {
                font-weight: 700;
                color: red;
            }
        }
    }

    .litter-banner {
        .inner-banner {
            background-image: url('../../images/index-15/B1.png');
            padding: 3rem 0rem 3rem 0rem;
            background-repeat: no-repeat;
            background-size: cover;
            height: 100%;
        }

        .innertext {
            background-color: #3d5b81;
            border-radius: 8px;

            h1 {
                font-size: 3.5rem !important;
            }
        }
    }

    .slider-banner {

        .prev0,
        .next0 {
            position: absolute;
            top: 50%;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            left: -60px;
            width: 30px;
            height: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .next0 {
            right: -60px;
            left: unset;
        }

        .tab-carousel-js2 {
            .border {
                background-color: var(--color-page-background);
                // background-color: #f6f6f6;
            }

            .starts {
                i {
                    font-size: 10px;
                    color: #f2980c;
                }
            }
        }

        .countdown-start {
            background-color: var(--secondary);
            display: none;
            padding: 7px;

            .couunt-box {
                span {
                    padding: 3px 10px;

                }
            }

            .pre-timer {
                span {
                    small {
                        display: none;
                    }
                }
            }
        }

        .product:hover {
            .countdown-start {
                display: block;
                width: 100%;
            }
        }

        .countdown2 {
            justify-content: center;
        }

        .product {
            background-color: var(--color-page-background);
        }
    }

    .next1d,
    .prev1d {
        top: 50%;
        position: absolute;
        font-size: 30px;
        border: none;
        background: transparent;
        // border: 2px solid;
        right: -60px;
        height: 30px;
        width: 30px;
        // border-radius: 50%;
        display: flex;
        align-items: center;
        color: var(--color-text) !important;
        justify-content: center;
    }

    .prev1d {
        left: -60px;
    }

    .tabs-wrapper {

        .next1a,
        .prev1a {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1a {
            left: -60px;
        }

        .next1b,
        .prev1b {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1b {
            left: -60px;
        }

        .next1c,
        .prev1c {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1c {
            left: -60px;
        }

        .countdown-start {
            background-color: unset;
            padding: 0;
            width: 100%;

            .btn-primary {
                background-color: #98c0d9;
            }
            .d-flex{
                width: 100%;
            }
        }

        .btn-col {
            background-color: #dffbfc !important;
            padding: 23px 25px;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            color: var(--secondary);
        }

        .btn-col.active {
            background-color: var(--secondary) !important;
            color: white;
        }
    }

    .second-slider {
        .countdown-start {
            background-color: unset;
            padding: 0;

            .btn-primary {
                background-color: #98c0d9;
                padding: 17px;
            }

            .btn-secondary {
                padding: 17px;
            }
            
        }
    }

    .fullwidth-banner4-2 {
        background-color: unset;
    }

    .brands-banner {
        .contain-banner {
            display: block;
            padding: 18px;

            h4 {
                font-weight: 700;
            }
        }
    }

    .recent-add {

        .next1e,
        .prev1e {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1e {
            left: -60px;
        }

        .countdown-start {
            background-color: unset;
            padding: 0;

            .btn-primary {
                background-color: #98c0d9;
                padding: 17px;
            }

            .btn-secondary {
                padding: 17px;

            }
        }
    }

    .top-brands {
        background-color: var(--color-article1-background);

        .top-padd .brands-inner {

            background-color: unset;
            -webkit-box-shadow: unset;
            box-shadow: unset;
        }
    }

    .pre-top-sellers {
        .left-banner {
            background-color: #98c0d9;

            .left-caption-banner {
                background-image: url('../../images/index-15/delivery.png');
                padding: 2rem;
                background-repeat: no-repeat;
                background-position: right;
            }
        }

        .right-banner {
            background-color: var(--secondary);

            .left-caption {
                background-image: url('../../images/index-15/24-hours.png');
                padding: 2rem;
                background-repeat: no-repeat;
                background-position: right;
            }
        }
    }

    .customer-tes {

        .next1f,
        .prev1f {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1f {
            left: -60px;
        }

        .card-bg {
            background-color: var(--color-article1-background);
        }
    }

    .email-inter {
        background-color: var(--secondary);
        border-radius: 10px;
        padding: 100px 10px;

        .btn-primary {
            background-color: #98c0d9;
        }
    }

    .blog-wrapper {

        .next1g,
        .prev1g {
            top: 50%;
            position: absolute;
            font-size: 26px;
            border: none;
            background: transparent;
            // border: 2px solid;
            right: -60px;
            height: 30px;
            width: 30px;
            // border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-text) !important;
        }

        .prev1g {
            left: -60px;
        }

        .card-bg {
            background-color: var(--color-article1-background);
        }
    }
}

.bodyrtl {
    .pre-top-sellers .right-banner .left-caption {
        background-position: left;
    }

    .pre-top-sellers .left-banner .left-caption-banner {
        background-position: left;
    }

    .headerfifteen {
        .header-main {
            background: #99c1da !important;
        }
    }

    .blog-wrapper .prev1g {
        right: unset;
    }

    .customer-tes .prev1f {
        right: unset;
    }

    .recent-add .prev1e {
        right: unset;
    }

    .tabs-wrapper .prev1a {
        right: unset;
    }

    .prev1d {
        right: unset;
    }

    .tabs-wrapper .prev1c {
        right: unset;
    }
}

html.boxed {
    .home-index-15 .banner-box-one {
        padding: 11rem 0rem 11rem 0rem;
    }

    .home-index-15 .banner-box-one h1,
    .home-index-15 .banner-box-one .h1 {
        line-height: 68px;
        text-transform: uppercase;
        font-size: 50px;
    }
}

@media only screen and (max-width: 1350px) {
    .home-index-15 .banner_ .collection-banner {
        position: unset;
        overflow: unset;
    }

    .home-index-15 .page {
        h4 {
            font-size: 17px;
        }

        h5 {
            font-size: 15px;
        }

        p {
            font-size: 12px;
        }

        .btn {
            margin-top: 12px !important;
            padding: 8px;
        }
    }

    .home-index-15 .litter-banner .inner-banner {
        padding: 1rem 0rem 1rem 0rem;

        h3 {
            font-size: 16px;
        }
    }

    .home-index-15 .litter-banner .innertext {
        h1 {
            font-size: 2rem !important;
        }

        h3 {
            font-size: 16px;
        }
    }

    .home-index-15 .banner-box-one {
        padding: 12rem 0rem 9rem 0rem;
    }

    .home-index-15 .banner-box-one h1 {
        font-size: 50px;
        line-height: 57px;
    }
}

@media only screen and (max-width: 1100px) {
    .home-index-15 .banner-box-one {
        padding: 10rem 0rem 8rem 0rem;
    }

    .home-index-15 .banner-box-one h1 {
        font-size: 36px;
        line-height: 50px;
    }


}

@media only screen and (max-width: 991px) {
    .home-index-15 .banner-sale .banner-caption.left-caption h3 {
        font-size: 12px;
    }

    .home-index-15 .banner-sale .banner-caption.left-caption .btn {
        padding: 8px;
    }

    .home-index-15 .litter-banner .innertext {
        margin-top: 12px;
        margin-bottom: 12px;
    }

    .home-index-15 .banner-box-one {
        padding: 7rem 0rem 8rem 0rem;
    }

    .home-index-15 .banner-box-one h1 {
        font-size: 30px;
        line-height: 43px;
    }

    .home-index-15 .banner-box-one .btn {
        padding: 14px 21px;
    }
}

@media only screen and (max-width: 776px) {
    .home-index-15 .email-inter {
        border-radius: 0px;
    }

    .home-index-15 .litter-banner .inner-banner .w-50 {
        width: 100% !important;
    }

    .home-index-15 .banner-box-one {
        padding: 6rem 0rem 6rem 0rem;
    }

    p .home-index-15 .banner-box-one h1 {
        font-size: 25px;
        line-height: 35px;
    }

    .home-index-15 .banner-box-one .btn {
        padding: 12px 15px;
    }

}

@media only screen and (max-width: 500px) {
    .home-index-15 .hvr-rectangle-out::before {
        content: unset;
    }

    .home-index-15 .banner_ .collection-banner {
        img {
            min-height: 250px;
        }
    }

    .home-index-15 .banner-box-one .container {
        text-align: center;
    }

    .home-index-15 .banner-box-one h1 {
        font-size: 17px;
        line-height: 26px;
    }

    .home-index-15 .banner-box-one .btn {
        padding: 9px 11px;
    }

    .home-index-15 .banner-box-one {
        padding: 3rem 0rem 3rem 0rem;
    }
}

@media only screen and (max-width: 390px) {}