.vendornavbar {
    background-color: var(--color-article2-background);

    .v-nav {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        overflow: auto;

        .active {
            border-bottom: 2px solid var(--secondary);
        }

        li {
            display: inline-block;
            padding: 20px 0;
            margin: 0 15px;

            a {
                padding: 0;
                color: var(--color-text-secondary);
                white-space: nowrap;
            }

            &:first-child {
                margin-left: 0;

                a {
                    padding-left: 0;
                    position: relative;
                    color: var(--text-color-light);

                    &::before {
                        content: "";
                        position: absolute;
                        right: -15px;
                        top: 0;
                        width: 1px;
                        height: 100%;
                        background-color: var(--secondary);
                    }
                }
            }

        }
    }

    .searchbar {
        position: relative;

        .form-control {
            height: calc(2.2em + 0.75rem + 2px);
            padding-right: 40px;
        }

        .search {
            position: absolute;
            right: 0;
            top: 0;
            padding: 13px;

            i {
                font-size: 18px;
                color: var(--color-text);
            }
        }
    }
}

.page-header-vendor {
    background-position: 0px 80%;

    .vendor-navmain {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }

    .vlogoImg {
        width: 110px;
    }

    .storeinfo {
        padding: 10px 15px;
        background-color: var(--color-article4-background);

        ul {
            li {
                display: inline-block;
                padding: 0 10px;

                p {
                    margin-bottom: 0;
                    color: var(--color-text-secondary);
                }

                &:first-child {
                    padding-left: 0;
                    border-right: 1px solid var(--secondary);
                }
            }
        }
    }

    h6 {
        color: var(--color-text);
        font-family: $font-family-base-bold;
        font-size: 1rem;
        margin-bottom: 5px;

        span {
            display: block;
            font-family: $font-family-base;
            font-size: 1rem;
            color: var(--secondary);
        }
    }
}

.bannerwithcarousel {
    overflow: hidden;

    .slick-slider .slick-dots {
        margin-top: -60px !important;
    }
}

.bannwithproduct {
    .pro-heading-title {
        padding-bottom: 30px;
    }
}

.v-ban {
    padding-top: 30px;
    display: block;
}

.v-product {
    padding-top: 30px;

    .v-thumb {
        height: 190px;
        width: 100%;
        overflow: hidden;

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }

        .icon {
            position: absolute;
            right: 0;
            bottom: 0;
            outline: none;
            border: none;
            border-radius: 200px;
            display: flex;
            width: 40px;
            height: 40px;
            margin: 0px;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
            transition-duration: 0.3s;
            margin-bottom: 15px;
            margin-right: 15px;
        }

    }

    .v-content {
        margin-top: 10px;

        .v-heading {
            font-family: $font-family-base-bold;
            font-size: ($font-size-lg - 0.1rem);
            margin-bottom: 5px;
            color: var(--color-text-secondary);
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;

        }

        .v-price {
            ins {
                text-decoration: none;
                font-family: $font-family-base-bold;
                font-size: $font-size-lg;
                color: var(--secondary);
            }

            del {
                color: var(--color-text-dark);
                font-family: $font-family-base-bold;
                font-size: $font-size-base;

            }
        }
    }
}

.voucherCard {
    background-color: var(--color-article1-background);
    padding-bottom: 100px;
}

.v-vouchercard {
    margin-top: 30px;
    border: 2px solid var(--color-border);
    border-style: dashed;
    padding: 30px 60px;
    position: relative;
    background-color: var(--color-page-background);
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content: "";
        position: absolute;
        left: -2px;

        width: 25px;
        height: 50px;
        border-color: var(--color-border);
        border-bottom-right-radius: 200px;
        border-top-right-radius: 200px;
        border-bottom-style: dashed;
        border-top-style: dashed;
        border-right-style: dashed;
        background-color: var(--color-article1-background);

    }

    &::after {
        content: "";
        position: absolute;
        right: -2px;
        width: 25px;
        height: 50px;
        border-color: var(--color-border);
        border-bottom-left-radius: 200px;
        border-top-left-radius: 200px;
        border-bottom-style: dashed;
        border-top-style: dashed;
        border-left-style: dashed;
        background-color: var(--color-article1-background);
    }

    h6 {
        color: var(--color-text);
        font-family: $font-family-base-bold;
        font-size: $font-size-lg;
        margin-bottom: 15px;
    }
}

