.gridingoverlay {
    .overlapping-image-feature {
        $self: &;

        position: relative;
        overflow: hidden;
        flex-direction: column-reverse;

        &__image-column {
            @media screen and (min-width: 768px) {
                position: absolute;
                left: 0;
                top: 0;
                width: 58.333%;
                z-index: -1;

                img {
                    width: 100%;
                    height: 625px;
                }
            }


        }

        &__content-column {
            width: 100%;
            padding: 60px 45px;


            @media screen and (min-width: 768px) {
                width: 58.333%;
                background-color: var(--color-page-background);
                margin: 60px 0;
            }
        }

        &__content-wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &__content {
            width: 100%;
        }

        &--flip {
            @media screen and (min-width: 768px) {
                display: flex;
                flex-direction: row-reverse;
            }

            #{$self}__image-column {
                left: 0;

            }
        }
    }

    .pro-info {
        margin-top: 0;

        h2 {
            font-family: $font-family-base-bold;
            font-size: $font-size-lg;
            color: $danger;
            margin-bottom: 15px;
        }

        h3 {
            color: var(--color-text);
            font-family: $font-family-base-bold;
            font-size: ($font-size-lg + 1);
            line-height: 1.5;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;

            a {
                color: var(--color-text);
            }
        }

        p {
            color: var(--color-text-dark);
            margin-top: 14px;
            margin-bottom: 20px;
            line-height: 1.5;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
        }
    }

    .pro-price {
        font-family: $font-family-base-bold;
        margin-bottom: 25px;

        del {
            font-size: $font-size-base ;
            color: var(--color-text-dark);
            text-decoration: line-through;
        }

        ins {
            font-size: ($font-size-lg + 0.125);
            color: var(--secondary);
            text-decoration: none;
        }
    }
}

