.vendorregestration {

    #msform {
        position: relative;
    }


    #msform fieldset:not(:first-of-type) {
        display: none
    }

    .v-sinupfildes {
        display: flex;
        justify-content: space-between;

        .from-group {
            width: 48%;
        }
    }

    .form-control {
        height: calc(1.5em + 0.75rem + 5px);
    }

    .inputtop {
        position: absolute;
        top: 0;
        height: 100%;
        display: flex;
        align-items: center;
        right: 12px;
    }

    .ms-parent {
        .ms-choice {
            background-color: transparent;
            border: 2px solid var(--color-border);
            height: calc(1.5em + 0.75rem + 2px);

            >span {
                top: 4px;
            }
        }

    }


    #progressbar {
        padding-top: 5px;
        margin-bottom: 30px;
        overflow: hidden;
        display: flex;
        justify-content: center;

        li {
            list-style-type: none;
            font-size: 15px;
            width: 25%;

            position: relative;
            display: flex;
            justify-content: center;

            &::after {
                content: '';
                width: 100%;
                height: 2px;
                background: var(--color-border);
                position: absolute;
                left: -42px;
                top: 22px;
                z-index: 0;
            }

            &:first-child::after {
                opacity: 0;
                display: none;
            }
        }

        .stepiner {
            position: relative;
            border-radius: 200px;
            width: 45px;
            height: 45px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: var(--color-page-background);
            border: 1px solid var(--color-border);
            color: var(--color-border);
            z-index: 1;

        }

        .m-step {
            position: absolute;
            top: -5px;
            left: 5px;
            width: 15px;
            height: 15px;
            background-color: var(--color-border);
            color: var(--text-color-secondary);
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 10px;
            border-radius: 200px;
            line-height: normal;
        }

        .active {
            background-color: transparent;

            &::after {
                background-color: var(--secondary);
            }

            .stepiner {

                color: var(--secondary);
                border: 1px solid var(--secondary);

                &::before {

                    background-color: var(--secondary);
                }

                .m-step {
                    background-color: var(--secondary);
                    color: var(--text-color-secondary);
                }
            }
        }
    }

    .nav-pills {
        .nav-item {
            width: 33.33%;

            &:first-child {
                .nav-link:first-child:after {
                    opacity: 0;
                }
            }
        }

        .nav-link {
            background-color: transparent;
            position: relative;

            &::after {
                content: '';
                width: 100%;
                height: 2px;
                background-color: var(--color-border);
                position: absolute;
                left: -73%;
                top: 30px;
            }



        }


    }
}

