.footerAndNewslater{
    position: relative;
    .newsletter-1{
        position: absolute;
        top: -60px;
        padding-top: 0;
        width: 100%;
    }
}
.footer10{
    background-color: var(--color-article4-background);
    padding-top: 110px;
    padding-bottom: 60px;
    margin-top: 120px;

    h2 {
        color: var(--color-text);
        font-family: "Poppins-Bold", sans-serif;
        font-size: 1rem;
        margin-bottom: 15px;
    }
    ul {
        list-style-type: none;
        margin: 0;
      li {
            margin-bottom: 5px;
            color: var(--color-text);
            &:last-child{
                margin-bottom: 0;
            }
            a{
                font-family: $font-family-base;
            }
        }
    }
    .policies{
        padding-top: 60px;
    }
} 
.newsletter-1{
    .newslaterinner{
        border-radius: 200px;
        background-color: var(--dark);
        padding: 30px;
    }
  .titled{
      h5{
          color: var(--color-white);
          font-size: ($font-size-lg + 0.5rem);
          font-family: $font-family-base-bold;
      }
      p{
        color: var(--color-white);
          margin-bottom: 0;

      }
  }
  .newslatersearch{
    .input-group > .input-group-append >  .btn{
        border-radius: 200px;
    }
     .input-group {
        background-color: var(--color-article1-background);
        padding: 6px;
        border-radius: 200px;
        overflow: hidden;
      
    }
      .form-control{
          height: auto;
          border: none;
          box-shadow: none;
      }
  }
}


@media only screen and (max-width:992px){
    .newsletter-1 {
        .newslaterinner{
            .titled{
                margin-bottom: 15px;
                text-align: center;
            }
        }
    }
    .footerAndNewslater{
        display: flex;
        flex-direction: column;
        margin-top: 60px;
        .footer10{
            order: 2;
            margin-top: 0;
            padding-top: 60px;
        }
       .newsletter-1{
           position: relative;
           top: auto;
           margin-bottom: 30px;
       }
    }
}
@media only screen and (max-width:576px){
    .newsletter-1 .newslaterinner {
        border-radius: 15px; 
        padding: 15px;
        .newslatersearch{
            .input-group{
                flex-direction: column;
                border: none;
                background-color: transparent;
                border-radius: unset; 
                .form-control{
                    border-top-right-radius: 3px;
                    border-bottom-right-radius: 3px; 
                    width: 100%; 
                }
                .input-group-append{
                    margin-top: 15px;
                }
            }
        }
    }
  .footer10{
      .col-12.col-lg-2{   
        margin-top: 30px; 
          &:first-child{ 
            margin-top: 0px; 
          }
      }
  }
}