.home11 {
    $secondary: #FE7C1F;

    .footer-subscribe {
        background-color: var(--secondary);
    }

    .rev_slider_wrapper .rev_slider li .BigBold-SubTitle {
        color: var(--secondary);
    }
}

.catgorymenuheader .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 1.5rem;
    padding-left: 0.5rem;
}

.v-tabs-content {
    background-color: var(--color-page-background);

    .pro-heading-title-1 {
        display: flex;
        padding-bottom: 20px;
        justify-content: space-between;
        border-bottom: 1px solid var(--color-border);

        p {
            line-height: 1.5;
            letter-spacing: 1.5px;
            font-size: 16px;
            color: var(--color-text-secondary);
            margin: 0 0 -6px 0;
            text-decoration: none;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
        }

    }

    .tab-content-1 {}
}

.v-product-card1 {
    background-color: var(--color-article-6-backgound);
    border-radius: 5px;

    article {
        display: inline-block;
        width: 100%;
        padding: 0;

        .pro-thumb {
            position: relative;
            height: 270px;
            width: 100%;
            overflow: hidden;
            padding: 15px;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        .pro-info {
            // margin-top: 15px;
            margin-bottom: 0px;

            .new-price {
                display: flex;
                justify-content: center;
                align-items: center;

                ins {
                    font-family: $font-family-base-bold;
                    font-size: 1.475rem;
                    color: #fe4536;
                    text-decoration: none;
                    margin-right: 10px;

                    del {
                        font-size: 0.875rem;
                        color: var(--color-text-dark);
                        text-decoration: line-through;
                    }
                }

            }

            .pro-stars {
                display: flex;
                justify-content: center;
                margin-top: 10px;

                .checked {
                    color: orange;
                }
            }

            .pro-timer {
                border-top: 1px solid var(--color-border);
                border-bottom: 1px solid var(--color-border);
                margin-bottom: 5px;
                margin-top: 15px;
                display: flex;
                justify-content: center;

                span {
                    border: none;
                    border-radius: 3px;
                }

                .days,
                .hours,
                .mintues,
                .seconds {
                    float: left;
                    padding: 8px;
                    font-family: $font-family-base-bold;
                    font-size: $font-size-lg;
                    color: var(--color-text);
                    height: 60px;
                    margin-right: 0;
                    text-align: center;

                    small {
                        display: block;
                        font-family: $font-family-base;
                        font-size: $font-size-sm;
                    }
                }
            }

            .pro-buttons {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 15px 0px;

                a {
                    font-family: "Poppins-Bold", sans-serif;
                    font-size: 0.75rem;
                    line-height: 13px;
                    text-transform: uppercase;
                    border: none;
                    margin-right: 15px;
                }
            }
        }
    }
}

