// banner style
.banner-seventeen .group-banners {
    .banner-image {
        position: relative;
        border-radius: 0;
        overflow: hidden;
        margin-bottom: 0;

        img {
            width: 100%;
            webkit-transform: scale(1);
            transform: scale(1);
            -webkit-transition: .8s ease-in-out;
            transition: .8s ease-in-out;

            &:hover {
                -webkit-transform: scale(1.1);
                transform: scale(1.1);
            }
        }

        .v-contain-banner {
            position: absolute;
            top: 0px;
            padding-top: 30px;
            width: 100%;
            display: flex;
            justify-content: center;

            .banner-in {
                height: auto;
            }

            h2 {
                font-family: $font-family-base-bold;
                font-size: 33px;
                color: var(--text-color-secondary);
            }

            h4 {
                font-family: $font-family-sans-serif;
                color: var(--text-color-secondary);
            }
        }
    }

    .imagespace {
        margin-bottom: 32px;
    }
}

