.footer-v-fourteen {
    .email {
        background-color: var(--color-white-background);

        input {
            background-color: transparent;
        }
    }

    .categories {
        margin: 12px 0px;
    }

    .down-footer {
        .image-box {
            text-align: center;
            position: relative;
        }

        .image-box::before {
            content: " ";
            width: 42%;
            height: 2px;
            color: black;
            background-color: black;
            display: block;
            position: absolute;
            top: 50%;
        }

        .image-box::after {
            content: " ";
            width: 42%;
            height: 2px;
            color: black;
            background-color: black;
            display: block;
            position: absolute;
            top: 50%;
            right: 11px;
        }

        img {
            position: relative;
            z-index: 1;
        }
    }
}

@media only screen and (max-width: 1100px) {

    .footer-v-fourteen .down-footer .image-box::before,
    .footer-v-fourteen .down-footer .image-box::after {
        width: 38%;
    }
}

@media only screen and (max-width: 776px) {

    .footer-v-fourteen .down-footer .image-box::before,
    .footer-v-fourteen .down-footer .image-box::after {
        width: 32%;
    }
}

@media only screen and (max-width: 500px) {

    .footer-v-fourteen .down-footer .image-box::before,
    .footer-v-fourteen .down-footer .image-box::after {
        width: 25%;
    }
}