.newsletterfooter-v-six {
    padding-bottom: 30px;

    .newsletter {
        display: flex !important;
        align-items: start !important;

        .input-group {
            border: none !important;
            input{
                background-color: var(--color-article1-background !important);
            }

            button {
                border: 2px solid #383838;
                color: var(--color-text) !important;
                background: transparent !important;
            }
        }

        h2 {
            color: var(--color-text);
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            text-transform: uppercase;
            margin: 0;
        }

        p {
            color: var(--color-text);
            font-size: 12px;
            font-style: normal;
            font-weight: 300;
            line-height: 140%;
            // a{
            //     text-decoration: underline !important;
            // }
        }
    }
}

.footer-main {
    .categories {
        .social {
            list-style-type: none;
            display: flex;
            flex-wrap: wrap;

            li {
                width: 40px;
                height: 40px;
                background-color: #000;
                color: white !important;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-self: center;
                margin-right: 10px;
                margin-top: 5px;

                a {
                    color: white;
                    align-self: center;
                }
            }
        }
    }
}

.footer-bottom {
    background-color: #000;

    .detail {
        p {
            color: white;

            a {
                text-decoration: underline !important;
                color: white !important;
            }
        }
    }
}
@media only screen and (min-width:320px) and (max-width:576px) {
    .bgfooter {
        .input-group {
            input {
                width: 100% !important;
            }
        }
    }
}

