.home-12 {
    $secondary: #ed711b;
    --secondary: #{$secondary};
}



.home-12,
.index-container-12 {
    .collection-off .img-m-banner {
        background-image: url('../../images/index-12/Cimage.png');
        background-repeat: no-repeat;
        background-size: cover;
        padding: 12rem;
    }

    .header-icon {
        place-items: center;
    }

    .categories-panel {
        padding: 0;
        border: none;
        -webkit-box-shadow: unset;
        box-shadow: unset;
        border-radius: 5px;

        .navflex-basis {
            background: var(--bg-icone);
        }

        ul .nav-item a img {
            width: 26px;
            height: 26px;
        }
    }

    // BANNER IMAGE SECTION

    .img-hover-zoom {
        background-image: url("../../images/index-12/2000988-model-girl-classy-women\ \(1\).jpg");
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;

        span {
            width: 40px;
            margin-left: 5px;
        }

        .orange_border {
            border-top: 2px solid var(--secondary);
            width: fit-content;
        }
    }

    .text-body {
        color: var(--color-text) !important;
    }

    .brand-container {

        .brand-images,
        .brand-images-2,
        .brand-images-1 {
            background-image: url('../../images/index-12/Cimage2.png');
            background-repeat: no-repeat;
            background-size: cover;
            padding: 29px;

            svg {
                width: 20px;
            }
        }

        .brand-images-2 {
            background-image: url('../../images/index-12/Cimage3.png');
        }

        .brand-images-1 {
            background-image: url('../../images/index-12/Cimage1.png');
        }
    }

    .product article .pro-hover-icons .icons {
        display: block;

        .btn-light {
            background-color: transparent !important;
            border: none;
            box-shadow: unset;
        }
    }

    .product article .pro-hover-icons {
        display: grid;
        justify-content: end;
        bottom: 0px;
        top: 0px;

        .btn-light:hover {
            color: var(--secondary);
        }
    }

    .best-wrapper {

        .border-dark {
            .btn-secondary {
                -webkit-transition-duration: 0.3s;
                transition-duration: 0.3s;
                -webkit-transition-property: color, background-color;
                transition-property: color, background-color;
                background-color: transparent;
                border-color: var(--secondary);
                color: var(--color-text) !important;
                padding: 5px 15px;

                &.active {
                    color: var(--secondary) !important;
                }
            }
        }

        .tabs-content .nav .nav-link.active,
        .tabs-content .nav .nav-link:hover {
            color: var(--secondary) !important;
        }
    }

    .shop-now {
        svg {
            width: 20px;
        }
    }

    .slider-banner {

        .prev0,
        .next0 {
            position: absolute;
            top: 50%;
            font-size: 26px;
            border: none;
            background: transparent;
        }

        .next0 {
            right: 0;
        }

        .product article .pro-hover-icons {
            .btn-light:hover {
                color: var(--secondary);
            }
        }
    }

    .collection-off {
        svg {
            width: 30px;
        }
    }

    .new-products {
        .new8-carousel-js1 {
            .slick-slide {
                outline: none;
                padding-right: 0px !important;
                padding-left: 0px !important;
            }
        }

        .arrow-style8 {
            .slick-list {
                height: 100% !important;
            }
        }

        .arrow-style8 .slick-arrow {
            top: -45px;
        }
    }

    .blog-wrapper {
        svg {
            width: 20px;
        }
    }
}



.bodyrtl {
    .ms-lg-3 {
        margin-left: unset !important;
        margin-right: 1rem !important;
    }

    .ps-3 {
        padding-left: 0rem !important;
        padding-right: 1rem !important;
    }

    .text-rtl {
        text-align: end;
    }

    .text-rtll {
        justify-content: end;
    }

    .ps-5 {
        padding-left: 0rem !important;
        padding-right: 3rem !important;
    }

    .me-4 {
        margin-right: 0rem !important;
        margin-left: 1.5rem !important;
    }

    .ms-3 {
        margin-left: 0rem !important;
        margin-right: 1rem !important;
    }

    .best-wrapper .product3 {
        padding-left: 0rem;
        padding-right: 0rem;
    }

    .text-just-rtl {
        justify-content: start !important;
    }

    @media only screen and (min-width: 1400px) {
        .header-main {
            background: linear-gradient(90deg, var(--secondary) 70.2%, #333333 23%) !important;
        }
    }
}

.index-container-12.bodyrtl {

    .navbar-nav {
        padding-left: 0rem;
        padding-right: 0rem;
    }

    .header-thirteen .bg-top-header .list-unstyled {
        border-right: unset;
        border-left: 2px solid black;
        border-radius: 0px;
        padding-left: 20px;
        padding-right: unset;
    }

    .pee-5 {
        padding-right: 3rem !important;
    }
}

@media only screen and (max-width: 991px) {
    .m-footer-up {
        text-align: center;
    }
}

@media only screen and (max-width: 770px) {
    .border-m {
        border: none !important;
    }

    .blog-text {
        text-align: center;
    }

    .home-12 .img-hover-zoom,
    .index-container-12 .img-hover-zoom {
        padding: 50px;
    }

    .m-footer-up {
        text-align: center;
    }

    .index-container-12 .new-products .new8-carousel-js1 .slick-slide {
        padding-right: 15px !important;
        padding-left: 15px !important;
    }

    .footer-thirteen .footerbottomin {
        text-align: center;
    }
}

@media only screen and (max-width: 500px) {

    .home-12 .img-hover-zoom,
    .index-container-12 .img-hover-zoom {
        padding: 35px 0px;
    }

    .m-footer-up {
        text-align: center;
    }

    .index-container-12 .new-products .new8-carousel-js1 .slick-slide {
        padding-right: 15px !important;
        padding-left: 15px !important;
    }

    .home-12 .collection-off .img-m-banner,
    .index-container-12 .collection-off .img-m-banner {
        padding: 8rem;
    }

    .shop-now {
        .deal-index5-carousel-js {
            height: fit-content !important;
        }
    }

    .home-12 .new-products .arrow-style8 .slick-list,
    .index-container-12 .new-products .arrow-style8 .slick-list {
        height: fit-content !important;
    }
}