/*-----------
/----- Header Top bar -------- */
.header-fourteen-index {
    $secondary: #fff159;
    background-color: var(--color-article1-background);
    width: 100%;
    background-color: var(--color-bg14-background);

    .header-mini {
        box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 16px -4px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;

        .navbar-lang {
            display: flex;

            .dropdown {

                .btn.dropdown-toggle {
                    color: var(--color-text);
                    font-family: $font-family-base;
                    font-size: $font-size-base;
                    border-color: var(--color-border);
                    -webkit-transition: 0.4s;
                    transition: 0.4s;
                    padding: 17px 18px 16px 12px;
                    margin: 0px 0;

                    &::after {
                        right: 4px;
                        top: 46%;
                    }
                }

                .dropdown-menu {
                    animation: animationmenu ease 0.5s;
                    animation-iteration-count: 1;
                    transform-origin: 50% 50%;
                    animation-fill-mode: forwards;
                    -webkit-transition: 0.4s;
                    transition: 0.4s;
                    padding: 1.2rem 0;
                    border-width: 0;
                    margin: 1px 0 0 -25px;
                    -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);

                    .dropdown-item {

                        padding: 5px 1.7rem;

                        &:hover,
                        &.active,
                        &:active {
                            background-color: transparent;
                            color: var(--secondary);

                        }
                    }
                }

                &:last-child>.btn {
                    border-right: none;
                }

                &:last-child>.dropdown-menu {
                    margin: 1px 0 0 0px;
                }

                &:hover>.dropdown-menu {
                    display: block;
                    -webkit-transition: 0.4s;
                    transition: 0.4s;
                }

                &:first-child>.btn {
                    padding-left: 0;
                }
            }
        }

        .pro-header-right-options {
            padding-left: 0;
            padding-right: 0px;
            margin-bottom: 0;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            .dropdown-menu {
                animation: animationcartmenu ease 0.5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
                padding: 15px 0;
                border-radius: 0;
                border: none;
                margin: 12px 0 0 -25px;
                -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
            }

            .dropdown-item:hover,
            .dropdown-item:focus {
                color: var(--secondary);
                text-decoration: none;
                background-color: transparent;
            }

            .dropdown-toggle {
                padding-right: 28px;

                &::after {
                    content: "";
                }
            }

            li {
                position: relative;
                display: inline-block;
                list-style: none;

                .fas {
                    margin-right: 5px;
                    color: var(--color-text);
                }

                .list-link {

                    font-size: $font-size-base;
                    color: var(--color-text);

                    &:hover {
                        color: var(--secondary);
                    }
                }

                &:first-child .list-link {
                    padding-left: 0px;
                    border-left: none;
                    background-color: transparent;
                    border: none;
                    outline: none;
                }

                .btn-light {
                    background-color: $white;
                    border: none;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 34px;
                    width: 34px;
                    padding: 0;
                    border-radius: 200px;

                    .fas,
                    .far {
                        font-size: $font-size-lg;
                        color: $black;

                        -webkit-transition: 0.4s;
                        transition: 0.4s;

                        &:hover {
                            color: var(--secondary);
                        }
                    }
                }
            }

            .dropdown {
                float: right;

                .dropdown-toggle::after {
                    content: none;
                }

                .dropdown-menu {
                    animation: animationcartmenu ease 0.5s;
                    animation-iteration-count: 1;
                    animation-fill-mode: forwards;
                    -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                    width: 270px;
                    border-radius: $border-radius;
                    border-color: $white;
                    z-index: 99999;
                    margin-top: 12px;
                    left: auto !important;
                }
            }
        }
    }

    .showtrans{
        transform: translate(11px, 0px) !important;
    }

    .search-field2 {
        ul {

            li {
                list-style: none;
                position: relative;
                margin-right: 15px;

                &:last-child {
                    margin-right: 0;
                }
            }

            .dropdown {
                float: right;
                border-left: 2px solid white;
                // padding: 28px 11px;
                padding: 28px 3px 28px 9px;
                .btn{
                    padding: unset;
                    text-transform: unset;
                }

                .btncart {

                    .fas {
                        font-size: 22px;
                        color: var(--color-text);
                    }

                    .badge {
                        position: absolute;
                        right: 0px;
                        bottom: -5px;
                        top: -10px;
                        height: 16px;
                        width: 16px;
                        color: var(--text-color-secondary);
                        border-radius: $border-radius;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        z-index: 99;
                        border-radius: 200px;
                    }
                }

                .dropdown-toggle::after {
                    content: none;
                }

                .dropdown-menu {
                    background-color: var(--color-article1-background);
                    border-color: var(--color-article1-background);
                    animation: animationcartmenu ease 0.5s;
                    animation-iteration-count: 1;
                    animation-fill-mode: forwards;
                    -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                    width: 270px;
                    border-radius: $border-radius;
                    z-index: 99999;
                    margin-top: 23px;
                    left: auto !important;

                    .dropdown-item {
                        color: var(--color-text);
                    }

                    .shopping-cart-items {

                        padding-left: 25px;
                        padding-right: 25px;
                        padding-top: 15px;
                        padding-bottom: 15px;
                        max-height: 350px;
                        overflow-y: auto;
                        overflow-x: hidden;

                        li {
                            float: left;
                            position: relative;
                            width: 100%;
                            margin-bottom: 10px;
                            border-bottom: $border-width solid;
                            border-color: var(--color-border);
                            padding-bottom: 10px;
                            margin-left: 0;

                            .item-thumb {
                                position: absolute;
                                left: 0;
                                top: 0;


                                .image {
                                    width: 64px;
                                    height: 64px;
                                    border: $border-width solid;
                                    border-color: var(--color-border);
                                    border-radius: $border-radius;
                                    overflow: hidden;

                                }
                            }

                            .item-detail {
                                float: left;
                                padding-left: 74px;

                                h3 {
                                    font-family: $font-family-base-bold;
                                    font-size: $font-size-base;
                                    text-transform: capitalize;
                                    color: var(--color-text);
                                }

                                .item-s {
                                    display: flex;
                                    align-items: center;
                                    justify-content: space-between;
                                    font-size: $font-size-base;
                                    color: var(--color-text);


                                    .fas {
                                        color: var(--color-text-secondary);
                                        float: right;
                                        margin-left: 8px;
                                        -webkit-transition: 0.4s;
                                        transition: 0.4s;

                                        &:hover {
                                            color: var(--secondary);
                                        }
                                    }
                                }
                            }

                            &:last-child {
                                border-bottom: none;
                                margin-bottom: 0;
                                padding: 0;
                            }
                        }

                        .item-summary {
                            font-family: $font-family-base-bold;
                            font-size: $font-size-lg;
                            font-weight: $font-weight-bold;
                            color: var(--secondary);
                            margin-bottom: 0;
                            margin-top: 5px;
                            white-space: normal;
                        }
                    }
                }
            }
        }

        .dropdown-menu {
            padding: 0;
        }

        .search-field-module {

            .form-control {
                width: 290px;
            }

            .btn {
                position: absolute;
                top: 0;
                right: 0;
                padding: 10px;
            }

            .fas {
                font-size: $font-size-lg;
                color: var(--dark);

                -webkit-transition: 0.4s;
                transition: 0.4s;

                &:hover {
                    color: var(--secondary);
                }
            }
        }
    }

    .navbar {
        padding: 0px;

        .navbar-collapse {

            .navbar-nav {

                .nav-item {
                    margin-right: 45px;
                    padding: 18px 0 18px 0px;

                    &:last-child {
                        margin-right: 0;
                    }

                    .nav-link {
                        position: relative;
                        font-family: $font-family-base;
                        font-size: $font-size-base;
                        padding: 0;

                        &:hover {
                            text-decoration: none;
                        }

                        .badge {
                            position: absolute;
                            top: 2px;
                            left: 10px;
                            font-family: $font-family-base;
                            background-color: $danger;
                            color: color-yiq($danger);
                        }
                    }

                    .dropdown-menu {
                        animation: animationmenu ease 0.5s;
                        animation-iteration-count: 1;
                        transform-origin: 50% 50%;
                        animation-fill-mode: forwards;
                        margin: 0px 0 0 -25px;
                        padding: 15px 0;
                        border-radius: $border-radius;
                        border: none;
                        width: 180px;
                        -webkit-box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);
                        box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);

                        .dropdown-item {
                            white-space: normal;

                            img {
                                width: auto;
                                position: fixed;
                                right: -150px;
                                top: -5px;
                                visibility: hidden;
                                z-index: 3;
                                width: 150px;
                                -webkit-box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                opacity: 0;
                                -webkit-transition: .2s;
                                transition: .2s;
                            }

                            &:hover {
                                background-color: transparent;
                                color: var(--secondary);
                                transition: 0.5s;

                                img {
                                    visibility: visible;
                                    top: 0;
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }

                .nav-item.dropdown:hover>.dropdown-menu {
                    display: block;
                }
            }
        }
    }

    .all-catg {
        color: #a3a3a3;
        background: transparent;

        &::after {
            position: absolute;
            right: -14px;
            bottom: 18px;
            font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
            font-weight: 900;
            content: "\f107";
            border: none;
            font-size: 11px;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 5px;
            width: 10px;
        }
    }

    .all-btn-cat {
        transform: translate(389px, 7px) !important;
        min-width: 15rem;
        padding: 0px;
    }

    .search-field-module {
        position: relative;
        background-color: var(--color-article1-background);
        box-shadow: none;
        width: 100%;
        border-radius: 5px;

        .search-field-wrap {
            input {
                width: 70%;
                border: none;
                background: transparent;
                padding: 0px 15px;
                color: #a3a3a3;
            }

            select {
                border: none;
                background-color: transparent;
                text-align: end;
                width: auto;
                color: #a3a3a3;

                option {
                    text-align: left;
                }
            }

            .btn {
                background-color: var(--color-article1-background);
            }
        }
    }

    .mega-menu-md {
        width: 654px !important;
    }

    .mega-menu-sm {
        width: 436px !important;
    }

    .mega-menu {
        padding: 0 !important;

        .no-gutters {
            padding: 0;
        }

        ul {
            list-style: none;
            padding: 15px 0;

            li.dropdown-header {
                white-space: normal;

                h5 {
                    font-family: $font-family-base;
                    font-size: $font-size-base;
                    color: var(--color-text);
                    text-transform: uppercase;
                    font-weight: bold;
                }
            }


        }

        .banner-content {
            position: absolute;
            top: 0;
            left: 0;
            padding: 15px;

            .banner-title {
                font-family: $font-family-base;
                font-size: $font-size-lg;

                span {
                    strong {
                        font-family: $font-family-base-bold;
                        font-size: ($font-size-lg + 1rem);
                    }
                }
            }
        }
    }
}

.dropdown-submenu:hover>.dropdown-menu {
    display: block;
}

.dropdown-submenu {
    position: relative;

    .dropdown-menu {
        top: -15px;
        left: 100%;
        margin-left: 0 !important;
    }
}

.bodyrtl {
    .ms-2 {
        margin-left: unset !important;
        margin-right: 0.5rem !important;
    }
    .header-fourteen-index .search-field2 ul .dropdown {
        float: right;
        border-left: unset;
        border-right: 2px solid white;
        padding: 28px 9px 28px 3px;
    }
    .header-fourteen-index .search-field2 .search-field-module .btn {
        right: auto;
        left: 0;
    }

    .header-fourteen-index .header-mini .pro-header-right-options li .fas {
        margin-right: 0;
        margin-left: 5px;
    }

    .header-fourteen-index .header-mini .pro-header-right-options li:first-child .list-link {
        padding-right: 0;
        padding-left: 28px;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown:first-child>.btn.dropdown-toggle {
        padding: 4px 0 4px 38px;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown .btn.dropdown-toggle::after {
        right: auto;
        left: 25px;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown .btn.dropdown-toggle {
        border-right: none;
        border-left: 2px solid $border-color;
        padding: 4px 28px 4px 38px;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown:last-child>.btn {
        border-left: none;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown .dropdown-menu {
        right: 0 !important;
        left: auto !important;
        text-align: right;
        margin: 1px -25px 0 0;
    }

    .header-fourteen-index .header-mini .navbar-lang .dropdown:last-child>.dropdown-menu {
        margin: 1px 0 0 0;
    }

    .header-fourteen-index .search-field2 ul .dropdown .dropdown-menu {
        right: auto;
        left: 0px !important;
        transform: translate3d(0px, 30px, 0) !important;
    }

    .header-fourteen-index .header-mini .pro-header-right-options .dropdown-menu {
        text-align: right;
    }

    .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item.dropdown:hover>.dropdown-menu {
        left: auto;
        right: 0;
        text-align: right;
        margin: 0px -25px 0 0;
    }

    .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-left: 45px;
        margin-right: 0;
    }

    .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu .dropdown-item img {
        left: -150px;
        right: auto;
    }
}

@media only screen and (max-width: 1199px) {
    .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-right: 30px;
    }

    .header-fourteen-index .search-field2 .search-field-module .form-control {
        width: 200px;
    }

    .header-fourteen-index .search-field2 ul .dropdown {
        padding: 22px 7px;
        margin-right: 8px;

        .btn,
        i {
            font-size: 11px !important;
        }
    }
}

@media only screen and (min-width: 1024px) and (max-width: 1199px) {
    .header-fourteen-index .navbar .navbar-collapse {
        justify-content: flex-start;
    }

    .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-right: 26px;
    }

    .bodyrtl .header-fourteen-index .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-left: 26px;
        margin-right: 0;
    }
}