// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system

$white:    #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;  // All Border Color
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;  // All Text Color
$black:    #000;

$blue:    #479af1;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #e83e8c;
$red:     #dc3545;
$orange:  #fd7e14;
$yellow:  #ffc107;
$green:   #28a745;
$teal:    #20c997;
$cyan:    #17a2b8;



$primary-dark: #28B293;
$primary-light: #022e46;


$primary:       #131313;  // Main color or Primary color of the website
$secondary:     #fe4536;  // Main color for buttons 

$secondary-hover: #131313; //icon-hover no used

$success:       $green;
$info:          $blue;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-200;  // For only use background color of the website
$dark:          $gray-800;


$color-gray-light: rgba($white, .15);
$color-gray-dark: rgba($black, .15);




:root {
  --primary: #{$primary};
  --secondary: #{$secondary};
  --dark : #{$dark};
  --light:#{$light};

  --primary-dark: #{rgba($primary, 0.85)};
  --primary-darkest: #{rgba($primary, 0.30)};

  --secondary-dark: #{rgba(var($secondary), 0.85)};
  --secondary-darkest: #{lighten($secondary, 30%)};

  --artical-background-success : #{$success};
  --artical-background-success-dark: #{rgba($success, 0.85)};
  --artical-background-success-darkest: #{rgba($success, 0.30)};
  --artical-background-info : #{$info};
  --artical-background-info-darkest: #{rgba($info, 0.30)};
  --artical-background-warning : #{$warning};
  --artical-background-warning-darkest: #{rgba($warning, 0.30)};
  --artical-background-danger : #{$danger};
  --artical-background-danger-darkest: #{rgba($danger, 0.30)};


  
  --color-border: #{$gray-300};

  --text-color-primary: #{$white};
  --text-color-primary-contrast: #{$white};

  --text-color-secondary: #{$white};
  --text-color-secondary-contrast: #{$white};
  
  --text-color-dark: #{$white};
  --text-color-dark-contrast: #{$white};

  --text-color-light: #{$black};
  --text-color-light-contrast: #{$black};

  --color-text-dark: #{$gray-800};
  --color-text-secondary:#{$gray-600};
  --color-text-primary: #{$white};
  --color-text: #{$gray-800};
  // --text-color: #{$white};
  --color-article1-background: #{#f5f5f5};
  --color-article13-background: #{#eeeeee};
  --color-article2-background: #{$gray-200};
  --color-article3-background: #{$gray-900}; 
  --color-article4-background: #{#f5f5f5};
  --color-article5-background: #{rgb(36, 34, 34)};
  --color-page-background : #{#fAfAfA};
  --color-white-background : #{#fff};
  --color-white : #{#fff};
  --skelton-color1: #{#ece9e9};
  --skelton-color2: #{#DDDBDD};
  --skelton-color3: #{#e8e8e8};
  --bg-icone: #{#f8f8f8};
  --color-bg14-background : #{#dbdad5};
  --color-bg5: #{#faf3f0};
}

.dark {
  --light:#{$dark};
  --dark:#{$light};
  --text-color-light:#{$white};

  --text-color-dark:#{$black};
  --text-color-dark-contrast: #{$black};

  --color-page-background: #{#333};
  --color-text: #{$white};
  --bg-icone: #{#333333};
  --color-text-dark: #{$gray-400};
  --color-text-light: #{$gray-700};
  --color-text-secondary: #{$gray-500};
  --color-text-primary:#{$black};
  --color-border: #{$gray-800};
  --color-article1-background: #{#222};
  --color-article13-background: #{#222};
  --color-article2-background:#{#2d2b2b};
  --color-article3-background: #{$gray-900};
  --color-article4-background: #{#222};
  --color-article5-background:#{rgb(36, 34, 34)};
  
  --color-white-background : #{#000};
  --color-white : #{#000};
  
  --skelton-color1: #{#222};
  --skelton-color2: #{#2d2b2b};
  --skelton-color3: #{rgb(36, 34, 34)};
  --secondary-darkest : #{#413433};
  --color-bg14-background : #{#222};
  --color-bg5: #{#222};
}

 

$body-bg:var(--color-page-background);
$body-bg-secondary:var( --color-article1-background);


$body-color: $black;
$body-color-secondary: $gray-600;
$box-color:#dbdbdb;


// Set a specific jump point for requesting color jumps
$theme-color-interval:      8%;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold:  150;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark:             $gray-900;
$yiq-text-light:            $white;




// Links
//
// Style anchor elements.

$link-color:                              $gray-900;
$link-decoration:                         none;
$link-hover-color:                        lighten($link-color, 20%);
$link-hover-decoration:                   underline;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
$emphasized-link-hover-darken-percentage: 15%;


$border-width:                1px;
$border-color:                $gray-300 ;

$border-radius:               0;
$border-radius-lg:            0;
$border-radius-sm:            0;


$bg-footer: #fff;
$bg-social: $primary;

