// stylelint-disable value-keyword-case
$font-family-sans-serif:      'Poppins-Regular', sans-serif;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
$font-family-base:            $font-family-sans-serif;

$font-size-base:               0.875rem; // Assumes the browser default, typically `16px`
$font-size-lg:                ($font-size-base + 0.125);
$font-size-sm:                ($font-size-base - 0.125) ;

$font-family-base-bold:        'Poppins-Bold', sans-serif;
$font-family-base-medium:        'Poppins-Medium', sans-serif;
$font-family-base-thin:        'Poppins-Thin', sans-serif;

$font-weight-normal:          400;
$font-weight-semibold:        500;
$font-weight-bold:          600;
$font-weight-bolder:          700;





