.tri-slick {
    .slick-slider {
        .slick-dots li {
            button {
                border-radius: 5px;
                box-shadow: inset 0 0 0 2px $primary;
            }
        }

    }

    .slick-slider .slick-dots li.slick-active button {
        background-color: var(--primary);
    }
}

.tri-top-sellers {
    .product {
        article {

            background-color: var(--color-article1-background);
        }
    }
}

.center-product-style {
    .product {
        article {
            background-color: var(--color-page-background);
            padding: 15px;

            .pro-info {

                .pro-heading {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    text-align: center;
                }


                .pro-price {
                    display: flex;
                    justify-content: center;
                }

                .countdown2 {
                    position: relative;
                    overflow: hidden;



                }
            }
        }
    }
}

.deal-of-the-day-js {
    .product {
        margin-bottom: 30px;
    }
}

.tabs-product-without-carousel,
.productcard-2 {
    .nav {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: -12px;

        .nav-link {
            padding-bottom: 0px;
            padding-top: 0;
            line-height: 1;
        }

        .nav-link:hover,
        .nav-link:focus {
            color: var(--secondary);
        }

        .active {
            color: var(--secondary);
        }
    }

    .product {
        padding-top: 30px;

        article {
            border: 1px solid;
            border-color: var(--color-border);
            padding: 15px;

            .pro-info {
                .pro-heading {
                    text-align: center;
                }

                .pro-buttons {

                    padding: 5px 0 0px 0px;

                    .btn {
                        padding: 0;

                    }

                    a {


                        font-family: $font-family-base-bold;
                        font-size: 0.75rem;
                        line-height: 13px;
                        text-transform: uppercase;
                        border: none;
                        margin-right: 15px;

                        .far {
                            font-weight: 900;
                        }
                    }
                }
            }
        }
    }
}

.Categories-content {
    .product {
        article {
            padding: 15px;
            border-radius: 5px;

            .pro-info {
                text-align: center;

                h3 {
                    a {
                        -webkit-line-clamp: 2;
                    }
                }

                .pro-price {
                    text-align: center;
                    justify-content: center;
                }
            }
        }
    }
}

.pro-category-product {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;

    .pro-cate-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;

        .pro-price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 10px;

            del {
                font-size: $font-size-base ;
                color: var(--color-text-dark);
                text-decoration: line-through;

            }

            ins {
                font-family: $font-family-base-bold;
                font-size: ($font-size-lg + 0.475);
                color: var(--secondary);
                text-decoration: none;
                margin-right: 10px;
            }

        }

        h3 {
            font-family: $font-family-base;
            margin: 0;

            strong {
                font-weight: 600;
            }
        }

        .btn {
            margin-top: 15px;
            border: 1px solid;
        }
    }
}

.dark-bg2 {
    background-image: linear-gradient(#d591a1, #625858);
}

.dark-bg {
    background-image: linear-gradient(#000, #a5b7c5);

    .pro-cate-content {
        .pro-price {
            del {
                color: var(--color-text-dark);
            }

            ins {
                color: var(--secondary);
            }
        }

        h3 {
            color: $white;
        }

        .btn {
            color: var(--color-text-primary);

            &:hover {
                color: var(--secondary);
            }
        }
    }
}

.light-bg {
    background-image: linear-gradient(#eef2f3, #a5b7c5);
}

.fullwidth-banner3 {
    position: relative;
    background-image: linear-gradient(#eef2f3, #a5b7c5);

    /* Set a specific height */
    //    height: 640px;
    height: 100%;
    width: 100%;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &:before {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        content: '';
        display: block;
    }

    .parallax-banner-text {


        h2 {
            font-size: 3rem;
            font-family: $font-family-base-bold;
            color: $body-color;
            margin: 0;
        }

    }
}

.deal-of-the-day {
    padding-bottom: 100px;

    .slick-slider .slick-dots {
        margin-top: 0 !important;
    }
}

