.deals {

    figure {
        position: relative;
        background-color: var(--color-page-background);
        border: 1px solid;
        border-color: var(--color-border);
        border-radius: $border-radius;
        overflow: hidden;
        margin-bottom: 30px;

        padding: 15px;
        width: 100%;



        .img {
            width: auto;
        }

        &.right {
            img {
                float: right;
            }
        }


    }

    .banner-caption {
        position: absolute;
        width: 100%;
        z-index: 2;

        .ratings {
            display: flex;
            align-items: center;
            margin-bottom: 15px;

            p {
                margin-bottom: 0;
                margin-left: 5px;
                color: var(--color-text-secondary)
            }
        }

        .pro-rating {
            .fas {
                color: $yellow;
            }
        }

        .pro-price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 10px;

            del {
                font-size: $font-size-base ;
                color: var(--color-text-dark);
                text-decoration: line-through;

            }

            ins {
                font-family: $font-family-base-bold;
                font-size: ($font-size-lg + 0.475);
                color: var(--secondary);
                text-decoration: none;
                margin-right: 10px;
            }

            .icon {

                outline: none;
                border: none;
                position: relative;
                border-radius: 200px;
                color: #9496b0;
                display: flex;
                width: 40px;
                height: 40px;
                margin: 7px;
                justify-content: center;
                align-items: center;
                text-decoration: none;
                box-shadow: 0 0 3px 0px rgba(59, 59, 59, 0.1);
                transition-duration: 0.3s;
                background-color: $body-bg-secondary;

                &:hover {
                    background-color: $dark;
                    border-color: $dark ;
                    color: color-yiq($dark);
                }



            }

        }

        h3 {
            color: var(--color-text);
            font-family: $font-family-base-bold;
            margin: 0;
            font-size: 14px;
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        p {
            color: var(--secondary);
        }

        .btn {
            padding: 15px 15px;
            margin-top: 15px;

        }

        &.left-caption {
            top: 0;
            left: 0;
            padding-left: 30px;
            padding-top: 30px;
            padding-right: 180px;
            text-align: left;
        }

        &.right-caption {
            top: 0;
            left: 0;
            padding-left: 155px;
            padding-right: 15px;
            padding-top: 20px;
            text-align: left;
        }
    }

}



.testimonial3 {
    .product {
        article {
            border: 1px solid;
            border-color: var(--color-border);
            padding: 30px;

            .media {
                .media-body {
                    margin-left: 30px;

                    p {
                        color: var(--color-text);
                        line-height: 1.5;
                        white-space: normal;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 4;
                        -webkit-box-orient: vertical;
                    }

                    h3 {
                        color: var(--color-text);
                        font-family: $font-family-base-bold;
                        font-size: $font-size-lg;
                        margin-bottom: 15px;
                    }

                    .fas {
                        color: var(--secondary);
                    }
                }
            }
        }
    }
}

.on-sale-product-js {
    .feat-banner-image {
        .Whitecolor {
            h3 {
                color: $white;
            }

            .pro-price {
                del {
                    color: $white;
                }
            }

            .btn {
                color: $white;

                &:hover {
                    color: var(--secondary);
                }
            }
        }
    }
}


