.bodyrtl {
    .productcard-2 .product article .pro-info .pro-buttons a {
        margin-right: 0px;
        margin-left: 15px;
    }

    .stock-products-js .banner-caption.right-caption {
        left: auto;
        right: 0;
        padding-left: 0px;
        padding-right: 155px;
        text-align: right;
    }

    .stock-products-js figure {
        direction: rtl;
    }

    .testimonial-js .product article .media {
        direction: rtl;

        .media-body {
            margin-left: 0px;
            margin-right: 30px;
        }
    }

}

@media only screen and (min-width:992px) and (max-width:1199px) {}

@media only screen and (min-width:768px) and (max-width:991px) {}

@media only screen and (max-width:991px) {
    .pro-thumb {
        display: flex;
        justify-content: center;
        align-items: center;
    }

}

@media only screen and (max-width:768px) {
    .deal-of-the-day {
        padding-bottom: 60px;
    }

    .tabs-product-without-carousel .nav .nav-link {
        padding-top: 1rem;
    }


}

@media only screen and (max-width:576px) {
    .testimonial-js .product article .media {
        flex-direction: column;

        .media-body {
            margin-left: 0px;
            margin-top: 30px;
        }
    }

    .stock-products-js .banner-caption h3 {
        font-size: 12px;
    }

    .stock-products-js .banner-caption .btn {
        margin-top: 10px;
        padding: 10px 10px;
    }

    .stock-products-js .banner-caption.right-caption {
        padding-left: 156px;
    }

}

@media only screen and (max-width:378px) {}

// rtl responsive
@media only screen and (max-width:576px) {
    .bodyrtl {
        .testimonial-js .product article .media .media-body {
            margin-left: 0px;
            margin-right: 0;
        }

        .stock-products-js .banner-caption.right-caption {
            padding-right: 156px;
        }
    }

}