.index-21 {
    .header-two {
        .header-mini {
            form {
                .dropdown-toggle {
                    color: #fff;
                    font-size: 14px;
                    display: flex;
                    padding: 16px 50px 16px 20px;
                    font-weight: 600;
                    border: none;
                    outline: none;
                    background-color: var(--secondary);

                    i {
                        align-self: center;
                        margin-right: 5px;
                    }
                }

                .dropdown-toggle::after {
                    position: absolute;
                    right: 20px;
                    bottom: 25px;
                    font-family: "Font Awesome 5 Free", Arial, Helvetica, sans-serif;
                    font-weight: 900;
                    content: "\f107";
                    border: none;
                    font-size: 11px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 5px;
                    width: 10px;
                }

                .dropdown-menu {
                    a:hover {
                        color: var(--secondary);
                    }
                }
            }

            a {
                color: #FFF;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                text-align: start !important;
            }
        }

        .search-field2 {
            .input-group {
                border: 2px solid var(--secondary);

                select {
                    border-left: 2px solid var(--secondary);
                    background-color: var(--color-article1-background) !important;
                    color: var(--color-text) !important;
                    border-top: none;
                    border-bottom: none;
                    border-right: none;
                }

                input {
                    width: 34%;
                }

                button {
                    background-color: var(--secondary);
                }
            }
        }

        .mini-header-right {
            display: flex;
            .dropdown{
                .dropdown-menu{
                    width: 300px;
                }
            }

            li {
                align-self: center;

                a {
                    display: block;
                    text-align: center;

                    h6 {
                        color: var(--gray-500, #8B96A5);
                        text-align: center;
                        font-feature-settings: 'clig' off, 'liga' off;
                        font-size: 12px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: normal;
                        margin: 3px 0px 0px 0px;
                    }
                }
            }
        }

        .navbar {
            background-color: transparent !important;

            .navbar-collapse {
                justify-content: flex-start;

                .nav-item {
                    padding: 15px 0px;
                    margin: 0px 20px;

                    .nav-link {
                        font-weight: 600;
                        font-size: 14px;
                    }
                }


            }
        }

        .header-mini {
            background-color: transparent;

            a {
                color: var(--color-text);
            }

            .btn.dropdown-toggle {
                color: var(--color-text) !important;
                font-weight: 600;
            }
        }
    }
}