.headerfifteen {
    .header-mini {
        padding-top: 0px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 16px -4px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
        background-color: var(--color-white-background);

        .navbar-lang .dropdown .btn.dropdown-toggle {
            border-color: var(--color-text);
        }

        .btncart {
            span {
                color: var(--color-text);
            }
        }
    }

    .header-maxi {
        padding: 15px 0 0px 0px;
        // background-color: var(--color-white-background);

        .form-inline {
            width: 100%;
            margin-left: 12px;

            input {
                background-color: var(--color-article2-background);
            }
        }

        .Categroy-toggle .dropdown-toggle {
            background-color: var(--color-article2-background);
            color: var(--color-text) !important;
        }

        .pro-header-right-options li .dropdown-toggle {
            background-color: var(--color-article2-background);
            border: none;
        }

        .pro-header-right-options li .cart-right .title-cart {
            font-weight: 300;
            font-family: unset;
        }
    }

    .header-main {
        background: #99c1da !important;

        .nav-item .dropdown-toggle {
            color: white !important;
        }
    }
    .user-text{
        text-transform: capitalize;
        font-family: unset;
        font-size: 0.879rem;
    }

    .header-mini .navbar-lang .dropdown .btn.btn-end::after {
        right: -9px;
    }
}