a {
  text-decoration: none !important;
}

body {
  background-color: var(--color-white-background);
}

.dropdown-menu-right {
  right: 0 !important;
  left: auto !important;
}

.pro-header-right-options {
  .dropdown-menu-right {
    right: 0 !important;
    left: auto !important;
    transform: translate(0px, 13px) !important;
  }
}

.dropdown-menu[x-placement^="top"] {
  right: auto;
  bottom: auto;
}

.dropdown-menu[x-placement^="bottom"] {
  right: 0 !important;
  bottom: auto;
}

.dropdown-menu {
  top: 100% !important;
}
.dropdown-menu.top-0 {
  top: 0% !important;
}

.navbar-nav {
  .dropdown {
    .dropdown-menu {
      top: 100% !important;
    }
  }
}

// popup button Show on screen

button.close {
  padding: 0;
  background-color: transparent;
  border: none;
  opacity: 0.5;
  top: -13px !important;
  font-weight: 500 !important;
}

button.close:hover {
  opacity: 0.8;
}

// switch Button set the location
.switecher-new {
  .dropdown-menu {
    left: 0px !important;
  }

  .dropdown-menu-right {
    right: auto !important;
    top: 0% !important;
  }
}

//  index-4 ka page

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.btn-link {
  text-decoration: none;
}

.alert-dismissible {
  .close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0.75rem 1.25rem;
    color: inherit;
    font-size: x-large;
  }
}

// index-5 given the color to navbar

.tabs5 .nav a {
  color: var(--dark);
}

.home6 a {
  color: var(--color-text) !important;
}

// home 11 Set navebar input & Button

.home11 .vendor_7_hover:hover {
  color: var(--color-text);
}

.home11 {
  .form-eight {
    height: calc(1.5em + 0.75rem + 2px);
  }

  .input-group-append {
    margin-left: -1px;
    display: flex;
  }
  .btn-secondary:hover {
    background-color: var(--dark) !important;
    color: var(--color-white) !important;
    transition: all 0.3s ease-in-out !important;

  }

  .rs-hover-ready:hover {
    background-color: $secondary-hover !important;
    transition: all 0.3s ease-in-out !important;
  }

  .search-field-module {
    border-radius: 5px;
  }
}

.container-w {
  .categories-panel {
    .navflex-basis {
      .dropdown-menu {
        top: 0 !important;
        right: auto;
        left: 100%;
      }
    }
  }
}

.navbar-nav {
  .dropdown {
    .pull-right {
      top: 0% !important;
      left: 100%;
    }
  }
}

// bages background color

.badge-info {
  background-color: $info;
  color: $white;
}

.badge-danger {
  color: #fff;
  background-color: $danger;
}

.badge-success {
  color: #fff;
  background-color: $success;
}

.badge-dark {
  color: #fff;
  background-color: $dark;
}

// element-product.html change 
.productcard12 {
  .form-control {
    font-size: 0.875rem;
  }
}

.productcard15 {
  ul {
    padding-left: 0;
  }
}

// tytypography section
.typography_section {

  .blockquote-footer {
    margin-top: 1rem;
  }
}

.navbar-collapse {
  .dropdown-submenu {
    .dropdown-menu {
      top: 0 !important;
    }
  }
}

.footer-seven {
  h4 {
    font-size: 29px !important;
  }
}

.media {
  display: flex;
  align-items: flex-start;
}

.nav-item {
  a {
    color: var(--color-text);
  }

  a:hover {
    color: var(--color-text);
  }
}

// cheks form ma Changes karny ky liya 
.form-check .form-check-input {
  float: none;
  margin-left: -1.5em;
}

.form-control {
  font-size: 0.874rem !important;
}

.tabs9-product {
  img {
    z-index: -1;
  }
}



.newsletterfooter-v-six .newsletter .input-group {
  width: 100%;
  border: 1px solid var(--secondary) !important;
  border-radius: 7px;
  overflow: hidden;
}

.pro-options ul {
  padding-left: 0px;
}

.pagination {

  .page-item.active {
    z-index: 1;
  }
}

.contact-content .contact-info .contact-list li {

  align-items: center;

  
}
