/*-----------
/----- Header Top bar -------- */

.dark {
    .header-eight {
        .header-mini {
            background-color: rgba(0, 0, 0, 0.29);
        }
    }

    .header-eight .mega-menu ul li.dropdown-header h5 {
        color: $white;
    }
}

.header-eight {

    width: 100%;
    position: absolute;
    z-index: 2;

    .header-mini {
        background-color: rgba(255, 255, 255, 0.43);

        .navbar-lang {
            display: flex;

        }

        .dropdown-toggle::after {
            color: var(--secondary);
        }

        .pro-mini-header-right {
            .dropdown .dropdown-menu {
                background-color: var(--color-article1-background);
                border-color: var(--color-article1-background);
                animation: animationcartmenu ease 0.5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
                -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
                width: auto;
                border-radius: 0;
                z-index: 99999;
                margin-top: 10px;
                transform: translate(0px, 0px) !important;
                left: auto !important;
            }

            .dropdown {
                padding: 17px 12px 15px 0px;
            }

            li {
                list-style: none;
                margin-left: 20px;
                font-size: 14px;
                text-transform: uppercase;

                button.dropdown-toggle {
                    background-color: transparent;
                    color: var(--color-text);
                    text-transform: uppercase;
                    cursor: pointer;
                    border: none;

                    &:focus {
                        outline: none;
                    }
                }

                .dropdown {
                    padding: 15px 12px 15px 0px;

                    .btn.dropdown-toggle {
                        color: var(--color-text);
                        font-family: $font-family-base;
                        font-size: 14px;
                        text-transform: uppercase;
                        -webkit-transition: 0.4s;
                        transition: 0.4s;
                        padding: 0;

                        &::after {
                            right: -12px;
                            top: 25%;
                        }

                    }

                    .dropdown-menu {
                        animation: animationmenu ease 0.5s;
                        animation-iteration-count: 1;
                        transform-origin: 50% 50%;
                        animation-fill-mode: forwards;
                        -webkit-transition: 0.4s;
                        transition: 0.4s;
                        padding: 1.2rem 0;
                        border-width: 0;
                        margin: 0px -10px 0 0px;
                        -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                        box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);

                        .dropdown-item {

                            padding: 5px 1.7rem;


                            &:hover,
                            &.active,
                            &:active {
                                background-color: transparent;
                                color: var(--secondary);

                            }
                        }
                    }


                    &:hover>.dropdown-menu {
                        display: block;
                        -webkit-transition: 0.4s;
                        transition: 0.4s;
                    }

                }

                &:first-child {
                    padding-right: 0;
                    margin: 0;

                    .far {
                        color: var(--secondary);
                    }
                }
            }
        }
    }

    .header-max {
        background-color: transparent;
    }

    .pro-header-right-options {
        padding-left: 0;
        padding-right: 0px;
        margin-bottom: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .search-field {
            .dropdown-menu {


                padding: 0;
            }

            .search-field-module {
                .btn {
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 10px;
                }

                .fas {
                    font-size: $font-size-lg;
                    color: var(--dark);

                    -webkit-transition: 0.4s;
                    transition: 0.4s;

                    &:hover {
                        color: var(--secondary);
                    }
                }

            }

        }

        li {
            position: relative;
            display: inline-block;
            list-style: none;
            margin-left: 25px;

            button {
                position: relative;
                padding: 0;
                text-decoration: none;
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .fas {
                font-size: 18px;
                color: var(--color-text);
            }

            &:first-child {
                margin-left: 0;
            }
        }

        .cart-right {
            margin-left: 15px;
            text-align: left;
            margin-bottom: 4px;

            .title-cart {
                color: var(--color-text);
                font-family: $font-family-base-bold;
            }

            .cart-item {
                text-transform: capitalize;
                color: var(--secondary);
                font-family: $font-family-base;
                margin-top: 5px;
            }
        }

        .cart-left {
            position: relative;

            .badge {
                top: -20px;
                position: absolute;
                right: -10px;
                border-radius: 200px;
            }
        }

        .dropdown {
            float: right;

            .dropdown-toggle::after {
                content: none;
            }

            .dropdown-menu {
                background-color: var(--color-article1-background);
                border-color: var(--color-article1-background);
                animation: animationcartmenu ease 0.5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
                -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.10);
                width: 270px;
                border-radius: $border-radius;
                z-index: 99999;
                margin-top: 18px;
                left: auto !important;

                .dropdown-item {
                    color: var(--color-text);
                }

                .shopping-cart-items {

                    padding-left: 25px;
                    padding-right: 25px;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    max-height: 350px;
                    overflow-y: auto;

                    li {
                        float: left;
                        position: relative;
                        width: 100%;
                        margin-bottom: 10px;
                        border-bottom: $border-width solid;
                        border-color: var(--color-border);
                        padding-bottom: 10px;
                        margin-left: 0;

                        .item-thumb {
                            position: absolute;
                            left: 0;
                            top: 0;


                            .image {
                                width: 64px;
                                height: 64px;
                                border: $border-width solid;
                                border-color: var(--color-border);
                                border-radius: $border-radius;
                                overflow: hidden;

                            }
                        }

                        .item-detail {
                            float: left;
                            padding-left: 74px;

                            h3 {
                                font-family: $font-family-base-bold;
                                font-size: $font-size-base;
                                text-transform: capitalize;
                                color: var(--color-text);
                            }

                            .item-s {
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                font-size: $font-size-base;
                                color: var(--color-text);


                                .fas {
                                    color: var(--color-text-secondary);
                                    float: right;
                                    margin-left: 8px;
                                    -webkit-transition: 0.4s;
                                    transition: 0.4s;

                                    &:hover {
                                        color: var(--secondary);
                                    }
                                }
                            }
                        }

                        &:last-child {
                            border-bottom: none;
                            margin-bottom: 0;
                            padding: 0;
                        }
                    }

                    .item-summary {
                        font-family: $font-family-base-bold;
                        font-size: $font-size-lg;
                        font-weight: $font-weight-bold;
                        color: var(--secondary);
                        margin-bottom: 0;
                        margin-top: 5px;
                        white-space: normal;
                    }

                }
            }
        }

    }


    .navbar {
        padding: 0px;

        .navbar-collapse {
            justify-content: center;

            .navbar-nav {

                .nav-item {
                    margin-right: 45px;
                    padding: 23px 0 23px 0px;

                    &:last-child {
                        margin-right: 0;
                    }


                    .nav-link {
                        position: relative;
                        font-family: $font-family-base;
                        font-size: $font-size-base;

                        padding: 0;


                        &:hover {
                            text-decoration: none;
                        }

                        .badge {
                            position: absolute;
                            top: 2px;
                            left: 10px;
                            font-family: $font-family-base;
                            background-color: $danger;
                            color: color-yiq($danger);

                        }


                    }

                    .dropdown-menu {
                        animation: animationmenu ease 0.5s;
                        animation-iteration-count: 1;
                        transform-origin: 50% 50%;
                        animation-fill-mode: forwards;

                        margin: 0px 0 0 -25px;
                        padding: 15px 0;
                        border-radius: $border-radius;
                        border: none;

                        width: 180px;
                        -webkit-box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);
                        box-shadow: 0 0 4px 0px rgba(51, 51, 51, 0.1);

                        .dropdown-item {
                            white-space: normal;
                            text-transform: capitalize;

                            img {
                                position: fixed;
                                right: -150px;
                                top: -5px;
                                visibility: hidden;
                                z-index: 3;
                                width: 150px;
                                -webkit-box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                box-shadow: 0 0 10px rgba(51, 51, 51, .06);
                                opacity: 0;
                                -webkit-transition: .2s;
                                transition: .2s;
                            }

                            &:hover {
                                background-color: transparent;
                                color: var(--secondary);
                                transition: 0.5s;

                                img {
                                    visibility: visible;
                                    top: 0;
                                    opacity: 1;
                                }
                            }

                        }
                    }
                }

                .nav-item.dropdown:hover>.dropdown-menu {
                    display: block;
                }
            }
        }
    }

    .mega-menu-md {
        width: 654px !important;
    }

    .mega-menu-sm {
        width: 436px !important;
    }

    .mega-menu {
        padding: 0 !important;

        .no-gutters {
            padding: 0;
        }

        ul {
            list-style: none;
            padding: 15px 0;

            li.dropdown-header {
                white-space: normal;

                h5 {
                    font-family: $font-family-base-bold;
                    font-size: $font-size-base;
                    color: var(--color-text);
                    text-transform: uppercase;
                    font-weight: bold;
                }
            }


        }

        .banner-content {
            position: absolute;
            top: 0;
            left: 0;
            padding: 15px;

            .banner-title {
                font-family: $font-family-base;
                font-size: $font-size-lg;

                span {
                    strong {
                        font-family: $font-family-base-bold;
                        font-size: ($font-size-lg + 1rem);
                    }
                }
            }
        }
    }

}


.dropdown-submenu:hover>.dropdown-menu {
    display: block;
}

.dropdown-submenu {
    position: relative;

    .dropdown-menu {
        top: -15px;
        left: 100%;
        margin-left: 0 !important;
    }
}





.bodyrtl {
    .header-eight .header-mini .pro-mini-header-right li .dropdown .dropdown-menu .dropdown-item {
        text-align: right;
    }

    .dropdown-menu .dropdown-item {
        text-align: right;
    }

    .header-eight .header-mini .pro-mini-header-right .dropdown .dropdown-menu {
        left: 0 !important;
        right: auto !important;
    }

    .header-eight .header-mini .pro-mini-header-right li:first-child {
        padding-left: 0;
    }

    .header-eight .header-mini .pro-mini-header-right .dropdown {
        padding: 10px 0px 10px 12px;
    }

    .header-eight .header-mini .pro-mini-header-right li {
        margin-right: 20px;
        margin-left: 0px;
    }

    .header-eight .header-mini .pro-mini-header-right li .dropdown {

        padding: 10px 0px 10px 12px;
    }

    .header-eight .header-mini .pro-mini-header-right li .dropdown .btn.dropdown-toggle::after {
        left: -12px;
        right: auto;
        margin-right: 0.255em;
        margin-left: 0;
    }

    .header-eight .pro-header-right-options .cart-right {
        margin-right: 15px;
        margin-left: 0;
    }

    .header-eight .pro-header-right-options .dropdown {
        margin-left: 0;
        margin-right: 25px;
    }

    .header-eight .pro-header-right-options .cart-left .badge {
        left: -8px;
        right: auto;
    }

    .header-eight .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-left: 45px;
        margin-right: 0;
    }

    .header-eight .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu {

        text-align: right;

    }

    .header-eight .navbar .navbar-collapse .navbar-nav .nav-item.dropdown:hover>.dropdown-menu {
        left: auto;
        right: 0;
        text-align: right;
        margin: 0px -25px 0 0;
    }

    .header-eight .pro-header-right-options .dropdown .dropdown-menu {
        right: auto;
        left: 0px !important;
        transform: translate3d(0px, 33px, 0) !important;
        margin-top: 15px;
    }

    .header-eight .pro-header-right-options .search-field .search-field-module .btn {
        right: auto;
        left: 0;
    }

    .header-eight .navbar .navbar-collapse .navbar-nav .nav-item .dropdown-menu .dropdown-item img {
        left: -150px;
        right: auto;
    }
}


@media only screen and (max-width: 1024px) {
    .header-desktop {
        display: none;
    }
}

@media only screen and (max-width: 1200px) and (min-width:1025px) {
    .header-two .header-mini .pro-mini-header-right .mycart .dropdown-menu {
        transform: translate3d(300px, 4px, 0px) !important;
    }

    .header-eight .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-right: 40px;
    }

    .bodyrtl .header-eight .navbar .navbar-collapse .navbar-nav .nav-item {
        margin-left: 40px;
        margin-right: 0;
    }
}