.home-21 {
    $secondary: #0067FF;
    --secondary: #{$secondary};
}
.index-21 {
    .productcard5a {
        a{
            color: black !important;
        }
    }
    
    .pro-content {
        background: transparent;
    }
    .categories-panel{
        ul{ 
            .nav-item {
                .fas {
                position: absolute;
                right: 0;
                font-size: 15px !important;
                /* margin-right: 10px; */
                color: var(--secondary) !important;
                cursor: pointer;
                margin: 0px !important;
            }

        } 

    } 
    }

    .btn-primary {
        background: var(--primary-gradient, linear-gradient(180deg, #127FFF 0%, #0067FF 100%));
        color: var(--text-color-primary);
    }

    
    .banner-carousel{
        margin: 0;
        background-color: var(--color-article1-background);
    }

    .categories-panel {
        box-shadow: none;

        ul {
            .nav-item {
                a {
                    border: none;
                }

                a:hover {
                    background-color: #E5F1FF !important;
                }
            }
        }
    }

    .login-panel {
        border-radius: 6px;
        background: #E3F0FF;
        padding: 20px;
        button{
            background-color: var(--secondary);
        }
    }

    .panel-1 {
        border-radius: 6px;
        background: #F38332;
        padding: 20px;

        h1 {
            color: var(--white, #FFF);
            font-feature-settings: 'clig' off, 'liga' off;
            /* Text-base */
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }
    }

    .panel-2 {
        border-radius: 6px;
        background: #55BDC3;
        padding: 20px;

        h1 {
            color: var(--white, #FFF);
            font-feature-settings: 'clig' off, 'liga' off;
            /* Text-base */
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }
    }

    .imgbanner-caption {
        padding: 75px 50px;

        h6 {
            color: var(--base-color-dark, #1C1C1C);
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 28px;
            font-style: normal;
            font-weight: 400 !important;
            line-height: normal;
        }

        h1 {
            color: var(--base-color-dark, #1C1C1C);
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 32px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
        }

        button {
            font-weight: normal !important;
        }
    }

    .productcard5a {
        margin: 0;
        padding: 0;

        article {
            background-color: white !important;
            height: 100% !important;

            .pro-thumb {
                height: auto;
            }
        }

        .pro-price {
            ins {
                color: var(--gray-500, #8B96A5);
                font-feature-settings: 'clig' off, 'liga' off;
                font-family: 'Poppins-Regular' !important;
                /* Text-small */
                font-size: 13px !important;
                font-style: normal;
                font-weight: 400 !important;
                line-height: normal;
            }
        }
    }

    .pro-login {
        background: linear-gradient(95deg, #2C7CF1 7.19%, rgba(0, 209, 255, 0.50) 89.49%);

        .pro-login-menu {
            background-color: white;
            padding: 15px;

            h1 {
                color: var(--base-color-dark, #1C1C1C);
                font-feature-settings: 'clig' off, 'liga' off;
                /* Title-H4 */
                font-size: 20px;
                font-style: normal;
                font-weight: 600;
                line-height: 28px;
                /* 140% */
                letter-spacing: -0.2px;
            }
            button{
                background-color: var(--secondary);
                color: white !important;
            }
        }

        .pro-info {
            .pro-price {
                display: inherit !important;

                ins {

                    border-radius: 5px;
                    background: #2D7CF2;
                    color: #FFF;
                    font-size: 20px;
                    padding: 0px 20px;
                    font-style: normal;
                    font-weight: normal !important;

                    line-height: normal del {
                        color: #717171;
                        font-size: 18px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: normal;
                        text-decoration-line: strikethrough;
                        background: transparent !important;
                        margin-top: 2px;
                    }
                }
            }
        }

        .pro-login-details {
            h1 {
                color: #FFF;
                font-feature-settings: 'clig' off, 'liga' off;
                /* Title-H2 */
                font-size: 32px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                letter-spacing: -0.2px;
            }

            p {
                color: #FFF;
                font-size: 18px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }
        }
    }

    .services {
        .pro-heading-title {
            h4 {
                color: var(--dark, #1C1C1C);
                font-feature-settings: 'clig' off, 'liga' off;
                font-size: 24px;
                font-style: normal;
                font-weight: 600;
                line-height: 32px;
                /* 133.333% */
                letter-spacing: -0.2px;
            }
        }

        .vproduct6 {
            margin-top: 15px;

            .pro-info {
                .start-100 {
                    left: 80% !important;
                }
            }

            ins {
                color: var(--color-text) !important;
                font-feature-settings: 'clig' off, 'liga' off;
                /* Text-title */
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 22px;
                /* 137.5% */
            }

            h3 {
                color: var(--dark, #1C1C1C);
                font-feature-settings: 'clig' off, 'liga' off;
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 22px;
                /* 137.5% */
            }

            .pro-icon {
                width: 45px;
                height: 45px;
                background-color: #D1E7FF;
                border-radius: 50%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
        }
    }

    .Region-services {
        .pro-heading-title {
            h4 {
                color: var(--dark, #1C1C1C);
                font-feature-settings: 'clig' off, 'liga' off;
                font-size: 24px;
                font-style: normal;
                font-weight: 600;
                line-height: 32px;
                /* 133.333% */
                letter-spacing: -0.2px;
            }
        }

        .region {
            margin-top: 20px;

            .region-details {
                padding-left: 15px;

                h6 {
                    color: var(--dark, #1C1C1C);
                    font-feature-settings: 'clig' off, 'liga' off;
                    /* Text normal */
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                    /* 150% */
                    letter-spacing: -0.2px;
                    margin: 0;
                }

                p {
                    color: var(--gray-500, #8B96A5);
                    font-feature-settings: 'clig' off, 'liga' off;
                    /* Text-small */
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    margin: 0;
                }
            }
        }

    }

    

    .categories {
        .social {
            list-style-type: none;
            display: flex;

            li {
                margin-right: 15px;
                width: 37px;
                height: 37px;
                background: #BDC4CD;
                color: white !important;
                display: flex;
                justify-content: center;
                border-radius: 50%;

                a {
                    color: white !important;
                    align-self: center;
                }
            }
        }
    }

    
}
@media only screen and (min-width:576px) and (max-width:768px){
    .home-21 .btn-sm, .btn-group-sm > .btn {
        padding: 10px !important;
    }
}
@media only screen and (min-width:320px) and (max-width:576px){
    .imgbanner-caption {
        padding: 30px !important;

        h6 {
            color: var(--base-color-dark, #1C1C1C);
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 18px !important;
            font-style: normal;
            font-weight: 400 !important;
            line-height: normal;
        }

        h1 {
            color: var(--base-color-dark, #1C1C1C);
            font-feature-settings: 'clig' off, 'liga' off;
            font-size: 22px !important;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
        }

        button {
            font-weight: normal !important;
        }
    }
}

.bodyrtl{
    .pro-banner-1{
        .end-0 {
            right: unset !important;
            left: 0 !important;
        }
    }
}